import React, { memo } from 'react';
import { useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { withRouter } from 'react-router-dom';
import CustomButton from '../../../UI/Button/CustomButton';
import CustomSwitch from '../../../UI/Input/Switch/CustomSwitch';
import { confirmMessage } from '../../../UI/Message/message';
import CustomModal from '../../../UI/Modal/CustomModal';
import { initCoin } from '../../../_actions/coin_action';
import { InitGame } from '../../../_actions/currentTime_action';
import { setEffectToggle, setEffectVolume, setMusicToggle, setMusicVolume } from '../../../_actions/setting_action';

const SettingModal = memo((props) => {

    const dispatch = useDispatch();
    const { Music, Effect } = useSelector(state => state.setting)

    const toggleMusic = (value) => {
        dispatch(setMusicToggle(value));
    }
    const onMainMusicSoundVolumeChange = (e) => {
        dispatch(setMusicVolume(Number(e.currentTarget.value)))
    }
    const toggleEffect = (value) => {
        dispatch(setEffectToggle(value))
    }
    const onEffectSoundVolumeChange = (e) => {
        dispatch(setEffectVolume(Number(e.currentTarget.value)))
    }
    const gotoMain = () => {
        confirmMessage('시작화면으로 돌아갑니다.', () => {
            // dispatch(ResetGame());
            dispatch(initCoin());
            dispatch(InitGame());
            props.history.push('/client/choice');
        })
    }
    useEffect(()=>{
        if(props.visible){
            const setting={Music,Effect};
            window.localStorage.setItem('setting',JSON.stringify(setting));
        }
    },[Music,Effect])
    return (
        <CustomModal
            {...props}
            title={'환경설정'}
            className={'setting_modal'}
        >
            <div className='options'>
                <div className='header'>
                    <span className='title'>배경음</span>
                    <CustomSwitch value={Music?.on} onChange={toggleMusic} />
                </div>
                <div className='range'>
                    <input type={'range'} value={Music?.volume} onChange={onMainMusicSoundVolumeChange} />
                    <span>{Music?.volume}</span>
                </div>
            </div>
            <div className='options'>
                <div className='header'>
                    <span className='title'>효과음</span>
                    <CustomSwitch value={Effect?.on} onChange={toggleEffect} />
                </div>
                <div className='range'>
                    <input type={'range'} value={Effect?.volume} onChange={onEffectSoundVolumeChange} />
                    <span>{Effect?.volume}</span>
                </div>
            </div>
            <div className='options'>
                <CustomButton className={'logout_btn'} onClick={gotoMain}>메인으로 나가기</CustomButton>
            </div>
        </CustomModal>
    )
})

export default withRouter(SettingModal);