import React, { memo, useRef, useState } from 'react';
import { useSelector } from 'react-redux';
import resting1 from './MonitoringImage/resting/resting_1.png';
import resting2 from './MonitoringImage/resting/resting_2.png';
import resting3 from './MonitoringImage/resting/resting_3.png';
import resting4 from './MonitoringImage/resting/resting_4.png';
import resting5 from './MonitoringImage/resting/resting_5.png';
import resting6 from './MonitoringImage/resting/resting_6.png';
import resting7 from './MonitoringImage/resting/resting_7.png';
import resting8 from './MonitoringImage/resting/resting_8.png';
import { useInterval } from './useInterval';

const CharacterResting= memo((props) =>{


    const characterImg = useRef([
        resting1, resting2, resting3, resting4, resting5, resting6, resting7, resting8
    ])
    const charIndex = useRef(0);
    const [charImg, setCharImg] = useState(resting1)
    const play = useSelector(state => state.play)
    const { interval } = useSelector(state => state.interval)

    useInterval(() => {
        setCharImg(characterImg.current[charIndex.current]);
        charIndex.current++;
        if (charIndex.current === characterImg.current.length) {
            charIndex.current = 0;
        }
    }, interval, play)

  return (
      <div className='resting'>
            <img src={charImg} />
      </div>
  );
})

export default CharacterResting;
