import React from 'react'
import "./scss/_Button.scss";
const CustomButton = ({ className = "", style = {}, onClick=()=>{}, type, disabled = false, selected, ref , children }) => {

    const onClickEvent = (e) => {
        e.stopPropagation();
        if (onClick && !disabled) {
            onClick();
        }
    }

    return (
        <div
            ref={ref}
            className={`button-${type || 'standard'} ${className} ${disabled ? `disabled` : ""}`}
            style={style}
            onClick={onClickEvent}
            selected={selected ? true : false}
        >{children}</div>
    )
}

export default CustomButton
