import React from 'react'
import { useSelector } from 'react-redux';
import { useDispatch } from 'react-redux'
import CustomButton from '../../../../UI/Button/CustomButton'
import { setActivity } from '../../../../_actions/activity_action';
import { Activities } from '../../../../Assets/ActionAsset';

function SelfTrainingPanel(props) {

    const dispatch = useDispatch();
    const currentTime = useSelector(state => state.currentTime);
    const {selfDevelopContents} = useSelector(state=>state.activity)
    const onSelfTraining = () => {
        dispatch(setActivity(Activities.self_develop,selfDevelopContents,currentTime))
        props.ModalHandler(false);
    }

    return (
        <div>
            자기계발을 통해 태그별 전문성을 올릴수 있습니다.
            <br />
            피로도+
            <hr />
            <div style={{ height: '8vh', display: 'flex', justifyContent: 'flex-end' }}>
                <CustomButton onClick={onSelfTraining}>실행</CustomButton>
            </div>
        </div>
    )
}

export default SelfTrainingPanel
