import React from 'react'
import { useEffect } from 'react';
import { useState } from 'react';
import { useDispatch } from 'react-redux';
import { useSelector } from 'react-redux'
import CustomButton from '../../../../UI/Button/CustomButton';
import CustomCheckBox from '../../../../UI/Input/CheckBox/CustomCheckBox';
import { infoMessage } from '../../../../UI/Message/message';
import CustomModal from '../../../../UI/Modal/CustomModal'
import { successAdv } from '../../../../_actions/adv_action';
import { getMoney } from '../../../../_actions/character_action';
import { addUploadVideo } from '../../../../_actions/uploaded_action';
import { removeVideo } from '../../../../_actions/video_action';

function AdvSubmitModal(props) {
    const {quest:{quest,contents}} = props;
    const dispatch = useDispatch();
    const youtube = useSelector(state=>state.youtubeVideo);
    const currentTime = useSelector(state=>state.currentTime);

    const [youtubeList, setYoutubeList] = useState({})
    const [selectedKey, setSelectedKey] = useState('')

    useEffect(() => {
        if(props.visible){
            const newList = {}
            Object.keys(youtube).map(key=>{
                if(youtube[key].contentsKey === contents.key && youtube[key].progress === 100 && youtube[key].rating >= quest.rating){
                    newList[key] = youtube[key];
                }
            });

            setYoutubeList(newList);
        }
      
    }, [props.visible])

    const onSelectSubmit = (key)=>{
        setSelectedKey(key);
    }
    
    const onSubmit=()=>{
        if(!selectedKey) return infoMessage(`제출할 영상을 선택해주세요`)
        const submitContents = youtubeList[selectedKey];
        submitContents.name = `[광고] ${submitContents.name}`;
        submitContents.advertisement = true;
        submitContents.upload_date = currentTime;

        // 퀘스트 보고
        dispatch(successAdv(quest))
        // 보상금 수령
        dispatch(getMoney(quest.reward));
        // 광고 유튭각 업로드
        dispatch(addUploadVideo(submitContents));
        // 업로드 목록에서 삭제
        dispatch(removeVideo(submitContents));

        props.ModalHandler(false);
    }

    return (
        <CustomModal
        {...props}
        title={'편집본 제출'}
        className={'submit_modal'}
        footer={
            <>
                <CustomButton onClick={onSubmit}>제출</CustomButton>
            </>
        }
        >
            <div className="submit_enable_list">
                {Object.keys(youtubeList).length > 0
                    ?
                    Object.keys(youtubeList).map(key => {
                        const enableYoutube = youtubeList[key];
                        const select = selectedKey === key;
                        return (
                            <div className='submit_contents' key={key}>
                                <div className='contents_name'>
                                <CustomCheckBox value={select} onChange={()=>onSelectSubmit(key)}>
                                    {enableYoutube.name}
                                </CustomCheckBox>
                                </div>
                                <div className='rating'>
                                <span className='star'>★</span>
                                {enableYoutube.rating}
                                </div>
                                
                            </div>
                        )
                    })
                    :
                    <div className='submit_empty'>제출 가능한 영상이 없습니다.<br/>조건에 맞는 영상의 편집 달성도를 100% 달성해주세요</div>
                }
            </div>
        </CustomModal>
    )
}

export default AdvSubmitModal