import { infoMessage } from "../UI/Message/message";
import { ADD_EDITOR, SET_EDITOR_WORK, SET_EDITOR_LIST, REMOVE_EDITOR } from "../_actions/type";

const initialState = [
    {
        key: '하만',
        name: '하만',
        edit_ability: 5,
        work_count: 1,
        cost: 400000,
        desc : '패드의 마술사',
        tag: [
            'MULTI',
            'FPS',
            'TPS',
            'ACTION',
            'BATTLEROYAL',
            'CONSOLE'
        ],
        working_time: [9, 18]
    },
]

export default function (state = initialState, action) {
    switch (action.type) {
        case ADD_EDITOR:
            return [...state, action.payload];
        case SET_EDITOR_LIST:
            if (action.payload === 'none') {
                return state
            } else {
                return action.payload;
            }
        case SET_EDITOR_WORK:
            const newState = [...state];
            const { editor, work } = action.payload;
            newState.map(item => {
                const itemIndex = item.working.indexOf(work);
                if (itemIndex > -1) {
                    item.working.splice(itemIndex, 1);
                }
            })
            // 신규 적용 , 가능 갯수보다 많으면 리턴
            const { working } = newState[newState.indexOf(editor)]
            newState[newState.indexOf(editor)].working = [...working, work];
            return newState;
        case REMOVE_EDITOR:
            const index = state.findIndex(item => item.key === action.payload.key);
            const removedState = [...state];
            removedState.splice(index, 1);
            return removedState;
        default: return state;
    }
}