import React from 'react'
import { memo } from 'react';
import "./scss/_CustomModal.scss";
import { ReactComponent as CloseButton } from './icon/x-square.svg';

const CustomModal = memo((props) => {
    const {
        style
        , className
        , visible
        , ModalHandler
        , toggle = true
        , title
        , footer
        , titleStyle
        , bodyStyle = style
    } = props;

    const onCloseModalFromBackground = (e) => {
        if (toggle) {
            ModalHandler && ModalHandler(false);
        }
    }
    const onCloseModal = (e) => {
        if (ModalHandler) {
            ModalHandler(false);
        }
    }
    const onModalBodyHandler = (e) => {
        e.stopPropagation()
    }
    const setHeight = () => {
        if (style?.height) {
            if (footer) {
                return `calc(${style.height} + 1.5rem)`;
            } else {
                return style.height
            }
        } else {
            return '20rem'
        }

    }
    const setBodyHeight = () => {
        let retStyle = '';
        if (style?.height) {
            if (footer) {
                retStyle = `calc(${style.height} - 4rem)`
            } else {
                retStyle = `calc(${style.height} - 1rem)`
            }
        } else {
            retStyle = `calc(20rem - 2rem)`
        }
        return retStyle;
    }
    if (visible) {
        return (
            <div className="ModalBackground" onClick={onCloseModalFromBackground}>
                <div className={`${className ? className : ''} ModalPanel`} onClick={onModalBodyHandler}>

                    <div className={`ModalHeader`}>
                        {title &&
                            < span className="ModalTitle" style={{ ...titleStyle }} >
                                {title}
                            </ span>
                        }
                        <div className="ModalCloseButton"  >
                            <i className="bi bi-x-circle-fill" onClick={onCloseModal}></i>
                            {/* <CloseButton onClick={onCloseModal}/> */}
                        </div>
                    </div>
                    <div className="ModalContents"
                        style={{ ...style, 
                            // minHeight: setHeight(),
                             ...bodyStyle }}
                    >
                        <div className="ModalContentsBody">
                            {props.children}
                        </div>

                    </div>
                    <div className="ModalFooter">
                        {footer && footer}
                    </div>
                </div>
            </div>
        )
    } else {
        return (
            <></>
        )
    }

})

export default CustomModal
