import { contents_product } from "../Assets/shopAsset";
import { SET_TAG, UP_TAG, DOWN_TAG } from "../_actions/type";


const tagInitial = () => {
    let tagList = [];
    contents_product.map(item => {
        item.tag.map(t => {
            if (!tagList.find(ta => ta.name === t.toUpperCase())) {
                const obj = new Object;
                obj.name = t.toUpperCase();
                obj.value = Math.floor(Math.random() * 40);
                tagList.push(obj);
            }
        })
    })
    return tagList
}
