const initialState = tagInitial();

export default function (state = initialState, action) {
    let newState = [];
    let targetTags = [];
    switch (action.type) {
        case SET_TAG:
            if (action.payload.length > 0) {
                newState = state.map(item=>{
                    const tag = action.payload.find(p=>p.name === item.name);
                    if(tag){
                        item.name = tag.name;
                        item.value = tag.value;
                    }
                    return item;
                })
                return newState;
            } else {
                return tagInitial();
            }
        case UP_TAG:
            targetTags = [...action.payload.tagList];
            const param = action.payload.param
            newState = [...state];
            //태그 능력치 상승
            targetTags.map(item => {
                let tag = newState.find(t => t.name === item.toUpperCase());
                let index = newState.indexOf(tag);
                if (index > -1) {
                    if (newState[index].value + param > 100) {
                        newState[index].value = 100
                    } else {
                        newState[index].value = newState[index].value + param
                    }
                }
            });
            // 태그 능력치 감소
            // newState.map(tag => {
            //     const index = targetTags.findIndex(gt => gt === tag.name);
            //     if (index < 0) {
            //         tag.value = (tag.value - 0.01)
            //         if (tag.value < 0) tag.value = 0
            //     }
            // })
            return newState;

        case DOWN_TAG:
            newState = [...state];
            targetTags = [...action.payload];
            // 태그 능력치 감소
            newState.map(tag => {
                const index = targetTags.findIndex(gt => gt === tag.name);
                if (index < 0) {
                    tag.value = (tag.value - 0.01)
                    if (tag.value < 0) tag.value = 0
                }
            })
            return newState
        default: return state;
    }
}