import { RESET_GAME, SET_CURRENT_TIME,INIT_GAME } from './type'

export function setCurrentTime(milli) {

    return {
        type: SET_CURRENT_TIME,
        payload: milli
    }
}

export function InitGame(){
    return{
        type : INIT_GAME,
    }
}

export function ResetGame() {
    window.localStorage.removeItem('currentTime');
    window.localStorage.removeItem('character');
    window.localStorage.removeItem('newsList');
    window.localStorage.removeItem('youtubeVideo');
    window.localStorage.removeItem('editorList');
    window.localStorage.removeItem('uploadedList');
    window.localStorage.removeItem('contentsList');
    window.localStorage.removeItem('asset');
    window.localStorage.removeItem('coin');
    window.localStorage.removeItem('bank');
    window.localStorage.removeItem('stress');
    window.localStorage.removeItem('equipment');
    window.localStorage.removeItem('computer');
    window.localStorage.removeItem('activity');
    window.localStorage.removeItem('sleep');
    window.localStorage.removeItem('tag');
    window.localStorage.removeItem('inventory');
    window.localStorage.removeItem('advertisement');
    window.localStorage.removeItem('difficulty');

    return {
        type: RESET_GAME
    }
}