import React from 'react';
import { useState } from 'react';
import { useDispatch } from 'react-redux';
import { useSelector } from 'react-redux';
import CustomButton from '../../../../UI/Button/CustomButton';
import { infoMessage } from '../../../../UI/Message/message';
import CustomModal from '../../../../UI/Modal/CustomModal';
import { setStatus } from '../../../../_actions/character_action';
import { setUseInventoryItem } from '../../../../_actions/inventory_action';
import { useInterval } from '../../../Main/Section/useInterval';

function CosmeticSurgeryModal(props) {

    const { face: { value: MyFace } } = useSelector(state => state.character)
    const inventory = useSelector(state => state.inventory)
    const dispatch=useDispatch();
    const [slotNumber, setSlotNumber] = useState(1);
    const [play, setPlay] = useState(false);

    useInterval(() => {
        const randomNumber = Math.round(Math.random() * (10 - 1) + 1);
        setSlotNumber(randomNumber);
    }, 50, play)

    const onChangeFace = () => {
        if(inventory.ex_8?.count > 0){
            setPlay(true)
        }else{
            infoMessage('남은 이용권이 없습니다.');
        }
        
    }
    const onStopSlot = () => {
        setPlay(false);
        const randomNumber = Math.random() * 10;
        let applyNumber = 3;
        if (randomNumber < 1.5) {
            applyNumber=1;
        } else if (randomNumber < 2.5) {
            applyNumber=2;
        } else if (randomNumber < 3.5) {
            applyNumber=3;
        } else if (randomNumber < 4.5) {
            applyNumber=4;
        } else if (randomNumber < 7) {
            applyNumber=5;
        } else if (randomNumber < 8) {
            applyNumber=6;
        } else if (randomNumber < 9) {
            applyNumber=7;
        } else if (randomNumber < 9.4) {
            applyNumber=8;
        } else if (randomNumber < 9.8) {
            applyNumber=9;
        } else if (randomNumber < 10) {
            applyNumber=10;
        }
        setSlotNumber(applyNumber);
        dispatch(setStatus('face',applyNumber))
        dispatch(setUseInventoryItem('ex_8'))
    }
    return (
        <CustomModal
            {...props}
            title={'랜덤 성형권'}
            className={'inventory_item_modal'}
        >
            <div className="dashboard">
                <div className="my_status">
                    현재 나의 외모지수 : {MyFace}점
                </div>

                <div className='slot_number'>
                    {slotNumber}
                </div>

                <div className='my_coupon'>
                    가지고 있는 성형권  : {inventory.ex_8?.count||0}개
                </div>
            </div>
            <div className="slot_button">
                {play ?
                    <CustomButton onClick={onStopSlot} type={'danger'}>
                        멈춰
                    </CustomButton> :
                    <CustomButton onClick={onChangeFace}>
                        돌려돌려!
                    </CustomButton>
                }
            </div>
        </CustomModal>
    );
}

export default CosmeticSurgeryModal;
