import React from 'react'
import { useState } from 'react'
import { useEffect } from 'react'
import CharacterMonitoring from './Section/CharacterMonitoring'
import ControlComponent from './Section/ControlComponent'
import FaintInfoComponent from './Section/EventScreen/FaintInfoComponent'
import SleepInfoComponent from './Section/EventScreen/SleepInfoComponent'
import NoticePopup from './Section/NoticePopup'
import SaveComponent from './Section/SaveComponent'
import StressComponent from './Section/StressComponent'
import TimerComponent from './Section/TimerComponent'
import TutorialComponent from './Section/TutorialComponent'
import UserStatus from './Section/UserStatus'


var delay = 300;
var timer = null;

function MainComponent() {

    const [screenMode, setScreenMode] = useState('mobile');

    useEffect(() => {
        if(window.innerWidth > 1279){
            setScreenMode('pc')
        }else{
            setScreenMode('mobile')
        }

        const resizeHandler=()=>{
            clearTimeout(timer);
            timer = setTimeout(function(){
                if(window.innerWidth > 1279){
                    setScreenMode('pc')
                }else{
                    setScreenMode('mobile')
                }
            }, delay);
        }

        window.addEventListener('resize', resizeHandler);
        return ()=>{
            window.removeEventListener('resize',resizeHandler)
        }
      
    }, []);
    

    if(screenMode === 'pc'){
        return (
            <div className='pc_wrapper'>
                <SaveComponent />
                <div className='pc_left_side'>
                    <UserStatus />
                    <CharacterMonitoring />
                    <StressComponent />
                    <TimerComponent />
                </div>
                <div className='pc_right_side'>
                    <ControlComponent />
                </div>
                <SleepInfoComponent />
                <FaintInfoComponent/>
            </div>
        )

    }else if(screenMode ==='mobile'){
        return (
            <React.Fragment>
                <SaveComponent />
                <UserStatus />
                <CharacterMonitoring />
                <StressComponent />
                <TimerComponent />
                <ControlComponent />
                <SleepInfoComponent />
                <FaintInfoComponent/>
                <NoticePopup/>
                <TutorialComponent/>
            </React.Fragment>
        )

    }
    
}

export default MainComponent
