import React, { useEffect, useState } from 'react'
import { memo } from 'react'
import { useSelector } from 'react-redux';
import { useDispatch } from 'react-redux';
import CustomButton from '../../../UI/Button/CustomButton';
import { confirmMessage, errorMessage, infoMessage } from '../../../UI/Message/message';
import CustomModal from '../../../UI/Modal/CustomModal'
import { AddComma, calcComputerScore, sortingForObjectList } from '../../../util/commonMethod';
import { payMoney } from '../../../_actions/character_action';
import { buyComputer, buyGpu, buyRam } from '../../../_actions/computer_action';
import { buyEquipment } from '../../../_actions/equipment_action';
import { furniture_category, furniture_product } from '../../../Assets/shopAsset';
import emptyProduct from './img/emptyproduct.png';
import { imgs } from '../../../Assets/imgAsset';

const FurnitureShopComponent = memo((props) => {
    const equipment = useSelector(state => state.equipment);
    const computer = useSelector(state => state.computer);
    const { money: { value: myMoney } } = useSelector(state => state.character)
    const { standardAverage, coinList } = useSelector(state => state.coin)
    const difficulty = useSelector(state=>state.difficulty);
    const [selectedCategory, setSelectedCategory] = useState('');
    const [searchText, setSearchText] = useState('');
    const [equipScoreSortDirection, setEquipScoreSortDirection] = useState(false);
    const [priceSortDirection, setPriceSortDirection] = useState(true);

    const dispatch = useDispatch();

    const [productList, setProductList] = useState([]);



    useEffect(() => {
        let newList = JSON.parse(JSON.stringify(furniture_product));
        newList = newList.filter(item => item.category.indexOf(selectedCategory) > -1)
        newList = newList.filter(item => {
            if (item.name.indexOf(searchText) > -1) {
                return item;
            } else {
                const tags = item.tag.filter(t => t.indexOf(searchText) > -1)
                if (tags.length > 0) {
                    return item;
                }
            }
        })
        if (newList.length > 0) {
            if(difficulty === 1){
                const currentCoinAverage = coinList.reduce((a, b) => { return a + b.price }, 0);
                let priceCoefficient = currentCoinAverage / standardAverage;
                newList.map(item => {
                    if (priceCoefficient > 10) priceCoefficient = 10
                    // 코인 가격 비교
                    if (item.category === 'computer') {
                        const { price } = furniture_product.find(fur => fur.key === item.key)
                        const newPrice = Math.floor(price * priceCoefficient);
                        if (item.price < newPrice) {
                            item.price = newPrice;
                        }
                    }
                })
            }
            setProductList(newList);
        }
    }, [searchText, selectedCategory, coinList])

    const onChangePriceSort = () => {
        setPriceSortDirection(!priceSortDirection)
        let newList = [...productList];
        newList = sortingForObjectList(newList, priceSortDirection, 'price');
        setProductList(newList);
    }
    const onChangeEquipSort = () => {
        setEquipScoreSortDirection(!equipScoreSortDirection);
        let newList = [...productList];
        newList = sortingForObjectList(newList, equipScoreSortDirection, 'equip_point');
        setProductList(newList)
    }

    const onBuyProduct = (item) => {
        // 방송컨텐츠 살때랑 가구 살때랑 구분
        confirmMessage(`${item.name}을 구매합니다.`, () => {
            if (item.price > myMoney) return infoMessage('잔액이 부족합니다.');
            try {
                switch (item.category) {
                    case 'computer':
                        // 램 또는 GPU 살때는 인덱스를 정해줘야 바꿀수 있음
                        if (item.tag[0] === 'RAM') {
                            dispatch(buyRam(item));
                        } else if (item.tag[0] === 'GPU') {
                            dispatch(buyGpu(item));
                        } else {
                            dispatch(buyComputer(item));
                        }
                        break;
                    default:
                        dispatch(buyEquipment(item));
                        break;

                }
                dispatch(payMoney(item.price));
            } catch (error) {
                console.log(error);
                errorMessage(`오류가 발생하였습니다. 다시 시도해주세요.`)
            }

        })
    }

    return (
        <div className="shop_modal">
            <div className={'search_bar'}>
                <div>
                    카테고리
                </div>
                <select onClick={(e) => e.stopPropagation()} value={selectedCategory} onChange={e => setSelectedCategory(e.target.value)} >
                    {furniture_category.map(item => {
                        return (
                            <option value={item.value} key={item.value}>
                                {item.name}
                            </option>
                        )
                    })}
                </select>
                <div>
                    상품명
                </div>
                <input placeholder={'상품명,태그'} value={searchText} onChange={e => setSearchText(e.currentTarget.value)} />
            </div>
            <div className={'search_bar'}>
                <div onClick={onChangeEquipSort}> 장비 점수{equipScoreSortDirection ? '▲' : '▼'} </div>
                <div onClick={onChangePriceSort}> 가격 {priceSortDirection ? '▲' : '▼'}  </div>
                <div style={{ gridColumn: 'span 2' }}>
                    <CustomButton
                        type={'gray'}
                        onClick={() => setProductList(furniture_product)}
                        style={{ borderWidth: '2px' }}
                    >
                        검색 초기화
                    </CustomButton>
                </div>
            </div>
            <div className={'shop_list'}>
                {productList.map(item => {
                    const { tag, lvl } = item;
                    let flag; // 보유중
                    let imgSource; // 이미지 소스

                    switch (item.category) {
                        case 'computer':
                            if (computer[item.tag[0]]) {
                                if (computer[item.tag[0]].key === item.key) flag = true;
                                imgSource = imgs[`${tag[0]}_${lvl}`];
                            }
                            break;
                        default:
                            if (equipment[item.tag[0]]) {
                                if (equipment[item.tag[0]].key === item.key) flag = true;
                                imgSource = imgs[`${tag[0]}_${lvl}`];
                                break;
                            }

                    }

                    return (
                        <div className={'product_box'} key={item.name}>
                            <div className={'product_image'} style={{ background: `center / contain no-repeat url(${imgSource || emptyProduct})` }} />
                            <div className={'product_name'}>{item.name}</div>
                            <div className={'buy_btn vc'}>
                                {
                                    flag ?
                                        <CustomButton type={'confirm'}>보유중</CustomButton>
                                        :
                                        <CustomButton onClick={() => onBuyProduct(item)}>구매</CustomButton>
                                }

                            </div>
                            {/* <div className={'product_category'}>카테고리 : {item.category}</div> */}
                            {item.lvl && <div>레벨 : {item.lvl}</div>}
                            {item.ramslot && <div>슬롯 : 램{item.ramslot} 개 GPU : {item.gpuslot}</div>}
                            {item.category === 'computer' && item.tag[0] !== 'COMPUTER' && <div>장비 점수 : {Math.ceil(item.equip_point)}</div>}
                            <div className={'product_price'}>{item.tag[0] === 'HOUSE' ? '월세' : '가격'} : {AddComma(item.price)}</div>
                            <div className={'tag_list'}>{item.tag.map(tag => (
                                <div className={'tag'} key={tag}>{tag}</div>
                            ))}</div>
                        </div>
                    )
                })}
            </div>
        </div>
    )
})

export default FurnitureShopComponent
