import React, { memo, useRef } from 'react'
import { useEffect } from 'react'
import { useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import CustomButton from '../../../../../../UI/Button/CustomButton'
import { infoMessage } from '../../../../../../UI/Message/message'
import CustomModal from '../../../../../../UI/Modal/CustomModal'
import { AddComma, shuffle } from '../../../../../../util/commonMethod'
import { getMoney, payMoney } from '../../../../../../_actions/character_action'
import { BlackJackCards } from './blackJackAsset'
import BlackJackGuide from './BlackJackGuide'


const syncPromise=(cb,time)=>{

    return new Promise((resolve,reject)=>{
        setTimeout(()=>{
            cb();
            resolve();
        },time)
    })

}

const BlackJackModal = memo((props) => {
    const {money: { value: myMoney }} = useSelector(state=>state.character);
    const [guideModalOpen, setGuideModalOpen] = useState(false);
    
    //블랙잭 이기면 2배 
    //블랙잭은 상금 2.5배 (150%) 가져옴

    // 힛
    // 스테이

    // 21에 근접하는 사람이 이김

    // 딜러는 17이상이 될때 까지 뽑음
    // A는 1이될수도 11이 될 수 도 있음
    // 플레이어는 모두 공개
    // 딜러는 1장만 공개
    const cards = useRef([]);
    const [play, setPlay] = useState(false);
    const [betMoney, setBetMoney] = useState('');
    const [msg, setMsg] = useState('배팅금액을 설정 후 시작')
    const dispatch = useDispatch();

    const [cardSplit, setCardSplit] = useState(false);
    const [showResult, setShowResult] = useState(false);

    //카드 보여주는 용
    const [dealerCard, setDealerCard] = useState([]);
    const [playerCard, setPlayerCard] = useState([]);
    //카트 계산용 REF
    const dealerRef = useRef([]);
    const playerRef = useRef([]);


    useEffect(async () => {
        if (play) {
            setCardSplit(true);
            const dealer = [];
            const player = [];
            setMsg('게임을 시작합니다.')
            await syncPromise(
                ()=>{
                    setMsg('카드를 섞고 있습니다.')
                    cards.current = shuffle(JSON.parse(JSON.stringify([...BlackJackCards])));
                },1000)
                .then(res=>{
                    syncPromise(()=>{
                        setMsg('카드를 분배합니다.')
                        return true;
                    },1000)
                })
                .then(res=>{
                    syncPromise(()=>{
                        const d = cards.current.pop();
                        const p = cards.current.pop();
                        dealerRef.current.push(d); dealer.push(d);
                        playerRef.current.push(p); player.push(p);
                        setDealerCard(dealer);
                        setPlayerCard(player);
                        return true;
                    },1000)
                })
                .then(res=>{
                    syncPromise(()=>{
                        const d = cards.current.pop();
                        const p = cards.current.pop();
                        d.hide = true;
                        dealerRef.current.push(d); dealer.push(d);
                        playerRef.current.push(p); player.push(p);
                        setDealerCard(dealer);
                        setPlayerCard(player);
                        setCardSplit(false);

                        if(scoreSum(player) === 21){
                            setTimeout(() => {
                                calcResult();
                            }, 800);
                        }
                        return true;
                    },2000)
                })
        }
    }, [play])

    const onStartGame = () => {
        
        if(betMoney === 0 || betMoney==='') return infoMessage('금액을 입력해주세요.');
        if(betMoney < 10000) return infoMessage('1만원 이하는 배팅 할 수 없습니다.');
        if(myMoney < betMoney) return infoMessage('배팅 금액이 소지금보다 많습니다.');
        setPlay(true);
        dispatch(payMoney(betMoney));
    }

    const onStop = () => {
        setPlay(false);
        setCardSplit(false);
        setShowResult(false);
        setDealerCard([]);
        setPlayerCard([]);
        dealerRef.current = [];
        playerRef.current = [];
        setMsg('배팅금액을 설정 후 시작');
    }

    const scoreSum = (list)=>{
        const ret = JSON.parse(JSON.stringify(list));
        const sort = ret.sort((a,b)=>a.value>b.value ? -1 : 1)
        const sum = sort.reduce((ac,cu)=>{
            if(cu.value === 1){
                if(ac + 11 > 21){
                    return ac + cu.value;
                }else{
                    return ac + 11;
                }
            }else{
                return ac + cu.value;
            }
        },0)

        return sum;
    }

    const onHit = ()=>{
        
        const newCard = cards.current.pop()
        const newList = [...playerCard];

        newList.push(newCard);
        playerRef.current.push(newCard);
        setPlayerCard(newList);

        setMsg(`${newCard.mark} ${newCard.text} 카드 획득`);
        const score = scoreSum(newList);
        if(score > 21){
            setTimeout(()=>{
                setCardSplit(true);
                calcResult();
            },300)
        }else if(score === 21){
            calcResult();
        }
    }
    const onStayGame=()=>{
        //게임 스테이
        // 1. 딜러 카드 공개
        setMsg('딜러의 카드를 공개 합니다.');
        setTimeout(() => {
            const newDealer = [...dealerCard]
            newDealer.map(item => item.hide = false);    
            setDealerCard(newDealer);
            setShowResult(true);

            if(scoreSum(dealerCard) > scoreSum(playerCard)){
                calcResult();
            }else if(scoreSum(dealerCard) < 17){
                setMsg('딜러가 17 미만이면 한장 더 뽑습니다.')
                setTimeout(() => {
                    const d = cards.current.pop();
                    setDealerCard([...dealerCard,d])    
                    dealerRef.current.push(d);
                    calcResult();
                }, 1000);
            }else{
                calcResult();
            }    
        }, 1000);
    }
    
    const calcResult = () => {
        setTimeout(() => {
            setShowResult(true);
            const dealerScore = scoreSum(dealerRef.current);
            const playerScore = scoreSum(playerRef.current);
            // 2. 딜러 근접치
            if (dealerScore > 21) {
                setMsg('플레이어가 이겼습니다.')
                dispatch(getMoney(betMoney*2));
            } else if (playerScore === 21) {

                setMsg('!!!블랙잭!!!')
                infoMessage('블랙잭');
                dispatch(getMoney(betMoney*2.5));

            } else if (playerScore === dealerScore) {

                setMsg('비겼습니다.')
                dispatch(getMoney(betMoney));

            } else if (dealerScore === 21) {

                setMsg('딜러가 이겼습니다.')

            } else if (playerScore > 21) {

                setMsg('딜러가 이겼습니다.')

            } else if (dealerScore < playerScore) {

                setMsg('플레이어가 이겼습니다.')
                dispatch(getMoney(betMoney*2));

            } else if (dealerScore > playerScore) {
                setMsg('딜러가 이겼습니다.');
            }
        }, 500);
    }

    return (
        <>
        <CustomModal
            {...props}
            title={'블랙잭'}
            className={'blackjack_modal'}
            toggle={false}
        >
            
            <div className='game_panel'>
                <div className='dealer'>
                    <div className='card_panel'>
                        {dealerCard.map(card => {
                            const color = (card.mark === '◆' || card.mark === '♥') ? 'red' : 'black';

                            return (
                                <div className={`card ${card.hide ? 'hide' : 'show'}`} style={{ color: color }} key={card.mark+card.text}>
                                    <div className='number'>
                                        {card.text}
                                    </div>
                                    <div className="mark" style={{ color: color }}>
                                        {card.mark}
                                    </div>
                                </div>
                            )
                        })}
                    </div>
                    {showResult && <div className='dealer_sum'>합계 : {scoreSum(dealerCard)}</div>}
                </div>
                <div>
                    {msg}
                </div>
                <div className='player'>
                    <div className="card_panel">
                        {playerCard.map(card => {
                            const color = (card.mark === '◆' || card.mark === '♥') ? 'red' : 'black'
                            return (
                                <div className='card' style={{ color: color }} key={card.mark+card.text}>
                                    <div className='number'>
                                        {card.text}
                                    </div>
                                    <div className="mark" style={{ color: color }}>
                                        {card.mark}
                                    </div>
                                </div>
                            )
                        })}
                    </div>
                    {play &&
                        <>
                            <div className='player_sum'>합계 : {scoreSum(playerCard)}</div>
                            {(!cardSplit && !showResult) &&
                                <div className='play_btn'>
                                    <CustomButton onClick={onHit}>HIT</CustomButton>
                                    <CustomButton type={'danger'} onClick={onStayGame}>STAY</CustomButton>
                                </div>
                            }
                        </>
                    }
                </div>
            </div>
            <CustomButton onClick={()=>setGuideModalOpen(true)}>게임 규칙</CustomButton>
            <div className='btn_wrapper'>
                {play ?
                <>
                <div className='bet_money'>배팅금 : {AddComma(betMoney)}</div>
                </>
                    
                    :
                    <>
                        <input className='bet_money' type={'number'} placeholder={'배팅금 설정'} value={(betMoney).toString()} onChange={(e) => setBetMoney(Number(e.target.value))} />
                        <CustomButton onClick={onStartGame} className={'start_btn'}>
                            시작
                        </CustomButton>
                    </>
                }

            </div>
            <div className='close_wrapper'>
                {!play
                    ?
                    <CustomButton type={'danger'} onClick={()=>props.ModalHandler(false)}>
                        나가기
                    </CustomButton>
                    :
                    <>
                    <div className='hc'>
                            {showResult ?
                                <CustomButton onClick={onStop}>다시 하기</CustomButton>
                                :
                                <>게임 승리 시 {AddComma(betMoney * 2)} 획득!!</>
                            }
                    </div>
                    </>
                }
            </div>
           
        </CustomModal>
        <BlackJackGuide visible={guideModalOpen} ModalHandler={setGuideModalOpen}/>
        </>
    )
})

export default BlackJackModal;