import React, { memo, useEffect, useState } from 'react'
import { useSelector } from 'react-redux';
import CustomModal from '../../../UI/Modal/CustomModal'
import NewsDetailModal from './Section/NewsDetailModal';


const NewsModal = memo((props) => {
    const newsList = useSelector(state => state.newsList);

    const [detailModal, setDetailModal] = useState(false);
    const [selectedNews, setSelectedNews] = useState()

    const onNewsDetail = (news) => {
        setDetailModal(true);
        setSelectedNews(news);
    }

    return (
        <CustomModal
            title={"뉴스 소식"}
            {...props}
            style={{
                height: '70vh',
                maxWidth: '90vw'
            }}
        >
            <div className="news_modal_body" style={{ width: '100%', height: '100%' }}>
                {newsList.length > 0
                    ?
                    newsList.map((item, idx) => {
                        return (
                            <div onClick={() => onNewsDetail(item)} className="news_modal_contents" key={idx} >
                                <div className="thumbnail" style={{ background: `center / contain no-repeat url(${item.image})` }} />
                                <div className="title">
                                    {item.title}
                                </div>
                            </div>
                        )
                    })
                    :
                    <div className={'hc vc'}>
                        새로운 소식이 없습니다.
                    </div>
                }
            </div>

            <NewsDetailModal visible={detailModal} ModalHandler={setDetailModal} news={selectedNews} />
        </CustomModal>
    )
})

export default NewsModal
