import React, { memo, useEffect, useState } from 'react'
import { useDispatch } from 'react-redux';
import { useSelector } from 'react-redux'
import CustomButton from '../../../UI/Button/CustomButton';
import CustomCheckBox from '../../../UI/Input/CheckBox/CustomCheckBox';
import { confirmMessage } from '../../../UI/Message/message';
import CustomModal from '../../../UI/Modal/CustomModal'
import { AddComma, momentToDateString, sortingForObjectList } from '../../../util/commonMethod';
import { fireEditor } from '../../../_actions/editor_action';
import { setStopWorking } from '../../../_actions/video_action';
import VideoManagerComponent from '../Video/VideoManagerComponent';
import EditorEmployeModal from './Section/EditorEmployeModal';

//편집자 관리 하는 모달


const EditorModal = memo((props) => {
    const dispatch = useDispatch();
    const EDITOR_LIST = useSelector(state => state.editorList);
    const youtubeVideo = useSelector(state => state.youtubeVideo);
    const [searchTerm, setSearchTerm] = useState('');
    const [editorAbil, setEditorAbil] = useState(false); //false : 내림 차순 || true : 오름차순
    const [editorCost, setEditorCost] = useState(false);
    const [eidtorList, setEidtorList] = useState([])

    const [editorAddModal, setEditorAddModal] = useState(false); // 편집자 고용 화면
    const [editorWorkSetModal, setEditorWorkSetModal] = useState(false); // 편집 업무 부여
    const [selectedEditor, setSelectedEditor] = useState()

    useEffect(() => {
        setEidtorList(EDITOR_LIST);
    }, [EDITOR_LIST])

    const onAddEditorModal = () => {
        setEditorAddModal(true);
    }

    const onChangeSearchTerm = (e) => {
        setSearchTerm(e.currentTarget.value);
        const text = e.currentTarget.value;
        const newList = EDITOR_LIST.filter(item => item.name.includes(text));
        setEidtorList(newList)
    }

    const editorAbilSort = () => {
        setEditorAbil(!editorAbil);
        const newList = sortingForObjectList(eidtorList, !editorAbil, 'edit_ability');
        setEidtorList(newList)
    }

    const editorCostSort = () => {
        setEditorCost(!editorCost);
        const newList = sortingForObjectList(eidtorList, !editorCost, 'cost');
        setEidtorList(newList)
    }
    const onSetWork = (edt) => {
        setSelectedEditor(edt)
        setEditorWorkSetModal(true);
    }
    const onCloseModal = () => {
        setSelectedEditor();
        props.ModalHandler(false);
    }
    const onFireEditor = (editor) => {
        confirmMessage(`"${editor.name}"<br/>해고 합니다.`, () => {
            dispatch(fireEditor(editor))
        })
    }
    const onStopEditorWorking = (ytb) => {
        dispatch(setStopWorking(ytb))
    }

    return (
        <CustomModal
            {...props}
            title={'편집자 관리'}
            ModalHandler={onCloseModal}
            className={'editor_manager'}
        >
            <div className={'editor_manager_body'}>
                <div className={'search_bar'}>
                    <div> 검색 </div>
                    <input className='search_input' value={searchTerm} onChange={onChangeSearchTerm} placeholder={'편집자 명'} />
                    <div onClick={editorAbilSort}> 편집력 {editorAbil ? '▲' : '▼'} </div>
                    <div onClick={editorCostSort}> 주급 {editorCost ? '▲' : '▼'} </div>
                </div>
                <div className={'editor_list'}>
                    {eidtorList.map((editor, idx) => {
                        const { edit_ability, work_count, cost, name, tag, working_time, key,desc } = editor;
                        const videoKeys = Object.keys(youtubeVideo);
                        const working = [];
                        videoKeys.map(vk => {
                            if (youtubeVideo[vk].editor === key) {
                                working.push(youtubeVideo[vk]);
                            }
                        })
                        // const working = youtubeVideo.filter(_ytb => _ytb.editor === key)
                        return (
                            <div key={idx} className="editor_container">
                                <div className={'name_grid vc'}>
                                    <div>
                                        이름 : {name}
                                    </div>
                                    <div>
                                        <CustomButton type={'danger'} onClick={() => onFireEditor(editor)}>
                                            해고
                                        </CustomButton>
                                    </div>
                                </div>
                                <div className={'abil_grid'}>
                                    <div> 편집능력치 : {edit_ability} </div>
                                    <div> 주급 : {AddComma(cost)} </div>
                                </div>
                                <div> 근무 시간 : {working_time[0]}시 ~ {working_time[1]}시</div>
                                <div> 한줄 평 : {desc}</div>
                                <div> 전문 장르 </div>
                                <div className="tag_container">
                                    {tag.map(item => {
                                        return <span className="tag" key={item}>{item}</span>
                                    })}
                                </div>
                                <div> 동시 편집 가능 영상수 : {work_count} </div>
                                <div> 편집 중인 영상 </div>
                                <div className={'editor_working_list'}>
                                    {working.map(ytb => {
                                        return (
                                            <div className={'editor_working'} key={ytb.key}>
                                                <div className='header'>
                                                    <div className='title'>{ytb.name} </div>
                                                    <CustomButton type={'danger'} onClick={() => onStopEditorWorking(ytb)}>중지</CustomButton>
                                                </div>
                                                <div> 촬영일 : {momentToDateString(ytb.date)} </div>
                                                <div> 진척도 : {ytb.progress} % </div>
                                                <div className="progress_back">
                                                    <div className="progress_rating" style={{ width: `${ytb.progress}%` }}>
                                                        &nbsp;
                                                    </div>
                                                </div>
                                            </div>
                                        )
                                    })}
                                    {working.length <= work_count - 1 &&
                                        <CustomButton
                                            onClick={() => onSetWork(editor)}
                                            style={{ width: '100%' }}
                                        >편집 업무 부여</CustomButton>
                                    }
                                </div>
                            </div>
                        )
                    })}
                    <div className={'editor_add_button'} onClick={onAddEditorModal}> + 편집자 고용</div>
                </div>
            </div>
            <EditorEmployeModal toggle visible={editorAddModal} ModalHandler={setEditorAddModal} />
            <VideoManagerComponent toggle visible={editorWorkSetModal} ModalHandler={setEditorWorkSetModal} editor={selectedEditor} />
        </CustomModal>
    )
})

export default EditorModal
