import React, { memo, useState } from 'react'

const ModalUnfoldPanel = memo(({ text, value, toggle = false, foldStyle, unFoldStyle, onClick , children, textSize='normal'}) => {
    const [fold, setFold] = useState(true);

    const onUnfold = (e) => {
        e.stopPropagation();
        if (onClick) {
            onClick()

        } else if (toggle) {

            setFold(!fold);
        }
    }
    return (
        <div className={`panel ${fold ? 'fold' : 'unfold'}`} style={fold ? foldStyle : unFoldStyle} >
            <div className="title_wrapper" onClick={(e) => onUnfold(e)}>
                <span className={`title ${textSize}`}>{text}</span>
                <div className={'value'}>{value}</div>
            </div>
            <div className="contents">
                {children}
            </div>
        </div>
    )
})

export default ModalUnfoldPanel
