import { BUY_RAM, BUY_GPU, BUY_COMPUTER, SET_COMPUTER } from '../_actions/type';

const initialState = {
    COMPUTER: {
        key: '486 컴퓨터',
        name: '486 컴퓨터',
        category: 'computer',
        tag: ['COMPUTER'],
        lvl: 1,
        ramslot: 1,
        gpuslot: 1,
        price: 150000
    },
    RAM: [
        {
            key: '샘숭 DDR4 4GB 램',
            name: '샘숭 DDR4 4GB 램',
            category: 'computer',
            tag: ['RAM'],
            equip_point: 1.2,
            lvl: 1,
            price: 40000
        },
    ],
    GPU: [
        {
            key: 'K포스 750 그래픽카드',
            name: 'K포스 750 그래픽카드',
            category: 'computer',
            tag: ['GPU'],
            equip_point: 30,
            lvl: 1,
            price: 500000
        },
    ],
    CPU: {
        key: 'info 4650',
        name: 'info 4650',
        category: 'computer',
        tag: ['CPU'],
        lvl: 1,
        equip_point: 20,
        price: 430000
    },
}


export default function (state = initialState, action) {
    switch (action.type) {
        case SET_COMPUTER: return { ...state, ...action.payload };

        case BUY_RAM:
            let newRamList = [...state.RAM]
            if (newRamList.length + 1 > state.COMPUTER.ramslot) { //기존거 삭제 후 추가
                newRamList.splice(0, 1);
                newRamList = [...newRamList, action.payload]
            } else { // 그냥 추가
                newRamList = [...newRamList, action.payload]
            }
            return { ...state, RAM: newRamList }

        case BUY_GPU:
            let newGpuList = [...state.GPU]
            if (newGpuList.length + 1 > state.COMPUTER.gpuslot) { //기존거 삭제 후 추가
                newGpuList.splice(0, 1);
                newGpuList = [...newGpuList, action.payload]
            } else { // 그냥 추가
                newGpuList = [...newGpuList, action.payload]
            }
            return { ...state, GPU: newGpuList }

        case BUY_COMPUTER:
            let newComputer = { ...state };
            const key = action.payload.tag[0]
            newComputer[key] = action.payload;
            return newComputer

        default: return state;
    }
}