import { errorMessage } from '../UI/Message/message';
import { BUY_COIN, DEPOSIT_MONEY, PAY_LOAN_MONEY, SET_ASSET_INFORMATION, TERMINATE_DEPOSIT, WITHDRAW_MONEY, SELL_COIN, SIGN_IN_DEPOSIT, SIGN_IN_LOAN, DEPOSIT_INTEREST_APPLY, CHECK_MY_COIN } from '../_actions/type';



const initialState = {
    deposit: [],
    loan: [],
    coin: []
}

export default function (state = initialState, action) {
    let newList = [];
    let index = -1;
    switch (action.type) {
        case SET_ASSET_INFORMATION:
            return { ...state, ...action.payload }

        case BUY_COIN: //코인 구매
            newList = [...state.coin];
            index = newList.findIndex(coin => coin.key === action.payload.key);

            if (index > -1) {
                newList[index] = action.payload;
            } else {
                newList = [...newList, action.payload];
            }
            return { ...state, coin: newList }


        case WITHDRAW_MONEY: //출금
            newList = [...state.deposit];
            index = newList.findIndex(deposit => deposit.key === action.payload.key);
            if (index > -1) {
                if (Number(newList[index].money) - Number(action.payload.withdraw) > 0) {
                    newList[index].money = newList[index].money-action.payload.withdraw;
                    return { ...state, deposit: newList }
                } else if(Number(newList[index].money) - Number(action.payload.withdraw) === 0){
                    newList.splice(index, 1);
                    return { ...state, deposit: newList }
                }else{

                    errorMessage('출금 금액이 입금금액을 초과하였습니다.')
                    return { ...state}
                }
            }
            

        case DEPOSIT_MONEY: //입금
            newList = [...state.deposit];
            index = newList.findIndex(deposit => deposit.key === action.payload.key);
            newList[index] = {
                ...newList[index],
                money: Number(newList[index].money) + Number(action.payload.deposit)
            }
            return { ...state, deposit: newList }
        case TERMINATE_DEPOSIT: //예금 상품 삭제
            newList = [...state.deposit];
            index = newList.findIndex(deposit => deposit.key === action.payload.key);
            newList.splice(index, 1);

            return { ...state, deposit: newList }

        case DEPOSIT_INTEREST_APPLY: //예금 이자 적용
            // 내 예금 상품
            const myDeposit = [...state.deposit];

            myDeposit.map(depo => {
                if (depo.month_amount) { //매달 납입금이 있는 것만, 자유적금은 바로 통장에 꼽기
                    depo.money = (depo.money * (1 + depo.interest)) + depo.month_amount; //기존 금액 더하기*이자율 더하기 새입금액
                }
            })
            return { ...state, deposit: myDeposit };

        case PAY_LOAN_MONEY:
            newList = [...state.loan];
            index = newList.findIndex(loan => loan.key === action.payload.key);

            if (action.payload.money > 0) {
                newList[index] = { ...action.payload }
            } else if (index > -1) {
                newList.splice(index, 1);
            }
            return { ...state, loan: newList }

        case SELL_COIN:
            newList = [...state.coin];
            index = newList.findIndex(coin => coin.key === action.payload.key);

            if (action.payload.totalCount > 0) {
                newList[index] = { ...action.payload }
            } else if (index > -1) {
                newList.splice(index, 1);
            }
            return { ...state, coin: newList }

        case SIGN_IN_DEPOSIT: //예금 상품 가입
            newList = [...state.deposit, action.payload];
            return { ...state, deposit: newList }

        case SIGN_IN_LOAN:// 대출 가입
            newList = [...state.loan, action.payload];
            return { ...state, loan: newList }
            
        case CHECK_MY_COIN:
            newList = [...state.coin];
            const delistingCoinList =  action.payload;
            //거래중지 코인 내 자산에서 삭제 시킴
            delistingCoinList.map(item=>{
                index = newList.findIndex(c=>c.key === item.key);
                if(index>-1){
                    newList.splice(index,1);
                }
            })
            return {...state , coin : newList};
        default: return state;
    }
}