import {SET_EFFECT_TOGGLE, SET_EFFECT_VOLUME, SET_MUSIC_TOGGLE, SET_MUSIC_VOLUME,SET_SETTING} from './type';

export function setMusicVolume(sound){
    return {
        type : SET_MUSIC_VOLUME,
        payload : sound
    }
}
export function setMusicToggle(toggle){    
    return{
        type : SET_MUSIC_TOGGLE,
        payload : toggle
    }
}   
export function setEffectVolume(sound){
    return{
        type : SET_EFFECT_VOLUME,
        payload : sound
    }
}
export function setEffectToggle(toggle){
    return{
        type : SET_EFFECT_TOGGLE,
        payload : toggle
    }
}
export function setSetting(setting){
    return{
        type : SET_SETTING,
        payload : setting
    }
}