import React, { useRef, useState } from 'react';
import { useDispatch } from 'react-redux';
import { withRouter } from 'react-router-dom';
import CustomButton from '../../UI/Button/CustomButton';
import { confirmMessage } from '../../UI/Message/message';
import { setCharacterInfo } from '../../_actions/character_action';
import { setDifficuty } from '../../_actions/difficulty_action';

function CharacterStatusComponent(props) {

    const randomValue=()=>{
        let value = Math.random()*7
        value = value < 1 ? 1 : Math.round(value);
        return value;
    }

    const [name, setName] = useState("조철봉");
    const [face, setFace] = useState(randomValue());
    const [voice, setVoice] = useState(randomValue());
    const [speach, setSpeach] = useState(randomValue());
    const [health, setHealth] = useState(randomValue());
    const [physical, setPhysical] = useState(randomValue());
    const [personality, setPersonality] = useState(randomValue());
    const [edit, setEdit] = useState(5);

    const [diff, setDiff] = useState(1);

    const limit = useRef(5); //남은 재분배 횟수

    const dispatch = useDispatch();

    const changeStatus = () => {
        setFace(randomValue())
        setVoice(randomValue())
        setSpeach(randomValue())
        setHealth(randomValue())
        setPhysical(randomValue())
        setPersonality(randomValue())
        setEdit(randomValue())
    }
    const onNext=()=>{
        confirmMessage(`해당 능력치로 진행하시겠습니까?`,()=>{
            const body={
                name: {
                    text: '캐릭터 명',
                    value: name,
                },
                face: {
                    text: '외모',
                    value: face,
                },
                voice: {
                    text: "목소리",
                    value: voice
                },
                speach: {
                    text: '화술',
                    value: speach
                },
                health: {
                    text: '건강',
                    value: health
                },
                physical: {
                    text: '신체 활력',
                    value: physical
                },
                personality: {
                    text: '성격',
                    value: personality
                },
                edit: {
                    text: '편집능력',
                    value: edit
                },
            }

            dispatch(setCharacterInfo(body));
            dispatch(setDifficuty(diff));
            props.history.push('/client/landing');
        })
    }

    return (
        <div className='character_status_wrapper'>
            <div className="body">
                <div className="row">
                    능력치를 설정해주세요.
                </div>
                <div className="row">
                    <CustomButton className={'dice_btn'} onClick={changeStatus}>
                        <span>능력치 재분배</span><i className="bi bi-shuffle"></i>
                    </CustomButton>
                </div>
                {/* <div className="row">
                    남은 재분배 횟수 : 
                </div> */}
                <div className='row'>
                    <div className='title'>
                        이름
                    </div>
                    <input className='contents' type={'text'} value={name} onChange={e => setName(e.currentTarget.value)} />
                </div>
                <div className='row'>
                    <div className="title">외모</div>
                    <div className='contents'>{face}</div>
                </div>
                <div className='row'>
                    <div className="title">목소리</div>
                    <div className='contents'>{voice}</div>
                </div>
                <div className='row'>
                    <div className="title">화술</div>
                    <div className='contents'>{speach}</div>
                </div>
                <div className='row'>
                    <div className="title">건강</div>
                    <div className='contents'>{health}</div>
                </div>
                <div className='row'>
                    <div className="title">신체활력</div>
                    <div className='contents'>{physical}</div>
                </div>
                <div className="row">
                    <div className="title">성격</div>
                    <div className='contents'>{personality}</div>
                </div>
                <div className="row">
                    <div className="title">편집능력</div>
                    <div className='contents'>{edit}</div>
                </div>
                <div className='row'>
                    <div className='title'>난이도</div>
                    <div className="contents">
                        <select value={diff} onChange={(e)=>setDiff(Number(e.target.value))}>
                            <option value={1}>쉬움</option>
                            <option value={2}>보통</option>
                            {/* <option value={3}>하드</option> */}
                        </select>
                    </div>
                </div>
                <div className="row">
                    {diff === 1 && 
                    <>
                    <div className="title">
                        쉬움
                    </div>
                    <div className="contents">
                        방송 컨텐츠 : 게임<br/>
                        자산관리로 코인장 사용 가능
                    </div>
                    </>}
                    {diff === 2 && 
                    <>
                    <div className="title">
                        보통
                    </div>
                    <div className="contents">
                        방송 컨텐츠 : 게임 , 운동<br/>
                        자산관리로 겜블링(블랙잭) 사용 가능
                    </div>
                    </>}
                </div>
                <div className="row">
                    <CustomButton className='dice_btn' type={'danger'}
                    onClick={onNext}
                    >진행하기</CustomButton>
                </div>
            </div>

        </div>
    );
}

export default withRouter(CharacterStatusComponent);
