import moment from "moment"

export const momentToDateString = (args) => {
    const info = moment(args);
    const year = info.year();
    const month = info.month() + 1;
    const date = info.date();

    return `${year}년 ${month}월 ${date}일`;
}
export const momentToDatetimeString = (args) => {
    const info = moment(args);
    const year = info.year();
    const month = info.month() + 1;
    const date = info.date() < 10 ? `0${info.date()}`:info.date();
    const hour = info.hour() < 10 ? `0${info.hour()}`:info.hour();;
    const minute = info.minute() < 10 ? `0${info.minute()}`:info.minute();;

    return `${year}년 ${month}월 ${date}일 ${hour}시 ${minute}분`;
}

export const momentToTimerDateString = (args) => {
    const info = moment(args);
    const month = info.month() + 1;
    const date = info.date();

    let day = '';
    switch (info.day()) {
        case 0: day = '일요일'; break;
        case 1: day = '월요일'; break;
        case 2: day = '화요일'; break;
        case 3: day = '수요일'; break;
        case 4: day = '목요일'; break;
        case 5: day = '금요일'; break;
        case 6: day = '토요일'; break;
    }

    return `${month}월 ${date}일 ${day}`;
}

export const momentTimeString = (args) => {
    const info = moment(args);

    const hour = info.hour() < 10 ? '0' + info.hour() : info.hour();
    const minute = info.minute() < 10 ? '0' + info.minute() : info.minute();
    const seconds = info.seconds();

    return `${hour}:${minute}`;
}

export const TimeComparison = (targetTime, nowTime) => {

    const target = moment(targetTime);
    const now = moment(nowTime);
    const result = moment.duration(target.diff(now)).asMilliseconds();

    const sec = Math.floor(result / 1000);
    const hour = Math.floor(sec / 3600);
    const minute = Math.floor((sec % 3600) / 60);
    const seconds = Math.floor(((sec % 3600) % 60))

    return `${hour < 10 ? `0${hour}` : hour}:${minute < 10 ? `0${minute}` : minute}`
}

//정렬
export const sortingForObjectList = (list, direction, key) => {
    const newList = [...list];
    if (direction) {// 오름 차순
        newList.sort((a, b) => {
            if (a[key] > b[key]) {
                return 1
            } else if (a[key] < b[key]) {
                return -1
            } else {
                return 0;
            }
        })
    } else {
        newList.sort((a, b) => {
            if (a[key] > b[key]) {
                return -1
            } else if (a[key] < b[key]) {
                return 1
            } else {
                return 0;
            }
        })
    }
    return newList;
}
//날짜 정렬
export const sortingDateForList = (list, direction, key) => {
    let newList = [...list];
    newList.sort((a, b) => {
        if (moment(a[key]) > moment(b[key])) {
            if (direction) {
                return 1;
            } else {
                return -1;
            }
        } else if (moment(a[key]) < moment(b[key])) {
            if (direction) {
                return -1;
            } else {
                return 1;
            }
        } else {
            return 0;
        }
    })

    return newList;
}
/**
 * 콤마 넣어주는 함수
 * input type Number
 * output type String
 * **/
export const AddComma = (num) => {
    if (!num) return 0;
    var regexp = /\B(?=(\d{3})+(?!\d))/g;
    num = num * 100;
    num = Math.floor(num) / 100;

    return num.toString().replace(regexp, ',');
}

// 컴퓨터 장비 스코어 계산
export const calcComputerScore = (computer) => {
    const cpuScore = computer.CPU.equip_point;
    const gpuScore = computer.GPU.reduce((acc, current) => { return acc + current.equip_point }, 0);
    // const gpuScore = computer.gpu.reduce((acc,current)=>{return acc + current.equip_point},0);
    //램 스코어
    let ramScore = 2;
    if (computer.RAM.length > 1) {
        ramScore = 0;
        computer.RAM.map((item, idx) => {
            if (idx > 0) {
                ramScore += (item.equip_point * 0.15)
            } else {
                ramScore += item.equip_point;
            }
        })
    } else {
        ramScore = computer.RAM.reduce((acc, current) => { return acc + current.equip_point }, 0)
    }

    return (cpuScore + gpuScore) * ramScore;
}

// JSON to Array
export const JSONtoArray = (jsonObject) => {
    const arr = []
    const keys = Object.keys(jsonObject);
    keys.map(key => {
        arr.push(jsonObject[key]);
    })

    return arr;
}

//JSON key length
export const JSONKeyLength = (jsonObject) => {
    const keys = Object.keys(jsonObject);
    return keys.length
}

// 랜덤 인덱스
export const getRadomIndex = (min,max)=>{
    return Math.round(( Math.random () * (max - min) ) + min)   
}

//array shuffle

export const shuffle =(arr=[])=>{
    const ret = [...arr];
    ret.sort(()=>Math.random() < 0.5 ? 1 : -1)
    return ret;
} 