import moment from 'moment';
import React, { memo, useState } from 'react'
import { useDispatch } from 'react-redux';
import { useSelector } from 'react-redux';
import KrwIcon from '../../../../../../scss/icon/KrwIcon';
import CustomButton from '../../../../../../UI/Button/CustomButton';
import { confirmMessage } from '../../../../../../UI/Message/message';
import { AddComma, momentToDateString } from '../../../../../../util/commonMethod';
import { terminateDeposit } from '../../../../../../_actions/asset_action';
import { getMoney } from '../../../../../../_actions/character_action';
import DepositModal from './DepositModal';

const AssetDeposit = memo((props) => {

    const asset = useSelector(state => state.asset);
    const currentTime = useSelector(state => state.currentTime);
    const [depositModal, setdepositModal] = useState(false);

    const [selecteDeposit, setSelecteDeposit] = useState();
    const dispatch = useDispatch();


    const onDepositModalOpen = (item) => {
        setSelecteDeposit(item)
        setdepositModal(true);
    }

    const onTerminate = (item) => {
        const name = item.name;
        let now = moment(currentTime);
        let contract = moment(item.start);
        const diff = now.diff(contract, 'months');
        const refund = (diff + 1) * item.month_amount;
        confirmMessage(`<span className="strong">${name}</span>을 해지합니다.<br/><span class="strong">${AddComma(refund)}</span>원이 반환됩니다.`, () => {
            dispatch(terminateDeposit(item))
            dispatch(getMoney(refund));
        })
    }

    return (
        <>
            <div className={'title'}>
                <span>은행 예금액</span>
                <span className={'money'}>
                    <KrwIcon className={'krw_icon'} />
                    {AddComma(asset.deposit.reduce((acc, current) => { return acc + current.money }, 0))}
                </span>
            </div>
            <div className={'list'}>
                <div className={'list_contents header'}>
                    <span>상품명</span>
                    <span>예금액</span>
                    <span>금리</span>
                    <span>월 납입금</span>
                    <span>가입일</span>
                    <span>만기일</span>
                    <span>비고</span>
                </div>
                {asset.deposit.map(item => {
                    const { key, name, money, interest, month_amount, expire, start } = item;
                    return (
                        <div className={'list_contents'} key={key}>
                            <span>{name}</span>
                            <span>{AddComma(Math.round(money))}</span>
                            <span>{(interest * 100).toFixed(2)}%</span>
                            <span>{month_amount ? AddComma(month_amount) : <CustomButton onClick={() => onDepositModalOpen(item)} style={{ width: '70%', minWidth: 'auto' }}>입츨금</CustomButton>}</span>
                            <span>{start ? momentToDateString(start) : 'N'}</span>
                            <span>{expire ? momentToDateString(expire) : 'N'}</span>
                            <span>{month_amount ? <CustomButton type={'danger'} style={{ width: '70%', minWidth: 'auto' }} onClick={() => onTerminate(item)}>해지</CustomButton> : ''}</span>
                        </div>
                    )
                })}
            </div>
            <DepositModal visible={depositModal} ModalHandler={setdepositModal} selecteDeposit={selecteDeposit} />
        </>
    )
})

export default AssetDeposit
