import { useDispatch, useSelector } from 'react-redux';
import axios from 'axios';
import { setLoading } from './_actions/progress_action';
import LoadingComponent from './component/Loading/LoadingComponent';
import { Redirect, Route, Switch } from 'react-router';
import LoginComponent from './component/Login/LoginComponent';
import LayoutComponent from './component/Layout/LayoutComponent';
import MainComponent from './component/Main/MainComponent';
import "moment/locale/ko";
import { useEffect } from 'react';
import LandingComponent from './component/Landing/LandingComponent';
import CharacterChoiceComponent from './component/Landing/CharacterChoiceComponent';
import IntroComponent from './component/Landing/IntroComponent';
import CharacterStatusComponent from './component/Landing/CharacterStatusComponent';
import EndingComponent from './component/Landing/EndingComponent';

function App() {
  const dispatch = useDispatch();
  //리퀘스트전
  axios.interceptors.request.use(function (req) {
    dispatch(setLoading(true));
    return req;
  }, function (err) {
    dispatch(setLoading(false));
    return Promise.reject(err)
  })

  // 리스폰스 후
  axios.interceptors.response.use(function (res) {
    dispatch(setLoading(false));
    return res;
  }, function (err) {
    dispatch(setLoading(false));
    return Promise.reject(err)
  });



  return (
    <>
      <LoadingComponent />
      <Switch>

        <Route exact path="/">
          <Redirect to="/client/choice" />
        </Route>
        <Route path="/user/(.+)" render={() => (
          <>
            <Route exact path="/user/Login" component={LoginComponent} />
          </>
        )} />
        <Route path="/client/(.+)" render={() => (
          <LayoutComponent>
            {/* <NavbarComponent /> */}
            <Route exact path="/client/choice" component={CharacterChoiceComponent} />
            <Route exact path="/client/intro" component={IntroComponent} />
            <Route exact path="/client/status" component={CharacterStatusComponent} />
            <Route exact path="/client/ending" component={EndingComponent} />
            <Route exact path="/client/landing" component={LandingComponent} />
            <Route exact path="/client/main" component={MainComponent} />
          </LayoutComponent>
        )} />
      </Switch>
    </>
  );
}

export default App;
