import { RECOVER_STRESS, SET_STRESS, UP_STRESS } from './type';


export function setStress(stress) {
    return {
        type: SET_STRESS,
        payload: stress
    }
}

export function recoverStress(number) {

    return {
        type: RECOVER_STRESS,
        payload : number
    }
}

export function upStress(number) {
    return {
        type: UP_STRESS,
        payload: number
    }

}