import React, { memo, useState } from 'react'
import { useSelector } from 'react-redux';
import CustomModal from '../../../UI/Modal/CustomModal'
import BankStatus from './Section/BankStatus/BankStatus';
import BlackJackComponent from './Section/Gamble/BlackJack/BlackJackModal';
import CoinStatus from './Section/Coin/CoinStatus';
import MyAsset from './Section/MyAsset/MyAsset';
import GambleComponent from './Section/Gamble/GambleComponent';

const AssetManagerModal = memo((props) => {

    const [tabNumber, setTabNumber] = useState(1);
    const difficulty =  useSelector(state => state.difficulty);


    return (
        <CustomModal
            {...props}
            title={'자산 관리'}
            className={'asset_manager_wrapper'}
        >
            <div className={'asset_body'}>
                <div>
                    {tabNumber === 1 && <MyAsset />}
                    {tabNumber === 2 && <BankStatus />}
                    {tabNumber === 3 &&
                        <>
                            {difficulty === 1 && <CoinStatus />}
                            {difficulty === 2 && <GambleComponent/>}
                            {difficulty === 3 && <CoinStatus />}
                        </>
                    }
                </div>
                <div className={'tab_container'}>
                    <div className={`tab_button ${tabNumber === 1 ? 'selected' : ''}`}>
                        <div className={'hc vc'} onClick={() => setTabNumber(1)} >
                            나의 자산
                        </div>
                    </div>
                    <div className={`tab_button ${tabNumber === 2 ? 'selected' : ''}`}>
                        <div onClick={() => setTabNumber(2)} className={'hc vc'} >
                            은행
                        </div>
                    </div>
                    <div className={`tab_button ${tabNumber === 3 ? 'selected' : ''}`}>
                        <div onClick={() => setTabNumber(3)} className={'hc vc'}>
                            {difficulty === 1 ? '코인장' : '도박장'}
                        </div>
                    </div>
                </div>
            </div>
        </CustomModal>
    )
})

export default AssetManagerModal
