import { ADD_VIDEO, SET_VIDEO_LIST, SET_VIDEO_EDITOR, SET_VIDEO_TITLE, REMOVE_VIDEO, SET_VIDEO_MY_EDIT, SET_VIDEO, UP_PROGRESS_MINE, UP_EDITOR_PROGRESS, SET_STOP_WORKING, DELETE_VIDEO_RATING } from './type';

export function addVideo(voice, speach, MIC, face, CAMERA, tag, contents, personality, time, currection, category) {
    return {
        type: ADD_VIDEO,
        payload: { voice, speach, MIC, face, CAMERA, tag, contents, personality, time, currection, category }
    }
}

export function setVideoList(list) {
    return {
        type: SET_VIDEO_LIST,
        payload: list
    }
}

export function setWorkEditor(editor, video) {
    return {
        type: SET_VIDEO_EDITOR,
        payload: { editor, video }
    }
}
export function setWorkMine(video) {
    return {
        type: SET_VIDEO_MY_EDIT,
        payload: video
    }
}

export function setVideoTitle(video, name) {

    return {
        type: SET_VIDEO_TITLE,
        payload: { video, name }
    }
}

export function removeVideo(video) {
    return {
        type: REMOVE_VIDEO,
        payload: video
    }
}

export function setVideo(contents) {
    return {
        type: SET_VIDEO,
        payload: contents
    }
}

export function upMyVideoProgress(edit, tag) {
    return {
        type: UP_PROGRESS_MINE,
        payload: { edit, tag }
    }
}

export function upEditorProgress(newTime, youtubeVideo, editorList) {

    return {
        type: UP_EDITOR_PROGRESS,
        payload: { newTime, youtubeVideo, editorList }
    }

}

export function setStopWorking(video) {
    return {
        type: SET_STOP_WORKING,
        payload: video,
    }
}

export function deleteRatingVideos(ratings){
    return {
        type : DELETE_VIDEO_RATING,
        payload : ratings
    }
}