import React, { memo, useEffect, useState } from 'react'
import { useDispatch } from 'react-redux';
import { useSelector } from 'react-redux';
import CustomButton from '../../../../../UI/Button/CustomButton';
import { errorMessage, infoMessage } from '../../../../../UI/Message/message';
import CustomModal from '../../../../../UI/Modal/CustomModal'
import { AddComma } from '../../../../../util/commonMethod';
import { buyCoin } from '../../../../../_actions/asset_action';
import { payMoney } from '../../../../../_actions/character_action';

const CoinBuyModal = memo((props) => {
    const { coin } = props;

    const { coin: myCoinList } = useSelector(state => state.asset);
    const { money: { value: myMoney } } = useSelector(state => state.character);

    const [coinName, setCoinName] = useState('');//코인명
    const [coinPrice, setCoinPrice] = useState(0);//코인가격
    const { coinList } = useSelector(state => state.coin);

    const [reserve, setReserve] = useState(0);//보유량
    const [buyCount, setBuyCount] = useState(0); //구매량
    const dispatch = useDispatch();

    useEffect(() => {
        if (coin) {
            const myCoin = myCoinList.find(myCoin => myCoin.key === coin.key);
            if (myCoin) {
                setReserve(myCoin.totalCount);
            }
            setCoinName(coin.name)
        }
    }, [coin]);
    useEffect(() => {
        if (coin) setCoinPrice(coinList.find(c => c.key === coin.key).price)
    }, [coin, coinList])

    const onCountChange = (e) => {
        setBuyCount(Number(e.currentTarget.value));
    }
    const onCheckEnalbleBuyCount = ()=>{
        const _enableCount = Math.floor(myMoney/coin.price);
        setBuyCount(Math.round(_enableCount));
    }

    const onBuyCoin = () => {
        if(coinPrice < 1) return infoMessage('1원 이하의 코인은 구매할 수 없습니다.')
        if(buyCount < 1) return infoMessage('구매량을 입력해주세요.')
        
        const buyPrice = coinPrice * Number(buyCount);

        if (myMoney - buyPrice < 0) return errorMessage('보유 금액을 초과하였습니다.');

        const myCoin = myCoinList.find(myCoin => myCoin.key === coin.key);
        const body = {
            key: coin.key,
            name: coin.name,
        }
        if (myCoin) {
            body.averagePrice = ((myCoin.averagePrice * myCoin.totalCount) + buyPrice) / (myCoin.totalCount + Number(buyCount));
            body.totalCount = Number(myCoin.totalCount) + Number(buyCount)
        } else {
            body.averagePrice = coinPrice;
            body.totalCount = buyCount
        }
        dispatch(buyCoin(body));
        dispatch(payMoney(coinPrice * Number(buyCount)));
        setBuyCount(0);
        props.ModalHandler(false);
    }

    return (
        <CustomModal
            {...props}
            title={`${coinName} 구매`}
            className={'coin_buy_modal'}
        >
            <div className={'coin_buy'}>
                <div className={'title hc'}>{coinName}  사자!</div>
                <div className={'price hc'}>개당 가격 : {AddComma(coinPrice)}</div>
                <div className={'check hc'} onClick={onCheckEnalbleBuyCount}>구매가능량 확인</div>
                <div className={'input hc'}>구매량<input type="number" min={0} value={(buyCount).toString()} onChange={onCountChange} />개</div>
                <div className={'button hc'}>
                    <CustomButton onClick={onBuyCoin}>사자</CustomButton>
                    <CustomButton type={'danger'} onClick={() => props.ModalHandler(false)}>취소</CustomButton>
                </div>
            </div>
        </CustomModal>
    )
})

export default CoinBuyModal
