import React from 'react'
import { useEffect } from 'react';
import { useState } from 'react';
import { useSelector } from 'react-redux';
import { Categories } from '../../../../Assets/ActionAsset';
import { contents_product } from '../../../../Assets/shopAsset';
import CustomButton from '../../../../UI/Button/CustomButton';
import CustomModal from '../../../../UI/Modal/CustomModal'
import { AddComma, momentToDatetimeString } from '../../../../util/commonMethod';
import AdvSubmitModal from './AdvSubmitModal';


function AdvQuestDetailModal(props) {
    const {quest} = props;
    const {name:{value:myName}} = useSelector(state=>state.character);

    const [submitModalOpen, setSubmitModalOpen] = useState(false);

    useEffect(() => {
        
      
    }, [props.visible])
    const onAcceptQuest = ()=>{
        setSubmitModalOpen(true);
    }

    const renderingContents=()=>{
        if(quest?.client.category === Categories.game){
            return(
                <>
                    이번 저희 {quest?.client.name}는 {myName}님에게 저희 게임 {quest?.contents.name}의 홍보를 의뢰 드리고자 합니다.
                    <br />
                    <br />
                    이번 홍보는 저희의 새로운 프로모션을 홍보하기 위함이며, 이에 맞춰 높은 게임 이해도를 가지신 {myName}님의 도움이 있다면
                    저희 {quest?.client.name}에게 이번이 좋은 기회가 되리라 생각합니다.
                    <br />
                    <br />
                    - 의뢰 내용
                    <br />
                    <div className='title'>* 게임명 </div>
                    <div className="contents">{quest?.contents.name} </div>
                    <div className='title'>* 기한 날짜</div>
                    <div className="contents">
                        {momentToDatetimeString(quest?.quest.period)}까지
                    </div>
                </>
            )
        }else if(quest?.client.category === Categories.exercise){
            return(
                <>
                    이번 저희 {quest?.client.name}는 {myName}님에게 저희의 헬창 프로젝트 {quest?.contents.name}의 홍보를 의뢰 드리고자 합니다.
                    <br />
                    <br />
                    이번 홍보는 전국민의 3대 500을 달성하기 위함이며, 이에 맞춰 헬창으로의 가능성을 가지신 {myName}님의 도움이 있다면
                    저희 {quest?.client.name}에게 이번이 좋은 기회가 되리라 생각합니다.
                    <br />
                    <br />
                    - 의뢰 내용
                    <br />
                    <div className='title'>* 운동 프로그램명 </div>
                    <div className="contents">{quest?.contents.name} </div>
                    <div className='title'>* 기한 날짜</div>
                    <div className="contents">
                        {momentToDatetimeString(quest?.quest.period)}까지
                    </div>
                </>
            )
        }else{
            return(
                <>
                </>
            )
        }
    }
    
    return (
        <CustomModal 
        {...props}
        title={'의뢰 상세 내용'}
        className={'quest_detail_modal'}
        >
            <div className='detail_script'>
                안녕하세요. {myName}님
                <br/>
                <br/>
                {renderingContents()}
                <div className='title'>* 필요 유튭각</div>
                <div className="contents"><span className="star">★</span>{quest?.quest.rating}</div>
                <div className='title'>* 광고 보상금</div>
                <div className="contents">{AddComma(quest?.quest.reward)}원</div>
                <br />
                <br />
                내용을 확인하여 저희에게 제출 해주시면 됩니다. 
                <br />
                <br />
                이상입니다.
                잘 부탁드립니다.
                <br/>
                <br/>
                {quest?.client.name} 드림
            </div>
            <div className='detail_button'>
                <CustomButton type={'confirm'} onClick={onAcceptQuest}>
                    편집본 제출
                </CustomButton>
            </div>
            {quest?.contents ?
                <AdvSubmitModal quest={quest} visible={submitModalOpen} ModalHandler={setSubmitModalOpen} />
                :
                <></>
            }
        </CustomModal>
    )
}

export default AdvQuestDetailModal