import React, { memo } from 'react';
import { useState } from 'react';
import CustomModal from '../../../UI/Modal/CustomModal';
import NoticeDetail from './NoticeDetail';
import NoticeList from './NoticeList';

const NoticeModal = memo((props) =>{

    const [detailModalOpen, setDetailModalOpen] = useState(false);
    const [detailNotice, setDetailNotice] = useState({});

    const onClickNotice=(notice)=>{
        setDetailModalOpen(true);
        setDetailNotice(notice)
    }

    const onOpenYoutubeLink=()=>{
        window.open(`https://www.youtube.com/c/%EB%B9%85%ED%97%A4%EB%93%9C`)
    }

    return (
        <>
        <CustomModal 
        title={'공지사항'}
        {...props}
        className={'notice_modal'}
        >
                <div className='notive_contents_wrapper header' onClick={onOpenYoutubeLink}>
                    구독.. 누르셨습니까? ← 클릭
                </div>
                {[...NoticeList].reverse().map((item,idx)=>{
                    return <div className='notive_contents_wrapper' onClick={()=>onClickNotice(item)}>
                    {NoticeList.length-idx}. {item.title}
                </div>
                })}
            
        </CustomModal>
        <NoticeDetail visible={detailModalOpen} ModalHandler={setDetailModalOpen} notice={detailNotice}/>
        </>
    );
})

export default NoticeModal;
