import React, { memo, useEffect } from 'react'
import { useSelector } from 'react-redux';
import { withRouter } from 'react-router';

const SaveComponent = memo((props) => {


    const currentTime = useSelector(state => state.currentTime)
    const play = useSelector(state => state.play);
    const progress = useSelector(state => state.progress); //?
    const character = useSelector(state => state.character);
    const newsList = useSelector(state => state.newsList);
    const youtubeVideo = useSelector(state => state.youtubeVideo);
    const editorList = useSelector(state => state.editorList);
    const uploadedList = useSelector(state => state.uploadedList);
    const contentsList = useSelector(state => state.contentsList)
    const asset = useSelector(state => state.asset);
    const coin = useSelector(state => state.coin);
    const bank = useSelector(state => state.bank);
    const stress = useSelector(state => state.stress);
    const equipment = useSelector(state => state.equipment);
    const computer = useSelector(state => state.computer)
    const activity = useSelector(state => state.activity);
    const sleep = useSelector(state => state.sleep)
    const tag = useSelector(state => state.tag);
    const setting = useSelector(state=>state.setting);
    const inventory = useSelector(state=>state.inventory);
    const advertisement = useSelector(state=>state.advertisement);
    const difficulty =  useSelector(state => state.difficulty);
    
    useEffect(() => {
        if (currentTime === 0) return props.history.push('/client/choice');
        window.localStorage.setItem('currentTime', JSON.stringify(currentTime))
        window.localStorage.setItem('character', JSON.stringify(character))
        window.localStorage.setItem('newsList', JSON.stringify(newsList))
        window.localStorage.setItem('youtubeVideo', JSON.stringify(youtubeVideo))
        window.localStorage.setItem('editorList', JSON.stringify(editorList))
        window.localStorage.setItem('uploadedList', JSON.stringify(uploadedList))
        window.localStorage.setItem('contentsList', JSON.stringify(contentsList))
        window.localStorage.setItem('asset', JSON.stringify(asset))
        window.localStorage.setItem('coin', JSON.stringify(coin))
        window.localStorage.setItem('bank', JSON.stringify(bank))
        window.localStorage.setItem('stress', JSON.stringify(stress))
        window.localStorage.setItem('equipment', JSON.stringify(equipment))
        window.localStorage.setItem('computer', JSON.stringify(computer))
        window.localStorage.setItem('activity', JSON.stringify(activity))
        window.localStorage.setItem('sleep', JSON.stringify(sleep))
        window.localStorage.setItem('tag', JSON.stringify(tag))
        window.localStorage.setItem('inventory',JSON.stringify(inventory));
        window.localStorage.setItem('advertisement',JSON.stringify(advertisement));
        window.localStorage.setItem('difficulty',JSON.stringify(difficulty));
    }, [currentTime])

    return (
        <>
        </>
    )
})

export default withRouter(SaveComponent)
