import React, { useRef, useState } from 'react';
import faintChar from '../MonitoringImage/faintChar.png';
import faintSound from '../../../../audio/faint_sound.wav';
import { useSelector } from 'react-redux';
import { useDispatch } from 'react-redux';
import { useEffect } from 'react';
import { faintTips } from '../../../../util/tips';
import { setPlay } from '../../../../_actions/play_action';
import { calcForceRecover, forceSleepTime } from '../../../../util/calcAlgorithm';
import moment from 'moment';
import { setCurrentTime } from '../../../../_actions/currentTime_action';
import { setActivity } from '../../../../_actions/activity_action';
import { recoverStress } from '../../../../_actions/stress_action';
import { setAwakeTime, setFaint } from '../../../../_actions/sleep_action';
import { initSpeed, setTimerInterval } from '../../../../_actions/interval_action';
import { setCharacterInfo } from '../../../../_actions/character_action';
import { Activities } from '../../../../Assets/ActionAsset';



function FaintInfoComponent() {
    const dispatch = useDispatch();
    const { awakeTime, faint } = useSelector(state => state.sleep);
    const currentTime = useSelector(state => state.currentTime);
    const {Effect:{on:EffectOn,volume:EffectVolume}} = useSelector(state=>state.setting);
    const { health: { value: health }} = useSelector(state => state.character);
    const stress = useSelector(state => state.stress);
    //쓰러졌을때 경고창
    const [downAlert, setDownAlert] = useState('');
    const [tip, setTip] = useState('');
    const [downSleepTime, setDownSleepTime] = useState(0);
    const faintSoundRef = useRef();
    const [isLoading, setIsLoading] = useState(true);

    const onReleaseAlert = () => {
        setDownAlert('appear disappear');
        setTimeout(() => {
            setDownAlert('disappear');
            dispatch(setActivity(Activities.resting, {}, currentTime));
            
            dispatch(setFaint(false));
            dispatch(setPlay(true));
        }, 1000);
    }

    useEffect(() => {
        if(faint){
            //사운드 실행
            faintSoundRef.current.volume = EffectVolume/100;
            if(EffectOn){
                faintSoundRef.current.play();
            }
            const sleepTime = forceSleepTime();
            //상태창 표시내용
            setDownSleepTime(sleepTime);
            
            setTip(faintTips[Math.floor(Math.random() * faintTips.length)])

            const nextAwakeTime = moment(currentTime).add(sleepTime, 'hour').valueOf()
            dispatch(setActivity(Activities.sleep, {}, nextAwakeTime)); //수면 활동으로 변경
            dispatch(setCharacterInfo({health:{value : health - 1 < 1 ? 1 : health - 1 , text : '건강' }}))

            setDownAlert('appear');
            dispatch(setPlay(true));
            dispatch(setTimerInterval(20));
            setIsLoading(true);
            dispatch(setAwakeTime(moment(currentTime).add(sleepTime, 'hour').valueOf()))//기상 시간 기록
            dispatch(recoverStress(calcForceRecover(stress,sleepTime, health))); // 피로도 회복
        }
    }, [faint]);

    useEffect(() => {
        if (currentTime === awakeTime) {
            setIsLoading(false);

            dispatch(initSpeed());
            dispatch(setPlay(false));
        }
    }, [currentTime])

    return (
        <>
            <div className={`faint_alert ${downAlert} `}>
                <div className={'sentence'}>
                    <div>
                        <img src={faintChar} /><img src={faintChar} /><img src={faintChar} /><img src={faintChar} /><img src={faintChar} />
                    </div>
                    작업피로도로 인하여 강제 수면에 들어가게 되었습니다.
                    <br />
                    수면 시간 : {downSleepTime}시간
                    <br />
                    <span className={'tip'}>
                        TIP : {tip}
                    </span>
                </div>
                {isLoading ? 
                <div className={'check_btn'}>회복 중...</div>
                :
                <div className={'check_btn'} onClick={onReleaseAlert}>
                    확인 {'>>>'}
                </div>
                }
                
            </div>
            <audio ref={faintSoundRef} src={faintSound} style={{ display: 'none' }} />
        </>
    );
}

export default FaintInfoComponent;
