import React from 'react';
import { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import CustomButton from '../../../UI/Button/CustomButton';
import { confirmMessage, infoMessage } from '../../../UI/Message/message';
import CustomModal from '../../../UI/Modal/CustomModal';
import { AddComma } from '../../../util/commonMethod';
import { payMoney } from '../../../_actions/character_action';
import { buyItem } from '../../../_actions/inventory_action';
import ModalUnfoldPanel from '../options/ModalUnfoldPanel';
import ExpandableBuyModal from './ExpandableBuyModal';
import { expandable_product } from '../../../Assets/shopAsset';

function ExpandableShopComponent(props) {

    const [searchTerm, setSearchTerm] = useState('');
    
    const [productList, setProductList] = useState(expandable_product);
    const [buyModalOpen, setBuyModalOpen] = useState(false);
    const [selectedItem, setSelectedItem] = useState()
    const onChangeSearchTerm = (e) => {
        const value = (e.currentTarget.value).toUpperCase();
        setSearchTerm(value);
        if (value) {
            const _newList = expandable_product.filter(p => p.name.indexOf(value) > -1);
            setProductList(_newList);
        } else {
            setProductList(expandable_product);
        }
    }
    const onBuyModalOpen=(item)=>{
        setBuyModalOpen(true);
        setSelectedItem(item);
    }



    return (
        <div className="expandable_modal">
            <div className='search_bar'>
                이름 검색 <input value={searchTerm} onChange={onChangeSearchTerm} />
            </div>
            {productList.map((item, idx) => {
                return (
                    <ModalUnfoldPanel key={item.key} unFoldStyle={{ height: '35%' }} toggle text={item.name} value={<CustomButton onClick={() => onBuyModalOpen(item)}>구매</CustomButton>}>
                        <span style={{ fontSize: '1.2rem' }}>가격 : {AddComma(item.cost)}</span>
                        <br /><br />
                        {item.desc}
                    </ModalUnfoldPanel>
                )
            })}
            <ExpandableBuyModal visible={buyModalOpen} ModalHandler={setBuyModalOpen} item={selectedItem}/>
        </div>
    );
}

export default ExpandableShopComponent;
