import { CHANGE_COIN_PRICE, INIT_COIN, SET_COIN, SET_COIN_LIST } from './type';


export function setCoin(coin) {
    return {
        type: SET_COIN,
        payload: coin
    }
}

export function setCoinList(list) {
    return {
        type: SET_COIN_LIST,
        payload: list
    }

}

export function changeCoinPrice() {
    return {
        type: CHANGE_COIN_PRICE
    }
}

export function initCoin(){
    return{
        type : INIT_COIN
    }
}