import React, { memo, useEffect, useState } from 'react'
import NewsModal from '../../Menu/News/NewsModal';
import { useDispatch } from 'react-redux';
import ActionChoiceModal from '../../Menu/ActionChoice/ActionChoiceModal';
import EditorModal from '../../Menu/Editor/EditorModal';
import UploadedListModal from '../../Menu/Uploaded/UploadedListModal';
import ContentChoiceModal from '../../Menu/ContentsChoice/ContentChoiceModal';
import AssetManagerModal from '../../Menu/AssetManager/AssetManagerModal';

import { useSelector } from 'react-redux';
import { withRouter } from 'react-router';
import VideoManagerComponent from '../../Menu/Video/VideoManagerComponent';
import ControlButton from './ControlButton';
import SettingModal from '../../Menu/Setting/SettingModal';
import NoticeModal from '../../Menu/Notice/NoticeModal';
import InventoryModal from '../../Menu/Inventory/InventoryModal';

import ShopModal from '../../Menu/Shop/ShopModal';
import { infoMessage } from '../../../UI/Message/message';
import AdvertisementModal from '../../Menu/Advertisement/AdvertisementModal';

const ControlComponent = memo((props) => {

    const youtubeVideo = useSelector(state => state.youtubeVideo)

    const [newsModal, setNewsModal] = useState(false); //뉴스 모달
    const [actionModal, setActionModal] = useState(false);// 활동 선택 모달
    const [videoManagerModal, setVideoManagerModal] = useState(false);// 편집관리 모달
    const [editorModal, setEditorModal] = useState(false);// 편집자 모달
    const [uploadedModal, setUploadedModal] = useState(false); // 업로드 영상 목록 모달
    const [contentsModalOpen, setContentsModalOpen] = useState(false); //방송컨텐츠 선택 모달
    const [assetManagerModalOpen, setAssetManagerModalOpen] = useState(false);// 자산관리 모달
    const [shopModalOpen, setShopModalOpen] = useState(false); // 상점 모달
    const [expandableShopOpen, setExpandableShopOpen] = useState(false); //소모품 상점 모달
    const [inventoryModalOpen, setinventoryModalOpen] = useState(false);//인벤토리 모당
    const [furnitureModalOpen, setFurnitureModalOpen] = useState(false); //가구상점 모달
    const [settingModalOpen,setSettingModalOpen]  = useState(false); // 환경설정 모달
    const [ProgressComplete, setProgressComplete] = useState(0); // 유튜브 완성된 갯수
    const [noticeModalOpen, setNoticeModalOpen] = useState(false);// 공지사항
    const [advertisementModalOpen, setAdvertisementModalOpen] = useState(false)

    const dispatch = useDispatch();

    useEffect(() => {
        callNewsInfo();
    }, [])

    useEffect(() => {
        if (youtubeVideo.length > 0)
            setProgressComplete(youtubeVideo.filter(ytb => ytb.progress === 100).length);
        else
            setProgressComplete(0);
    }, [youtubeVideo])

    //뉴스 정보 셋
    const callNewsInfo = () => {

    }
    //뉴스 모달 버튼
    const onNewsModalOpen = (e) => {
        setNewsModal(true);
    }

    //활동 선택 버튼
    const onActionChoiceModalOpen = (e) => {
        setActionModal(true)
    }
    //편집관리 버튼
    const onVideoManagerModalOpen = (e) => {
        setVideoManagerModal(true)
    }
    // 편집자 버튼
    const onEditorModalOpen = (e) => {
        setEditorModal(true);
    }
    // 업로드 목록 버튼
    const onUploadedModalOpen = (e) => {
        setUploadedModal(true);
    }
    //방송 컨텐츠 목록 버튼
    const onContentsChoiceModalOpen = (e) => {
        setContentsModalOpen(true);
    }
    //자산관리 모달
    const onAssetManagerModalOpen = (e) => {
        setAssetManagerModalOpen(true);
    }
    //상점 모달
    const onShopModalOpen = () => {
        setShopModalOpen(true);
    }
    const onEquipmentModalOpen = () => {
        setinventoryModalOpen(true)
    }
    //공지사항 모달
    const onNoticeModalOpend=()=>{
        setNoticeModalOpen(true);
    }

    //광고의뢰 모달
    const onAdvertisementModalOpen=()=>{
        setAdvertisementModalOpen(true);
    }

    //초기화
    const onResetGame = () => {
        setSettingModalOpen(true)
    }

    return (
        <div className="control_box">
            {/* 뉴스 */}
            <ControlButton id="control_1" onClick={onNewsModalOpen} text={'뉴-스'} icon={<i className="bi bi-newspaper"/>}/>
            {/* 활동선택 */}
            <ControlButton id="control_2" onClick={onActionChoiceModalOpen} text={'활동'} icon={
                <svg xmlns="http://www.w3.org/2000/svg" fill="currentColor" className="bi bi-robot" viewBox="0 0 16 16">
                    <path d="M6 12.5a.5.5 0 0 1 .5-.5h3a.5.5 0 0 1 0 1h-3a.5.5 0 0 1-.5-.5ZM3 8.062C3 6.76 4.235 5.765 5.53 5.886a26.58 26.58 0 0 0 4.94 0C11.765 5.765 13 6.76 13 8.062v1.157a.933.933 0 0 1-.765.935c-.845.147-2.34.346-4.235.346-1.895 0-3.39-.2-4.235-.346A.933.933 0 0 1 3 9.219V8.062Zm4.542-.827a.25.25 0 0 0-.217.068l-.92.9a24.767 24.767 0 0 1-1.871-.183.25.25 0 0 0-.068.495c.55.076 1.232.149 2.02.193a.25.25 0 0 0 .189-.071l.754-.736.847 1.71a.25.25 0 0 0 .404.062l.932-.97a25.286 25.286 0 0 0 1.922-.188.25.25 0 0 0-.068-.495c-.538.074-1.207.145-1.98.189a.25.25 0 0 0-.166.076l-.754.785-.842-1.7a.25.25 0 0 0-.182-.135Z" />
                    <path d="M8.5 1.866a1 1 0 1 0-1 0V3h-2A4.5 4.5 0 0 0 1 7.5V8a1 1 0 0 0-1 1v2a1 1 0 0 0 1 1v1a2 2 0 0 0 2 2h10a2 2 0 0 0 2-2v-1a1 1 0 0 0 1-1V9a1 1 0 0 0-1-1v-.5A4.5 4.5 0 0 0 10.5 3h-2V1.866ZM14 7.5V13a1 1 0 0 1-1 1H3a1 1 0 0 1-1-1V7.5A3.5 3.5 0 0 1 5.5 4h5A3.5 3.5 0 0 1 14 7.5Z" />
                </svg>
            } />
            <ControlButton id="control_3" onClick={onContentsChoiceModalOpen} icon={<i className="bi bi-controller" />} text={'방송 컨텐츠'} />
            {/* 방송 장비 확인 */}
            <ControlButton id="control_4" onClick={onEquipmentModalOpen} icon={
                // <svg xmlns="http://www.w3.org/2000/svg" fill="currentColor" className="bi bi-pc-display" viewBox="0 0 16 16">
                //     <path d="M8 1a1 1 0 0 1 1-1h6a1 1 0 0 1 1 1v14a1 1 0 0 1-1 1H9a1 1 0 0 1-1-1V1Zm1 13.5a.5.5 0 1 0 1 0 .5.5 0 0 0-1 0Zm2 0a.5.5 0 1 0 1 0 .5.5 0 0 0-1 0ZM9.5 1a.5.5 0 0 0 0 1h5a.5.5 0 0 0 0-1h-5ZM9 3.5a.5.5 0 0 0 .5.5h5a.5.5 0 0 0 0-1h-5a.5.5 0 0 0-.5.5ZM1.5 2A1.5 1.5 0 0 0 0 3.5v7A1.5 1.5 0 0 0 1.5 12H6v2h-.5a.5.5 0 0 0 0 1H7v-4H1.5a.5.5 0 0 1-.5-.5v-7a.5.5 0 0 1 .5-.5H7V2H1.5Z" />
                // </svg>
                <i className="bi bi-briefcase"></i>
            } text={'인벤토리'} />
            {/* 유튭각 목록 확인 및 편집 진행도 확인 */}
            <ControlButton id="control_5" onClick={onVideoManagerModalOpen} text={'편집 관리'} icon={<i className="bi bi-camera-video"/>} badge={ProgressComplete}/>
            {/* 편집자 고용 및 해고 */}
            <ControlButton id="control_6" onClick={onEditorModalOpen} text={'편집자'} icon={<i className="bi bi-person-plus"/>}/>
            {/* 유튭각 조회수 확인 및 수금 */}
            <ControlButton id="control_7" onClick={onUploadedModalOpen} text={'업로드 조회'} icon={<i className="bi bi-youtube"/>} />
            {/* 광고의뢰 */}
            <ControlButton id="control_8" onClick={onAdvertisementModalOpen} text={'광고의뢰'} icon={<i className="bi bi-badge-ad"></i>}/>
            {/* 자산관리 : 주식 , 코인, 부동산 */}
            <ControlButton id="control_9" onClick={onAssetManagerModalOpen} text={'자산관리'} icon={<i className="bi bi-piggy-bank"/>}/>
            {/* 상점 */}
            <ControlButton id="control_10" onClick={onShopModalOpen} text={'상점'} icon={<i className="bi bi-basket"></i>}/>
            {/* 공지사항 */}
            <ControlButton id="control_11" onClick={onNoticeModalOpend} text={'공지사항'} icon={<i className="bi bi-clipboard-check"/>}/>
            {/* 초기화 */}
            <ControlButton id="control_12" onClick={onResetGame} text={'옵션'} icon={ <i className="bi bi-gear"></i> }/>

            <NewsModal visible={newsModal} ModalHandler={setNewsModal} />
            <ActionChoiceModal visible={actionModal} ModalHandler={setActionModal} />
            <VideoManagerComponent visible={videoManagerModal} ModalHandler={setVideoManagerModal} />
            <EditorModal visible={editorModal} ModalHandler={setEditorModal} />
            <UploadedListModal visible={uploadedModal} ModalHandler={setUploadedModal} />
            <ContentChoiceModal visible={contentsModalOpen} ModalHandler={setContentsModalOpen} />
            <AssetManagerModal visible={assetManagerModalOpen} ModalHandler={setAssetManagerModalOpen} />
            <InventoryModal visible={inventoryModalOpen} ModalHandler={setinventoryModalOpen}/>
            <SettingModal visible={settingModalOpen} ModalHandler={setSettingModalOpen}/>
            <NoticeModal visible={noticeModalOpen} ModalHandler={setNoticeModalOpen}/>
            <ShopModal visible={shopModalOpen} ModalHandler={setShopModalOpen} />
            <AdvertisementModal visible={advertisementModalOpen} ModalHandler={setAdvertisementModalOpen}/>
        </div>
    )
})

export default withRouter(ControlComponent);
