import React, { memo, useEffect, useState } from 'react'
import CustomModal from '../../../../UI/Modal/CustomModal';
import { momentToDateString } from '../../../../util/commonMethod';

const NewsDetailModal = memo((props) => {
    const { news } = props;
    const [effectKeys, setEffectKeys] = useState([])

    useEffect(() => {
        if (news) {
            setEffectKeys(Object.keys(news.effect))
        }
    }, [news])

    return (
        <CustomModal
            {...props}
            title={'뉴스 상세 정보'}
            className={'new_detail_wrapper'}
        >
            {news &&
                <div className="news_detail_container">
                    <div className="header">
                        <div className='thumbnail' style={{ background: `center / contain no-repeat url(${news.image})` }} />
                        <div className="title">
                            {news.title}
                        </div>
                    </div>
                    <div className="news_time">
                        기사 업로드 날짜 : {momentToDateString(news.dateTime)}
                    </div>
                    <div className="contents">
                        {news.desc}
                    </div>
                    <hr />
                    <div className="effect" >
                        {effectKeys.map((item, idx) => {
                            const value = news.effect[item];
                            let result = ' 합니다.';
                            return (
                                <div className="body" key={idx}>
                                    <span className="genre">{item.toUpperCase()}</span>
                                    의 인기도가
                                    <span className={`change ${value > 0 ? 'plus' : 'minus'}`}>{Math.abs(value)}% {value > 0 ? '증가' : '감소'}</span>
                                    {result}
                                </div>
                            )
                        })}
                    </div>
                    <hr />
                    <div className="duration">
                        해당 효과는 업로드 후
                        <span>{news.duration}</span>
                        일 동안 지속 됩니다.
                    </div>
                </div>
            }
        </CustomModal>
    )
})

export default NewsDetailModal
