import { SET_DIFFICULTY } from "../_actions/type";

const initial_state = 1;

/**
 * 난이도
 * 1 : 쉬움
 * 2 : 보통
 * 3 : 어려움
 * **/

export default function(state=initial_state,action){
    switch (action.type) {
        case SET_DIFFICULTY:
            if(action.payload){
                return action.payload
            }else{
                return state
            }
        default: return state;
    }
}