import moment from 'moment';
import React, { memo } from 'react'
import { useSelector } from 'react-redux'
import KrwIcon from '../../../../../scss/icon/KrwIcon';
import CustomButton from '../../../../../UI/Button/CustomButton';
import { AddComma, momentToDateString } from '../../../../../util/commonMethod'
import AssetCoin from './Section/AssetCoin';
import AssetDeposit from './Section/AssetDeposit';
import AssetLoan from './Section/AssetLoan';

const MyAsset = memo((props) => {

    const character = useSelector(state => state.character);
    const difficulty = useSelector(state => state.difficulty);

    return (
        <div className={'my_asset'}>
            <div className={'title'}> 소지금 </div>
            <div className={'value'}> <KrwIcon className={'krw_icon'} /> {AddComma(character.money.value)} </div>
            <AssetDeposit />
            <AssetLoan />
            {difficulty === 1 && <AssetCoin />}
        </div>
    )
})

export default MyAsset
