import React from 'react';
import { useState } from 'react';
import { useSelector } from 'react-redux';
import CustomButton from '../../../UI/Button/CustomButton';
import CustomCheckBox from '../../../UI/Input/CheckBox/CustomCheckBox';
import CustomModal from '../../../UI/Modal/CustomModal';
import NoticeDetail from '../../Menu/Notice/NoticeDetail';
import NoticeList from '../../Menu/Notice/NoticeList';

function NoticePopup() {

    const LastestNotice = NoticeList[NoticeList.length - 1];
    const popup = JSON.parse(window.localStorage.getItem('popup'));
    const [visible, setVisible] = useState(popup?.key === LastestNotice.key ? false : true);
    const [neverReview, setNeverReview] = useState(false);
    
    const onCloseModal=()=>{
        if(neverReview){
            window.localStorage.setItem('popup',JSON.stringify(LastestNotice))
        }else{
            window.localStorage.removeItem('popup');
        }
        setVisible(false);
    }

    return (
        <NoticeDetail
            style={{height:'60vh'}}
            notice = {LastestNotice}
            visible={visible}
            ModalHandler={onCloseModal}
            toggle={false}
            title={'신규 공지시항 & 안내'}
            footer={
                <>
                    <CustomCheckBox onChange={setNeverReview} value={neverReview}>다시 보지 않기</CustomCheckBox>
                    <CustomButton onClick={onCloseModal}>닫기</CustomButton>
                </>
            }
        />
    );
}

export default NoticePopup;
