import React from 'react'
import { useState } from 'react'
import { useDispatch } from 'react-redux'
import CustomButton from '../../../../UI/Button/CustomButton'
import CustomCheckBox from '../../../../UI/Input/CheckBox/CustomCheckBox'
import { confirmMessage, infoMessage } from '../../../../UI/Message/message'
import CustomModal from '../../../../UI/Modal/CustomModal'
import { deleteRatingVideos } from '../../../../_actions/video_action'

function VideoDeleteModal(props) {

    const [selectedStar, setSelectedStar] = useState([]);
    
    const dispatch = useDispatch();

    const onChangeCheckBox=(value,flag)=>{
        const newList=[...selectedStar]
        if(flag){
            newList.push(value)
        }else{
            newList.splice(newList.findIndex(v=>v===value),1) ;
        }
        setSelectedStar(newList)
    }
    const onDeleteVideo=()=>{
        if(selectedStar.length===0) return infoMessage('삭제할 대상을 선택해주세요.');
        confirmMessage(`선택한 등급의 유튜브각이 삭제됩니다.`,()=>{
            dispatch(deleteRatingVideos(selectedStar));
            props.ModalHandler(false);
        })
    }

    return (
        <CustomModal
        {...props}
        title={'영상 일괄 삭제'}
        className={'video_delete_modal'}
        >
            <div className='star_list'>
                <CustomCheckBox value={selectedStar.indexOf(1) > -1?true:false} onChange={(flag)=>onChangeCheckBox(1,flag)}><span className="star">★</span> 1</CustomCheckBox>
                <CustomCheckBox value={selectedStar.indexOf(2) > -1?true:false} onChange={(flag)=>onChangeCheckBox(2,flag)}><span className="star">★</span> 2</CustomCheckBox>
                <CustomCheckBox value={selectedStar.indexOf(3) > -1?true:false} onChange={(flag)=>onChangeCheckBox(3,flag)}><span className="star">★</span> 3</CustomCheckBox>
            </div>
            <div className='delete_btn'>
                <CustomButton type={'danger'} onClick={onDeleteVideo}>삭제</CustomButton>
            </div>
            
        </CustomModal>
    )
}

export default VideoDeleteModal