import React, { useEffect } from 'react'
import chairImg from './MonitoringImage/broadcast/chair.png';
import desk from './MonitoringImage/broadcast/desk.png';
import cameraImg from './MonitoringImage/broadcast/camera.png';

import character1 from './MonitoringImage/broadcast/char_frame_1.png';
import character2 from './MonitoringImage/broadcast/char_frame_2.png';
import character3 from './MonitoringImage/broadcast/char_frame_3.png';

import computer0 from './MonitoringImage/broadcast/computer_frame_0.png';
import computer1 from './MonitoringImage/broadcast/computer_frame_1.png';
import computer2 from './MonitoringImage/broadcast/computer_frame_2.png';
import computer3 from './MonitoringImage/broadcast/computer_frame_3.png';
import computer4 from './MonitoringImage/broadcast/computer_frame_4.png';
import computer5 from './MonitoringImage/broadcast/computer_frame_5.png';

import bedImg from './MonitoringImage/resting/bed_1.png';


import pullUp1 from './MonitoringImage/exercise/pullup/pullup1.png';
import pullUp2 from './MonitoringImage/exercise/pullup/pullup2.png';
import pullUp3 from './MonitoringImage/exercise/pullup/pullup3.png';
import pullUp4 from './MonitoringImage/exercise/pullup/pullup4.png';
import pullUp5 from './MonitoringImage/exercise/pullup/pullup5.png';
import pullUp6 from './MonitoringImage/exercise/pullup/pullup6.png';
import pullUp7 from './MonitoringImage/exercise/pullup/pullup7.png';
import pullUp8 from './MonitoringImage/exercise/pullup/pullup8.png';
import pullUp9 from './MonitoringImage/exercise/pullup/pullup9.png';
import pullUp10 from './MonitoringImage/exercise/pullup/pullup10.png';
import pullUp11 from './MonitoringImage/exercise/pullup/pullup11.png';
import pullUp12 from './MonitoringImage/exercise/pullup/pullup12.png';
import pullUp13 from './MonitoringImage/exercise/pullup/pullup13.png';
import pullUp14 from './MonitoringImage/exercise/pullup/pullup14.png';
import pullUp15 from './MonitoringImage/exercise/pullup/pullup15.png';
import pullUp16 from './MonitoringImage/exercise/pullup/pullup16.png';


import { useRef } from 'react';
import { useState } from 'react';
import { useSelector } from 'react-redux';
import { useInterval } from './useInterval';

const normalCharcterImgs = [
    character1,character2,character3
]
const pullUpImgs=[
    pullUp1 ,pullUp2 ,pullUp3 ,pullUp4 ,pullUp5 ,pullUp6 ,pullUp7 ,pullUp8 ,pullUp9 ,pullUp10 ,pullUp11 ,pullUp12 ,pullUp13 ,pullUp14 ,pullUp15 ,pullUp16
]

function CharacterBroadCast(props) {
    const {computer=true, chair=true, camera=true,character=true,bed=true,contents} = props;
    const [displayCharacterImgs, setDisplayCharacterImgs] = useState([...normalCharcterImgs]);
    
    const computerImg = useRef([
        computer0,computer1,computer2,computer3,computer4,computer5
    ])

    const computerIndex = useRef(0);
    const charIndex = useRef(0);
    
    const [charImg, setCharImg] = useState(character1)
    const [comImg, setComImg] = useState(computer0)


    const play = useSelector(state => state.play)
    const { interval } = useSelector(state => state.interval)


    useEffect(() => {
        if(contents?.category==='exercise'){
            //운동 캐릭터 설정 지점
            setDisplayCharacterImgs([...pullUpImgs]);
            setCharImg(pullUpImgs[0]);
        }else{
            setDisplayCharacterImgs([...normalCharcterImgs]);
            setCharImg(normalCharcterImgs[0]);
        }
    }, [contents])
    

    useInterval(() => {
        setCharImg(displayCharacterImgs[charIndex.current]);
        setComImg(computerImg.current[computerIndex.current]);

        charIndex.current++;
        if (charIndex.current >= displayCharacterImgs.length) {
            charIndex.current = 0;
        }

        computerIndex.current++;
        if (computerIndex.current >= computerImg.current.length) {
            computerIndex.current = 0;
        }
    }, interval, play)
    
    const renderingCharacter=()=>{
        if(contents?.category === "exercise"){
            let exClassName=''
            switch (contents.key) {
                case '데드_리프트': exClassName='pullUp'; break;
                case '벤치_프레스': exClassName='pullUp'; break;
                case '스쿼트': exClassName='pullUp'; break;
                case '버피_테스트': exClassName='pullUp'; break;
                case '팔_굽혀펴기': exClassName='pullUp'; break;
                case '윗몸_일으키키': exClassName='pullUp'; break;
                default: exClassName='pullUp';  break;
            } ;
            return(
                <>  
                    <img src={charImg} className={exClassName} alt="" />
                    <img src={cameraImg} className='camera' alt="" />
                </>
            )
        } else {
            
            return (
                <>
                    {chair ? <img src={chairImg} className='chair' alt="" /> : null}
                    {computer ? <img src={comImg} className='computer' alt="" /> : null}
                    {character ? <img src={charImg} className='character' alt="" /> : <img src={desk} className='desk' alt="" />}
                    {camera ? <img src={cameraImg} className='camera' alt="" /> : null}
                </>
            )
        }
    }

    return (
        <>
        <div className='resting'>
            {bed?<img src={bedImg}/>:null}
        </div>
        <div className='character_container' id='character_container'>
            {renderingCharacter()}
        </div>
        </>
        
    )
}

export default CharacterBroadCast
