import React, { useEffect, useState,useRef } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { TimeComparison } from '../../../util/commonMethod';
import CharacterInfoModal from '../../Menu/Character_Info/CharacterInfoModal';
import TagModal from '../../Menu/Tag/TagModal';
import HOUSE_1 from './MonitoringImage/HOUSE_1.png';
import HOUSE_2 from './MonitoringImage/HOUSE_2.png';

import resting from './MonitoringImage/resting.png'
import ExerciseComponent from './CharacterExercise';
import moment from 'moment';
import CharacterBroadCast from './CharacterBroadCast';

import mainThemeMusic from '../../../audio/theme_music.wav';
import CharacterResting from './CharacterResting';
import { setTutorial } from '../../../_actions/tutorial_action';
import { Activities } from '../../../Assets/ActionAsset';
import BackgroundComponent from './BackgroundComponent';

function CharacterMonitoring() {
    const [CharacterModal, setCharacterModal] = useState(false); //캐릭터 정보 모달
    const [tagModalOpen, setTagModalOpen] = useState(false); // 태그 모달
    const { activity, startTime, currentViewer, gameStartTime, contents } = useSelector(state => state.activity);
    const inventory = useSelector(state=>state.inventory);
    const currentTime = useSelector(state => state.currentTime);
    const {Music:{on:MusicOn,volume:MusicVolume}} = useSelector(state => state.setting);
    const dispatch = useDispatch();
    const mainAudioRef = useRef();

    const [itemStatus, setItemStatus] = useState(false);
    
    useEffect(()=>{
        if (mainAudioRef.current) {
            mainAudioRef.current.volume = MusicVolume/100;
            if (MusicOn) {
                mainAudioRef.current.play();
            } else {
                mainAudioRef.current.pause();
            }
        } else {
            mainAudioRef.current.onload = () => {
                mainAudioRef.current.volume = MusicVolume/100;
                if (MusicOn) {
                    mainAudioRef.current.play();
                } else {
                    mainAudioRef.current.pause();
                }
            }
        }
        
    },[MusicOn,MusicVolume])

    //스트리머 정보 모달 버튼
    const onCharacterModalOpen = (e) => {
        setCharacterModal(true);
    }
    const onTagModalOpen = (e) => {
        setTagModalOpen(true);
    }
    const onGuideStart=()=>{
        dispatch(setTutorial(true));
    }
    const useItemFold=()=>{
        setItemStatus(!itemStatus);
    }

    return (
        <div className="char_monitor" id={'char_monitor'}>
            <div className='bg'>
                <BackgroundComponent className='char_bg' id={'char_bg'} />
                {/* <img className='char_bg' ref={charBgRef} src={bg} alt='캐릭터 배경' /> */}
                {activity === Activities.broadcast &&
                    <>
                        <div className={'upTime'}>
                            {TimeComparison(currentTime, startTime)}
                        </div>
                        <div className={'viewer'}>
                            <svg xmlns="http://www.w3.org/2000/svg" fill="currentColor" className="bi bi-people-fill" viewBox="0 0 16 16">
                                <path d="M7 14s-1 0-1-1 1-4 5-4 5 3 5 4-1 1-1 1H7zm4-6a3 3 0 1 0 0-6 3 3 0 0 0 0 6z" />
                                <path fillRule="evenodd" d="M5.216 14A2.238 2.238 0 0 1 5 13c0-1.355.68-2.75 1.936-3.72A6.325 6.325 0 0 0 5 9c-4 0-5 3-5 4s1 1 1 1h4.216z" />
                                <path d="M4.5 8a2.5 2.5 0 1 0 0-5 2.5 2.5 0 0 0 0 5z" />
                            </svg>
                            <span className={'count'}>
                                {Math.floor(currentViewer)}
                            </span>
                        </div>
                        <CharacterBroadCast contents={contents}/>
                    </>
                }
                {activity === Activities.resting &&
                    <>
                        <CharacterResting />
                        <CharacterBroadCast character={false} bed={false} />
                    </>
                }
                {activity === Activities.exercise &&
                    <>
                        <CharacterBroadCast character={false} />
                        <ExerciseComponent />
                    </>
                }
                {
                    (activity === Activities.self_develop || activity === Activities.edit) &&
                    <>
                        <CharacterBroadCast camera={false} />
                    </>
                }
            </div>
            <div className={`onAir ${activity === Activities.broadcast ? 'activate' : ''}`}>
                ON AIR
            </div>

            <div className={'elapsed_date'}>
                D+{moment(currentTime).diff(gameStartTime, 'day')}
            </div>
            <div className={'guide_info'} onClick={onGuideStart}>
                <div className="control_icon"><i className="bi bi-question-circle"></i></div>
            </div>
            <div className={'tag_info'} onClick={onTagModalOpen}>
                <div className="control_icon"><i className="bi bi-bookmark-star"></i></div>
            </div>
            <div className={'char_info'} onClick={onCharacterModalOpen}>
                <div className="control_icon"><i className="bi bi-person-bounding-box "></i></div>
            </div>
            {
                itemStatus ?
                    <div className={`use_item_info fold`} onClick={useItemFold}>
                        <div className="control_icon"><i className="bi bi-bag-check"></i></div>
                    </div>
                    :
                    <div className={`use_item_info unfold`} onClick={useItemFold}>
                        <span className='title'>사용중인 아이템</span>
                        {Object.keys(inventory).map(key => {
                            const { use, sideEffect, debuffExpireTime, buffExpireTime, name } = inventory[key];
                            if (use) {
                                return (
                                    <div key={key} className={'use'}>
                                        <span className="name">{name}</span> : <span className="time">{TimeComparison(buffExpireTime, currentTime)}</span>
                                    </div>
                                )
                            } else if (sideEffect) {
                                return (
                                    <div key={key} className={'sideEffect'}>
                                        <span className='name'>{name}</span> : <span className='time'>{TimeComparison(debuffExpireTime, currentTime)}</span>
                                    </div>
                                )
                            } else {
                                return <></>
                            }

                        })}
                    </div>
            }
            <CharacterInfoModal visible={CharacterModal} ModalHandler={setCharacterModal} />
            <TagModal visible={tagModalOpen} ModalHandler={setTagModalOpen} />
            {/* 메인 테마곡 */}
            <audio ref={mainAudioRef} src={mainThemeMusic} style={{display:'none'}} loop/>
        </div>

    )
}

export default CharacterMonitoring
