import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import CustomButton from '../../../../UI/Button/CustomButton';
import { infoMessage } from '../../../../UI/Message/message';
import CustomModal from '../../../../UI/Modal/CustomModal';
import { setStatus } from '../../../../_actions/character_action';
import { setUseInventoryItem } from '../../../../_actions/inventory_action';
import { useInterval } from '../../../Main/Section/useInterval';

function RandomVoiceModal(props) {
    
    const { voice: { value: status } } = useSelector(state => state.character)
    const inventory = useSelector(state => state.inventory);

    const dispatch = useDispatch();
    const [slotNumber, setSlotNumber] = useState(1);
    const [play, setPlay] = useState(false);
    const [showText, setShowText] = useState(false);

    useInterval(() => {
        const randomNumber = Math.round(Math.random() * (10 - 1) + 1);
        setSlotNumber(randomNumber);
    }, 50, play)

    const onChangeHealth = () => {
        if(inventory.ex_9?.count > 0){
            setPlay(true);
            setShowText(false);
        }else{
            infoMessage('남은 이용권이 없습니다.');
        }
    }
    const onStopSlot = () => {
        setPlay(false);
        const randomNumber = Math.random() * 10;
        let applyNumber = 1;
        if (randomNumber < 1.5) {
            applyNumber = 1;
        } else if (randomNumber < 2.5) {
            applyNumber = 2;
        } else if (randomNumber < 4.5) {
            applyNumber = 3;
        } else if (randomNumber < 7.5) {
            applyNumber = 4;
        } else if (randomNumber < 8.5) {
            applyNumber = 5;
        } else if (randomNumber < 9) {
            applyNumber = 6;
        } else if (randomNumber < 9.2) {
            applyNumber = 7;
        } else if (randomNumber < 9.5) {
            applyNumber = 8;
        } else if (randomNumber < 9.8) {
            applyNumber = 9;
        } else if (randomNumber < 10) {
            applyNumber = 10;
        }
        setSlotNumber(applyNumber);
        dispatch(setStatus('voice', applyNumber));
        dispatch(setUseInventoryItem('ex_9'));
        setShowText(true);
    }

    useEffect(() => {
        if(props.visible){
            setShowText(false);
        }
      }, [props.visible]);
  return (
    <CustomModal
    {...props}
    title={'너의 목소리는?.....'}
    className={'inventory_item_modal'}
    >
        <div className="dashboard">
            <div className="my_status">
                나의 목소리 : {status}점
            </div>

            <div className='slot_number'>
                {slotNumber}
            </div>

            <div className='my_coupon'>
                변조 이용권 : {inventory.ex_9?.count||0}개
            </div>
            {showText&&
            <div className='info red blink'>여↗기→ 봉↗골↗레↗ 파스타↘ 하나→</div>}
        </div>
        <div className="slot_button">
            {play ?
                <CustomButton onClick={onStopSlot} type={'danger'}>
                    멈춰
                </CustomButton> :
                <CustomButton onClick={onChangeHealth}>
                    돌려돌려!
                </CustomButton>
            }
        </div>
    </CustomModal>
  );
}

export default RandomVoiceModal;
