import moment from 'moment';
import { SET_NEW_EDITOR_LIST, SET_NOW_TIME } from '../_actions/type';

const initialState = {
    recall: moment().set('h', 24).set('minute', 0).set('s', 0).valueOf(),
    now: moment().valueOf(),
}

export default function (state = initialState, action) {
    switch (action.type) {
        // case SET_NEW_EDITOR_LIST:
        //     return { ...state, list: action.payload };
        case SET_NOW_TIME:
            return { ...state, now: action.payload }
        default: return state;
    }
}