import { ADD_EDITOR, SET_EDITOR_WORK, SET_EDITOR_LIST, REMOVE_EDITOR } from './type'

export function addEditor(editor) {

    return {
        type: ADD_EDITOR,
        payload: editor
    }

}

export function setEditorWork(editor, work) {
    return {
        type: SET_EDITOR_WORK,
        payload: { editor, work }
    }

}

export function setEditorList(list) {
    return {
        type: SET_EDITOR_LIST,
        payload: list
    }
}

export function fireEditor(editor) {
    return {
        type: REMOVE_EDITOR,
        payload: editor
    }
}