import React, { memo } from 'react'
import { useEffect } from 'react';
import { useRef } from 'react';
import { useSelector } from 'react-redux';
import clickSound from '../../../audio/main_menu_open.wav'
const ControlButton = memo((props) => {

    const { icon, text, onClick = () => { } , badge=0, id=""} = props;
    const {Effect:{on:EffectOn,volume:EffectVolume}} = useSelector(state => state.setting);
    const clickAudio = useRef();
    const onClickButton = () => {
        clickAudio.current.volume = EffectVolume/100;
        if(EffectOn){
            clickAudio.current.play();    
        }
        if (onClick) {
            onClick();
        }
    }
    
    return (
        <div className="control_grid" id={id}>
            <div className="control_btn" onClick={onClickButton}>
                <div className={`badge ${badge>0?'show':''}`}>{badge}</div>
                <div className="control_icon">
                    {icon}
                </div>
                <div className="btn_text">{text}</div>
            </div>
            <audio ref={clickAudio} src={clickSound} style={{display:'none'}}/>
        </div>
    )
})

export default ControlButton
