import { BUY_RAM, BUY_GPU, BUY_COMPUTER, SET_COMPUTER } from './type';

export function setComputer(computer) {
    return {
        type: SET_COMPUTER,
        payload: computer
    }
}

export function buyRam(ram) {
    return {
        type: BUY_RAM,
        payload: ram
    }
}

export function buyGpu(gpu) {
    return {
        type: BUY_GPU,
        payload: gpu
    }
}

export function buyComputer(parts) {
    return {
        type: BUY_COMPUTER,
        payload: parts
    }
}