import React, { memo, useState } from 'react'
import { useSelector } from 'react-redux';
import KrwIcon from '../../../../../../scss/icon/KrwIcon';
import CustomButton from '../../../../../../UI/Button/CustomButton';
import { AddComma, momentToDateString } from '../../../../../../util/commonMethod';
import LoanPayModal from './LoanPayModal';

const AssetLoan = memo((props) => {

    const asset = useSelector(state => state.asset);
    const [loanPayModalOpen, setloanPayModalOpen] = useState(false);
    const [loanIndex, setLoanIndex] = useState(0);
    const onPayLoan = (index) => {
        setloanPayModalOpen(true);
        setLoanIndex(index);
    }

    return (
        <>
            <div className={'title'}>
                <span>은행 대출금</span>
                <span className={'money'}>
                    <KrwIcon className={'krw_icon'} />
                    {AddComma(asset.loan.reduce((acc, current) => { return acc + current.money }, 0))}
                </span>
            </div>
            <div className={'list'}>
                <div className={'list_contents header'}>
                    <span>상품명</span>
                    <span>잔금</span>
                    <span>금리</span>
                    <span>비고</span>
                    <span>가입일</span>
                </div>
                {asset.loan.map((item, index) => {
                    const { key, name, money, interest, start } = item;
                    return (
                        <div className={'list_contents'} key={key}>
                            <span>{name}</span>
                            <span>{AddComma(Math.round(money))}</span>
                            <span>{(interest * 100).toFixed(2)}%</span>
                            <span><CustomButton onClick={() => onPayLoan(index)}>지불</CustomButton></span>
                            <span>{momentToDateString(start)}</span>
                        </div>
                    )
                })}
            </div>
            <LoanPayModal visible={loanPayModalOpen} ModalHandler={setloanPayModalOpen} loanIndex={loanIndex} />
        </>
    )
})

export default AssetLoan
