import React, { memo, useEffect } from 'react'
import { useState } from 'react';
import "./scss/CheckBox.scss";
import defaultCheckedImg from './img/checked.png';
import defaultUnCheckedImg from './img/unchecked.png';
import defaultDisabledCheckedImg from './img/disabled.png';

const CustomCheckBox = memo((props) => {
    const {
        checkedImg = defaultCheckedImg
        , uncheckedImg = defaultUnCheckedImg
        , style
        , id = `checkbox${Math.floor(Math.random() * 10000)}`
        , onChange
        , value = false
        , name
        , disabled = false
    } = props;

    useEffect(() => {
        setChecked(value);
    }, [value])

    const [checked, setChecked] = useState(value);

    const onCheckChange = (flag) => {
        if (disabled) return false;
        setChecked(flag)
        if (onChange) {
            onChange(flag)
        }
    }
    const onClickEvent = (e) => {
        e.stopPropagation()
        onCheckChange(!checked)
    }

    return (
        <div className="CheckBox">
            <input type="checkbox" id={id} checked={checked} onChange={() => onCheckChange(!checked)} name={name} disabled />
            <img src={checked ? checkedImg : uncheckedImg} onClick={onClickEvent} alt="체크박스 이미지" />
            <label htmlFor={id} onClick={onClickEvent}>
                {props.children}
            </label>
        </div>
    )
})

export default CustomCheckBox
