import { GET_VIDEO_LIST_REVENUE, GET_VIDEO_REVENUE, ADD_UPLOAEDE_VIDEO, SET_UPLOADED_VIDEO_LIST,VIEW_COUNT_UP } from './type';

export function setUploadVideoList(list) {
    return {
        type: SET_UPLOADED_VIDEO_LIST,
        payload: list
    }
}

export function addUploadVideo(video) {

    video.revenue = 0;
    video.total_revenue = 0;
    video.view = 0;

    return {
        type: ADD_UPLOAEDE_VIDEO,
        payload: video
    }
}

export function getVideoRevenue(video) {
    return {
        type: GET_VIDEO_REVENUE,
        payload: video
    }
}

export function getVideoListRevenue() {
    return {
        type: GET_VIDEO_LIST_REVENUE,
    }
}

/**
 * 유튜브 조회수 
 * @param1 뉴스 리스트
 * @param2 현재시간
 * @param3 평균 시청자수
 * **/
export function viewerCountUp(newsList, currentTime, averageViewer) {
    return {
        type: VIEW_COUNT_UP,
        payload: { newsList, currentTime, averageViewer }
    }
}