import React, { memo, useEffect, useState } from 'react'
import { useSelector } from 'react-redux';
import { useDispatch } from 'react-redux';
import CustomButton from '../../../../../../UI/Button/CustomButton'
import { infoMessage } from '../../../../../../UI/Message/message';
import CustomModal from '../../../../../../UI/Modal/CustomModal'
import { depositMoney, withdrawMoney } from '../../../../../../_actions/asset_action';
import { getMoney, payMoney } from '../../../../../../_actions/character_action';

const DepositModal=memo((props) =>{
    const {selecteDeposit} = props;

    const {money:{value:myMoney}} = useSelector(state => state.character)
    const [name, setName] = useState(''); //이름
    const [amount, setAmount] = useState(0);//현재 예금액
    const [price, setPrice] = useState(0); // 입출금액

    const dispatch = useDispatch();

    useEffect(() => {
        if(selecteDeposit){
            setName(selecteDeposit.name);
            setAmount(selecteDeposit.money);
        }
    }, [selecteDeposit])

    const onDeposit = ()=>{
        if(price > myMoney) return infoMessage('입금액이 보유금 보다 큽니다.')
        const body={
            ...selecteDeposit,
            deposit : price
        }
        dispatch(depositMoney(body));
        dispatch(payMoney(price));
        setPrice(0)
        props.ModalHandler(false);
    }
    const onWithdraw=()=>{
        if(price > amount) return infoMessage('출금액이 저금액 보다 큽니다.')
        const body={
            ...selecteDeposit,
            withdraw : price
        }
        dispatch(withdrawMoney(body));
        dispatch(getMoney(price));
        setPrice(0)
        props.ModalHandler(false);
    }
    const onChnagePrice=(e)=>{
        setPrice(Number(e.currentTarget.value))
    }

    return (
        <CustomModal
        {...props}
            title={'입출금 하기'}
            className="depositPanel"
        >
            <div className="deposit_modal_body">
                <div className={'deposit_price hc'}>상품명 : {name}</div>
                <div className={'deposit_title hc '}> 금액을 입력해주세요</div>
                <div className={'deposit_input hc '}><input type="number" value={(price).toString()} onChange={onChnagePrice}/>원</div>
                <div className={'deposit_button hc'}>
                    <CustomButton onClick={onDeposit}>입금</CustomButton>
                    <CustomButton type={'confirm'} onClick={onWithdraw}>출금</CustomButton>
                </div>
            </div>
        </CustomModal>
    )
})

export default DepositModal
