import React, { memo, useEffect, useState } from 'react'
import { useDispatch } from 'react-redux';
import { useSelector } from 'react-redux';
import CustomButton from '../../../UI/Button/CustomButton';
import { infoMessage } from '../../../UI/Message/message';
import CustomModal from '../../../UI/Modal/CustomModal'
import { calcComputerScore } from '../../../util/commonMethod';
import { setActivity, setActivityContents } from '../../../_actions/activity_action';
import { Activities, Categories } from '../../../Assets/ActionAsset';
import ModalUnfoldPanel from '../options/ModalUnfoldPanel'

const ContentChoiceModal = memo((props) => {


    const dispatch = useDispatch();
    const computer = useSelector(state => state.computer);
    const currentTime = useSelector(state => state.currentTime);
    const CONTENTS_LIST = useSelector(state => state.contentsList);
    const { activity, broadcastContents, selfDevelopContents } = useSelector(state => state.activity);

    const [categorySelect, setCategorySelect] = useState('')
    const [searchTerm, setSearchTerm] = useState('');
    const [contentsList, setContentsList] = useState([]);


    const [computerScore, setComputerScore] = useState(0)


    useEffect(() => {
        setContentsList(CONTENTS_LIST);
    }, [CONTENTS_LIST])

    useEffect(() => {
        if (computer) {
            setComputerScore(calcComputerScore(computer));
        }

    }, [computer])

    const onChangeCategory = (e) => {
        let value = e.currentTarget.value;
        setCategorySelect(value)

        const newList = CONTENTS_LIST.filter(item => item.category.indexOf(value) > -1 && item.name.indexOf(searchTerm) > -1);
        setContentsList(newList);
    }

    const onChangeSearchTerm = (e) => {
        let value = e.currentTarget.value;
        setSearchTerm(value);

        const newList = CONTENTS_LIST.filter(item => item.name.indexOf(value) > -1 && item.category.indexOf(categorySelect) > -1);
        setContentsList(newList);
    }

    const onStartContents = (item) => {
        if (computerScore < item.equip_point) {
            return infoMessage('컴퓨터 점수가 부족합니다.<br/>더 좋은 장비가 필요합니다.');
        } else {
            if (activity === Activities.broadcast || activity === Activities.self_develop) {
                // 방송 중이면 컨텐츠만 변경
                dispatch(setActivityContents(item, currentTime));
            } else {
                //방송 중이 아니면 방송 셋
                dispatch(setActivity(Activities.broadcast, item, currentTime));
            }

        }
    }
    const categoryRender=(item)=>{
        if(item.category === Categories.game){
            return(
                <>
                    <span> 분류 </span>
                    <span> 게임 </span>
                    <span> 장비 점수 </span>
                    <span className={`${computerScore < item.equip_point ? 'disabled' : 'enable'}`} > {item.equip_point} </span>
                </>
            )
        }else if(item.category === Categories.exercise){
            return(
                <>
                    <span> 분류 </span>
                    <span> 운동 </span>
                    <span> 운동량 </span>
                    <span className={'enable'}> {item.advance*1000} </span>
                </>
            )
        }else{
            return(
                <>
                    <span> 분류 </span>
                    <span> 기타 </span>
                    <span> 장비 점수 </span>
                    <span className={`${computerScore < item.equip_point ? 'disabled' : 'enable'}`} > {item.equip_point} </span>
                </>
            )
        }
        
    }


    return (
        <CustomModal
            {...props}
            title="방송 컨텐츠 선택"
            className={'contents_choice_wrapper'}
        >
            <div className={'contents_choice_body'}>
                <div className="search_bar">
                    <div className="title">
                        태그
                    </div>
                    <div className="contents">
                        <select value={categorySelect} onChange={onChangeCategory}>
                            <option value={''}>전체</option>
                            <option value={'game'}>게임</option>
                            <option value={'cook'}>요리</option>
                            <option value={'exercise'}>운동</option>
                            <option value={'movie'}>영화</option>
                        </select>
                    </div>
                    <div className="title">
                        검색
                    </div>
                    <div className="contents">
                        <input value={searchTerm} onChange={onChangeSearchTerm} placeholder="이름 검색" />
                    </div>
                </div>
                <div className="search_bar">
                    현재 컴퓨터 점수 : {computerScore}
                </div>
                <div className='contents_list'>
                    {contentsList.map(item => {
                        return (
                            <div className="contents_container" key={item.key}>
                                <span> 이름 </span>
                                <span> {item.name} </span>
                                {categoryRender(item)}
                                {/* <span> 분류 </span>
                                <span> {item.category} </span>
                                <span> 장비 점수 </span>
                                <span className={`${computerScore < item.equip_point ? 'disabled' : 'enable'}`} > {item.equip_point} </span> */}
                                <div className='sub_tag' style={{ gridColumn: 'span 2' }}>
                                    <span> 연관태그 ▼</span>
                                    <hr />
                                    <div className="tag_container">
                                        {item.tag.map((item, idx) => {
                                            return (
                                                <span key={idx} className={'sub_tag'}>
                                                    {item}
                                                </span>
                                            )
                                        })}

                                    </div>
                                </div>
                                <div className={'btn_area'}>
                                    {computerScore < item.equip_point ?
                                        <CustomButton type={'danger'}>장비 점수 부족</CustomButton>
                                        :
                                        activity === Activities.broadcast ?
                                            item.key === broadcastContents.key ?
                                                <CustomButton type={'confirm'}>
                                                    방송 중인 컨텐츠
                                                </CustomButton>
                                                :
                                                <CustomButton onClick={() => onStartContents(item)}>
                                                    컨텐츠 시작
                                                </CustomButton>
                                            :
                                            activity === Activities.self_develop ?
                                                item.key === selfDevelopContents.key ?
                                                    <CustomButton type={'confirm'}>
                                                        자기계발 중인 컨텐츠
                                                    </CustomButton>
                                                    :
                                                    <CustomButton onClick={() => onStartContents(item)}>
                                                        컨텐츠 시작
                                                    </CustomButton>
                                                :
                                                <CustomButton onClick={() => onStartContents(item)}>
                                                    컨텐츠 시작
                                                </CustomButton>
                                    }
                                </div>
                            </div>
                        )
                    })}
                </div>
            </div>
        </CustomModal>
    )
})

export default ContentChoiceModal;