import moment from 'moment';
import React, { memo, useEffect, useRef, useState } from 'react'
import { useCallback } from 'react';
import { useDispatch } from 'react-redux';
import { useSelector } from 'react-redux'
import { Activities } from '../../../Assets/ActionAsset';
import { calcItemBoost, stressDanger } from '../../../util/calcAlgorithm';
import { setFaint } from '../../../_actions/sleep_action';
import { setStress } from '../../../_actions/stress_action';
import { useInterval } from './useInterval';

const StressComponent = memo(() => {

    const dispatch = useDispatch();
    const stress = useSelector(state => state.stress);
    const inventory = useSelector(state => state.inventory);
    const { activity } = useSelector(state => state.activity);
    const currentTime = useSelector(state => state.currentTime);
    // const stress = 60
    const { health: { value: health } } = useSelector(state => state.character); //건상수치
    const [percentage, setPercentage] = useState(0);
    const [barColor, setBarColor] = useState('#00c800');


    //아이템 최대 피로도 올리는 부스트
    const [boost, setBoost] = useState(0);

    const standardStress = useRef(50)


    useEffect(() => {
        if (currentTime !== 0 && moment(currentTime).minute() % 30 === 0) {
            if (activity !== Activities.sleep) {
                stressCheck();
            };
        }
    }, [currentTime]);
    

    useEffect(() => {
        let applyValue = calcItemBoost(inventory,'stress')
        setBoost(applyValue);
        if (stress > 0) {
            const pre = stress / (standardStress.current + (health * 10) + applyValue) * 100
            setPercentage(pre);

            if (pre < 55) {
                setBarColor('#00c800')
            } else if (pre < 80) {
                setBarColor('orange')
            } else {
                setBarColor('red')
            }

        } else if (stress === 0) {
            setPercentage(0)
        }
        //피로도 수치 100일때 자러감
        if (stress >= standardStress.current + applyValue + (health * 10)) {
            // console.log('강제수면이지?')
            // forceStop();
        };

    }, [stress,inventory])

    //피로도 한도 초과시 질병 및 겜멈춤
    const stressCheck = useCallback(
        () => {
            if (stress > ((standardStress.current + boost + (health * 10)) * 0.82)) {
                const flag = stressDanger(health);
                if (flag) {
                    setTimeout(() => {
                        forceStop();    
                    }, 200);
                }
            }
        },
        [stress, health],
    )

    //강제정지
    const forceStop = useCallback(
        () => {
            dispatch(setFaint(true));
        }, []
    )



    return (
        <div className={'stress_bar'} id={'stress_bar'}>
            <div className='stress_name'>
                피로도
            </div>
            <div>
                <div className="bar">
                    <div className='current' style={{ width: `${percentage}%`, background: barColor }} />
                </div>
            </div>
            <div className={'stress_number'}>
                {Math.floor(stress)} / {Math.floor(standardStress.current + boost + (health * 10))}
            </div>
        </div>
    )
})

export default StressComponent
