import { SET_EQUIPMENT_LIST, BUY_EQUIPMENT } from '../_actions/type';

const initialState = {
    MIC: {
        key: '대륙산 마이크',
        name: '대륙산 마이크',
        category: 'equipment',
        tag: ['MIC'],
        lvl: 1,
        price: 20000
    },
    CAMERA: {
        key: '평화나라 카메라',
        name: '평화나라 카메라',
        category: 'equipment',
        tag: ['CAMERA'],
        lvl: 1,
        price: 100000
    },
    CHAIR: {
        key: '접이식 의자',
        name: '접이식 의자',
        category: 'equipment',
        tag: ['CHAIR'],
        lvl: 1,
        price: 60000
    },
    DESK: {
        key: '1200 책상',
        name: '1200 책상',
        category: 'equipment',
        tag: ['DESK'],
        lvl: 1,
        price: 75000
    },
    HOUSE: {
        key: '원룸',
        name: '원룸',
        category: 'furniture',
        tag: ['HOUSE'],
        lvl: 1,
        price: 0
    }
}

export default function (state = initialState, action) {
    switch (action.type) {
        case SET_EQUIPMENT_LIST: return { ...state, ...action.payload };

        case BUY_EQUIPMENT:
            const newState = { ...state };
            newState[action.payload.tag[0]] = action.payload
            return newState;

        default: return state;
    }
}