import moment from 'moment';
import React, { useEffect } from 'react'
import { useState } from 'react';
import { useRef } from 'react';
import { useDispatch } from 'react-redux'
import { initActivity } from '../../_actions/activity_action';
import { setAsset } from '../../_actions/asset_action';
import { setBankInfomation } from '../../_actions/bank_action';
import { setCharacterInfo } from '../../_actions/character_action';
import { setCoin } from '../../_actions/coin_action';
import { setComputer } from '../../_actions/computer_action';
import { setContentsList } from '../../_actions/contents_action';
import { setCurrentTime } from '../../_actions/currentTime_action';
import { setEditorList } from '../../_actions/editor_action';
import { setEquipmentList } from '../../_actions/equipment_action';
import { setNewsList } from '../../_actions/news_action';
import { initSleep } from '../../_actions/sleep_action';
import { setStress } from '../../_actions/stress_action';
import { setTag } from '../../_actions/tag_action';
import { setUploadVideoList } from '../../_actions/uploaded_action';
import { setVideoList } from '../../_actions/video_action';
import opening from './img/opening.gif';
import openingStop from './img/opening_stop.png';

import introSound from '../../audio/intro_sound.wav';
import { setSetting } from '../../_actions/setting_action';
import { setInventory } from '../../_actions/inventory_action';
import { setAdvertisement } from '../../_actions/adv_action';
import { setDifficuty } from '../../_actions/difficulty_action';
import { confirmMessage } from '../../UI/Message/message';

function LandingComponent(props) {
    const dispatch = useDispatch();
    
    const [textShow, setTextShow] = useState(true)
    const openingImg = useRef();
    const introSoundRef = useRef();

    useEffect(() => {
        const currentTime = JSON.parse(window.localStorage.getItem('currentTime')) || moment().valueOf();
        const character = JSON.parse(window.localStorage.getItem('character'));
        const newsList = JSON.parse(window.localStorage.getItem('newsList')) || [];
        const youtubeVideo = JSON.parse(window.localStorage.getItem('youtubeVideo')) || {};
        const editorList = JSON.parse(window.localStorage.getItem('editorList')) || 'none';
        const uploadedList = JSON.parse(window.localStorage.getItem('uploadedList')) || [];
        const contentsList = JSON.parse(window.localStorage.getItem('contentsList')) || [];
        const asset = JSON.parse(window.localStorage.getItem('asset')) || {};
        const coin = JSON.parse(window.localStorage.getItem('coin'));
        const bank = JSON.parse(window.localStorage.getItem('bank'));
        const stress = JSON.parse(window.localStorage.getItem('stress'));
        const equipment = JSON.parse(window.localStorage.getItem('equipment'));
        const computer = JSON.parse(window.localStorage.getItem('computer'));
        const activity = JSON.parse(window.localStorage.getItem('activity'));
        const sleep = JSON.parse(window.localStorage.getItem('sleep'));
        const tag = JSON.parse(window.localStorage.getItem('tag')) || [];
        const setting = JSON.parse(window.localStorage.getItem('setting'));
        const inventory = JSON.parse(window.localStorage.getItem('inventory'));
        const advertisement = JSON.parse(window.localStorage.getItem('advertisement'));
        const difficulty = JSON.parse(window.localStorage.getItem('difficulty'));
        


        dispatch(setCurrentTime(Number(currentTime)));
        dispatch(setCharacterInfo(character));
        dispatch(setNewsList(newsList));
        dispatch(setVideoList(youtubeVideo));
        dispatch(setEditorList(editorList));
        dispatch(setUploadVideoList(uploadedList));
        dispatch(setContentsList(contentsList));
        dispatch(setAsset(asset));
        dispatch(setCoin(coin));
        // dispatch(setBankInfomation(bank));
        dispatch(setStress(Number(stress)));
        dispatch(setEquipmentList(equipment));
        dispatch(setComputer(computer));
        dispatch(initActivity(activity));
        dispatch(initSleep(sleep));
        dispatch(setTag(tag));
        dispatch(setSetting(setting));
        dispatch(setInventory(inventory));
        dispatch(setAdvertisement(advertisement));
        dispatch(setDifficuty(difficulty));

        const next=()=>{
            setTimeout(() => {
            
                setTextShow(false);
                setTimeout(() => {
                    openingImg.current.src = openingStop;
                    introSoundRef.current.volume = 0.5;
                    introSoundRef.current.play();
                }, 1500);
            }, 2000)
        }


        if (character) {
            const { season } = character;
            if (season) {
                const { value } = season;
                // if( moment().week() % 2 === 0){ //짝수 번째 주
                if (moment().week() - value > 3) { //게임 시작 1달 후 일요일
                    confirmMessage('본 게임은 한달에 1번 초기화를 진행합니다. 엔딩으로 가시겠습니까? (취소를 누를 경우 게임을 이어합니다.)',()=>{
                        setTimeout(() => {
                            props.history.push('/client/ending')
                        }, 1500);
                    },()=>{
                        next();
                    })
                } else {
                    next();
                }
            } else {
                next();
            }
        } else {
            next();
        }
    }, [])

    const onMoveTomain = ()=>{
        props.history.push('/client/main')
    }

    return (
        <div className={'Landing'}>
            {textShow ?  
            <span className='loading_text'>데이터를 불러오는 중입니다...</span>
            :
            <img  ref={openingImg} className='opening_img' src={opening} alt="오프닝 이미지"/>
            }
            <audio src={introSound} ref={introSoundRef} style={{display:'none'}} onEnded={onMoveTomain}/>
        </div>
    )
}

export default LandingComponent
