import React from "react";
import CustomModal from "../../UI/Modal/CustomModal";

function MainNoticeModal(props) {
  return (
    <CustomModal
      {...props}
      title={"시작 전 안내 말씀"}
      style={{
        height: "85vh",
      }}
    >
      * 안내 말씀 드립니다.
      <br />
      <br />
      싱글 모드와 멀티 모드가 있습니다.
      <br />
      <br />
      멀티 모드는 23년 4월에 개발되어 서버가 가동되는 마지막 순간까지 이용하실
      수 있습니다.
      <br />
      <br />
      서버가 멈출수도 있습니다. 개발자가 서버비용을 감당하지 못할 때 일 것
      같습니다.
      <br />
      <br />
      멀티의 서버가 멈추더라도 싱글모드는 언제든 이용하실 수 있으니 너무 걱정
      하지 않으셔도 될 것 같습니다.
      <br />
      <br />
      빅헤드를 봐주시고 함께 게임을 즐기시는 모든 분들께 감사의 말씀을 전합니다.
      <br />
      <br />
      너무 감사합니다.
      <br />
      <br />
      싱글 출시 당시에 게임이라는 것을 처음 만들어 보면서 여러모로 문제도 많았고
      도전도 많이 했었지만, 결국 느끼는 것은 게임은 기본적인 '보는맛' 이 있어야
      하는데, 저의 센스가 아직은 많이 부족한 듯 합니다.
      <br />
      <br />
      부족한 게임 이용해주시고 후기와 리뷰 남겨주셔서 정말로 감사드립니다.
      <br />
      <br />
      끝으로 재밌게 플레이 해주신 빅헤드님과 팬클럽 여러분께도 감사의 말씀을
      전합니다.
      <br />
      <br />
      <div style={{ display: "flex", justifyContent: "flex-end" }}>
        2022년 02월27일 싱글 제작
        <br />
        2023년 04월23일 멀티 제작
        <br />
      </div>
      <br />
      게임 만드는 웹개발자가 되고 싶은 <br />
      <div style={{ display: "flex", justifyContent: "flex-end" }}>
        "맥주엔나쵸" 올림{" "}
      </div>
      <br />
      <br />
    </CustomModal>
  );
}

export default MainNoticeModal;
