import {SET_MUSIC_TOGGLE,SET_MUSIC_VOLUME,SET_EFFECT_TOGGLE,SET_EFFECT_VOLUME, SET_SETTING} from '../_actions/type'

const initialState={
    Music : {
        on : false,
        volume : 30
    },
    Effect : {
        on : true,
        volume : 40
    }
}

export default function (state = initialState, action) {
    switch (action.type) {
        case SET_MUSIC_TOGGLE:
            return { ...state, Music: { ...state.Music, on: action.payload } };
        case SET_MUSIC_VOLUME:
            return { ...state, Music: { ...state.Music, volume: action.payload } };
        case SET_EFFECT_TOGGLE:
            return { ...state, Effect: { ...state.Effect, on: action.payload } };
        case SET_EFFECT_VOLUME:
            return { ...state, Effect: { ...state.Effect, volume: action.payload } };
        case SET_SETTING : 
            return action.payload || initialState;
    
        default: return state
    }
}