import { SET_INTERVAL, UP_SPEED, DOWN_SPEED, INIT_SPEED } from '../_actions/type'

//게임 인터벌
const initialState = {
    interval: 250,
    speed: 1
};

export default function (state = initialState, action) {
    let newSpeed = state.speed;
    let newInterval = state.interval;
    switch (action.type) {
        case SET_INTERVAL:
            return { ...state, interval: action.payload };
        case UP_SPEED:
            if (state.speed < 3) newSpeed = state.speed + 0.5;
            return { interval: 250 / (newSpeed + 0.5), speed: newSpeed }

        case DOWN_SPEED:
            if (state.speed - 0.5 > 0) newSpeed = state.speed - 0.5;
            return { interval: 250 / (newSpeed + 0.5), speed: newSpeed }

        case INIT_SPEED:
            return initialState
            
        default: return state;
    }
}