import moment from 'moment';
import React, { memo, useEffect, useRef, useState } from 'react'
import { useDispatch } from 'react-redux';
import { useSelector } from 'react-redux'
import { Activities } from '../../../../Assets/ActionAsset';
import { calcStressRecover, onEditorWorkInSleep, onViewCountInSleep } from '../../../../util/calcAlgorithm';
import { momentTimeString, momentToDateString } from '../../../../util/commonMethod';
import { setActivity } from '../../../../_actions/activity_action';
import { setCurrentTime } from '../../../../_actions/currentTime_action';
import { initSpeed, setTimerInterval } from '../../../../_actions/interval_action';
import { callNewsEvent } from '../../../../_actions/news_action';
import { setPlay } from '../../../../_actions/play_action';
import { setAwakeTime, setSleep } from '../../../../_actions/sleep_action';
import { recoverStress } from '../../../../_actions/stress_action';
import { setUploadVideoList } from '../../../../_actions/uploaded_action';
import { setVideoList } from '../../../../_actions/video_action';
import sleepChar from '../MonitoringImage/sleepChar.png'

const SleepInfoComponent = memo((props) => {

    const { awakeTime, flag } = useSelector(state => state.sleep);
    const currentTime = useSelector(state => state.currentTime);
    const { health: { value: health } } = useSelector(state => state.character)
    const stress = useSelector(state => state.stress);
    const [isLoading, setIsLoading] = useState(true);
    const sleepHour = useRef(1)

    const dispatch = useDispatch();

    const [show, setShow] = useState('')

    useEffect(() => {
        if (flag) {
            setShow('appear')
            dispatch(setPlay(true));
            dispatch(setTimerInterval(20));
            setIsLoading(true);
            sleepHour.current = moment(awakeTime).diff(currentTime, 'hour');
        }
    }, [flag])

    useEffect(() => {
        if (currentTime === awakeTime) {
            setIsLoading(false);

            dispatch(initSpeed());
            dispatch(setPlay(false));
        }
    }, [currentTime])


    const onWakeUp = () => {
        setShow('appear disappear')
        dispatch(setPlay(true));
        dispatch(setActivity(Activities.resting, {}, currentTime));
        const recover = calcStressRecover(sleepHour.current, health, stress);
        dispatch(recoverStress(recover));

        setTimeout(() => {
            setShow('disappear');
            dispatch(setSleep(false));
            dispatch(setAwakeTime(awakeTime));
        }, 1000);
    }

    return (
        <div className={`sleep_panel ${show}`}>
            <div className={'comment_box'}>
                <div className={'image_box'}>
                    <img src={sleepChar} /><img src={sleepChar} /><img src={sleepChar} />
                </div>

                <span className={'comment'}>
                    인트로게이숀!
                </span>
                <div>
                    기상 예정 시간 : {momentToDateString(awakeTime)} {momentTimeString(awakeTime)}
                </div>
            </div>
            {isLoading ?
                <div className={'check_btn'} >
                    로딩중 입니다......
                </div>
                :
                <div className={'check_btn'} onClick={onWakeUp}>
                    일어나기 {'>>'}
                </div>
            }

        </div>
    )
})

export default SleepInfoComponent
