import React, { memo } from 'react'
import { useState } from 'react';
import { useSelector } from 'react-redux'
import { contents_product } from '../../../../Assets/shopAsset';
import ModalUnfoldPanel from '../../options/ModalUnfoldPanel';

function AdvClientComponent() {

    const { clients } = useSelector(state => state.advertisement);
    return (
        <div className='client_side'>
            {Object.keys(clients).map((key) => {
                const { name, friendliness, completed, product } = clients[key];
                return (
                    <ModalUnfoldPanel key={key} text={name} toggle={true} unFoldStyle={{ height: '22vh' }}>
                        <div className='client'>
                            <div className='client_contents'>
                                우호도 : {friendliness}<br />
                            </div>
                            <div className='client_contents'>
                                완료한 의뢰 갯수 : {completed}
                            </div>
                            <div className='client_contents'>
                                <GameList product={product}/>
                            </div>
                        </div>
                    </ModalUnfoldPanel>
                )
            })}
        </div>
    )
}



export default AdvClientComponent

const GameList = memo((props) => {
    const {product} = props
    const [show, setShow] = useState(false);

    return (
        <>
            <div onClick={() => setShow(!show)}>관련 게임 목록 {show ? '닫기' : '보기'}</div>
            {show ?
                <div className='product_list'>
                    {product.map(item => {
                        const game = contents_product.find(c => c.key === item);
                        return (
                            <div key={item}>
                                {game.name}
                            </div>
                        )
                    })}
                </div>
                :
                <></>
            }
        </>

    )
})