import moment from 'moment';
import React, { memo, useEffect, useState } from 'react'
import { useDispatch } from 'react-redux';
import { useSelector } from 'react-redux'
import CustomButton from '../../../../../UI/Button/CustomButton';
import CustomCheckBox from '../../../../../UI/Input/CheckBox/CustomCheckBox';
import { confirmMessage } from '../../../../../UI/Message/message';
import { AddComma, sortingForObjectList } from '../../../../../util/commonMethod';
import { SignInDeposit, SignInLoan } from '../../../../../_actions/asset_action';
import { getMoney, payMoney } from '../../../../../_actions/character_action';

const BankStatus = memo((props) => {


    const { deposit, loan } = useSelector(state => state.bank);
    const { deposit: assetDeposit, loan: assetLoan } = useSelector(state => state.asset);
    const currentTime = useSelector(state => state.currentTime);

    const dispatch = useDispatch();

    const [usingGoods, setusingGoods] = useState(false);
    const [depositList, setDepositList] = useState([]);
    const [loanList, setLoanList] = useState([]);

    const [depositInterestSortDirection, setDepositInterestSortDirection] = useState(false); //예금 금리 정렬
    const [depositMonthAmountSortDirection, setDepositMonthAmountSortDirection] = useState(false); //예금 납입금 정렬

    const [loanInterestSortDirection, setLoanInterestSortDirection] = useState(false); //대출 금리 정렬
    const [loanMaxSortDirection, setLoanMaxSortDirection] = useState(false); //대출 납입금 정렬


    useEffect(() => {
        setDepositList(deposit);
        setLoanList(loan);
    }, [loan])
    useEffect(() => {
        setDepositList(deposit);
    }, [deposit])


    const onChangeFilter = (flag) => { //사용증인 상품 제외
        setusingGoods(flag);
        if (flag) {
            const newDepositList = deposit.filter(item => {
                const SignInCheck = assetDeposit.find(d => d.key === item.key);
                if (!SignInCheck) return item;
            })
            setDepositList(newDepositList)
            const newLoanList = loan.filter(item => {
                const SignInCheck = assetLoan.find(d => d.key === item.key);
                if (!SignInCheck) return item;
            })
            setLoanList(newLoanList)
        } else {
            setDepositList(deposit);
            setLoanList(loan);
        }
    }

    const depositInterestSort = () => { //예금 금리 정렬
        setDepositInterestSortDirection(!depositInterestSortDirection)
        const newList = sortingForObjectList(depositList, !depositInterestSortDirection, 'interest');
        setDepositList(newList);
    }
    const depositMonthAmountSort = () => { //예금 납입금 정렬
        setDepositMonthAmountSortDirection(!depositMonthAmountSortDirection)
        const newList = sortingForObjectList(depositList, !depositMonthAmountSortDirection, 'month_amount');
        setDepositList(newList);
    }

    const loanInterestSort = () => { //대출 금리 정렬
        setLoanInterestSortDirection(!loanInterestSortDirection)
        const newList = sortingForObjectList(loanList, !loanInterestSortDirection, 'interest');
        setLoanList(newList);
    }
    const loanMaxSort = () => { //대출 한도 정렬
        setLoanMaxSortDirection(!loanMaxSortDirection)
        const newList = sortingForObjectList(loanList, !loanMaxSortDirection, 'max');
        setLoanList(newList);
    }

    const onSignInDeposit = (product) => {
        const { key, name, month_amount, expire, interest } = product;

        const expireMoment = expire ? moment(currentTime).add(expire.year, 'year').add(expire.month, 'months').add(expire.day, 'day').set('hour', 0).set('minute', 0).set('second', 0).valueOf() : null;

        confirmMessage(`<span className='strong'>${name}</span> 상품에 가입합니다.${month_amount ? `<br/>가입시 주 납입금의 1주치(${AddComma(month_amount)})가 지불됩니다.` : ''}`, () => {
            const body = {
                key: key,
                name: name,
                money: month_amount,
                month_amount,
                start: currentTime,
                interest,
                expire: expireMoment,
            }
            dispatch(SignInDeposit(body));
            dispatch(payMoney(month_amount));
        })
    }

    const onSignInLoan = (product) => { //대출 실행
        const { key, name, interest, max, expire } = product;
        const expireMoment = expire ? moment(currentTime).add(expire.year, 'year').add(expire.month, 'months').add(expire.day, 'day').set('hour', 0).set('minute', 0).set('second', 0).valueOf() : null;
        confirmMessage(`<span className='strong'>${name}</span> 상품에 가입합니다.<br/>가입시 첫 금리가 적용됩니다.`, () => {
            const body = {
                key: key,
                name: name,
                interest,
                money: max + (max * interest),
                start: currentTime,
                expirt: expireMoment
            }
            dispatch(SignInLoan(body));
            dispatch(getMoney(max));
        })
    }


    return (
        <div className={'bank'}>
            <div>
                <CustomCheckBox value={usingGoods} onChange={onChangeFilter}>사용 중인 상품 제외</CustomCheckBox>
            </div>
            <div className={'title'}><span>예금 상품</span></div>
            <div className={'list'}>
                <div className='list_contents header'>
                    <span>상품명</span>
                    <span>가입</span>
                    <span onClick={depositInterestSort}>금리{depositInterestSortDirection ? '▲' : '▼'}</span>
                    <span onClick={depositMonthAmountSort}>월납입금{depositMonthAmountSortDirection ? '▲' : '▼'}</span>
                    <span>만기</span>
                </div>
                {depositList.map(item => {
                    const SignInCheck = assetDeposit.find(d => d.key === item.key);
                    return (
                        <div className='list_contents' key={item.key}>
                            <span>{item.name}</span>
                            <span className={SignInCheck ? 'plus' : ''}>{SignInCheck ? '실행 중' : <CustomButton onClick={() => onSignInDeposit(item)}>가입</CustomButton>}</span>
                            <span>{(item.interest * 100).toFixed(2)}%</span>
                            <span>{item.month_amount ? AddComma(item.month_amount) : '자유'}</span>
                            {item.expire ?
                                <span>
                                    {item.expire.year ? item.expire.year + '년' : ''}
                                    {item.expire.month ? ` ${item.expire.month}월` : ''}
                                    {item.expire.day ? ` ${item.expire.day}일` : ''}
                                </span>
                                :
                                <span>N</span>
                            }
                        </div>
                    )
                })}
            </div>
            <div className={'title'}><span>대출 상품</span></div>
            <div className={'list'}>
                <div className='list_contents header'>
                    <span>상품명</span>
                    <span>대출</span>
                    <span onClick={loanMaxSort}>대출액 {loanMaxSortDirection ? '▲' : '▼'}</span>
                    <span onClick={loanInterestSort}>금리 {loanInterestSortDirection ? '▲' : '▼'}</span>
                    {/* <span>만기</span> */}
                </div>
                {loanList.map(item => {
                    const SignInCheck = assetLoan.find(d => d.key === item.key);
                    return (
                        <div className='list_contents' key={item.key}>
                            <span>{item.name}</span>
                            <span className={SignInCheck ? 'plus' : ''}>{SignInCheck ? '실행 중' : <CustomButton onClick={() => onSignInLoan(item)}>대출</CustomButton>}</span>
                            <span>{item.max ? AddComma(item.max) : '제한 없음'}</span>
                            <span>{(item.interest * 100).toFixed(2)}%</span>
                            {/* {item.expire ?
                                <span>
                                    {item.expire.year ? item.expire.year + '년' : ''}
                                    {item.expire.month ? ` ${item.expire.month}월` : ''}
                                    {item.expire.day ? ` ${item.expire.day}일` : ''}
                                </span>
                                :
                                <span>N</span>
                            } */}
                        </div>
                    )
                })}
            </div>
        </div>
    )
})

export default BankStatus
