import donationImage from './MonitoringImage/donation.png';
// 안내 메세지
export const shootingInfomationMsg = (text = "", color = '') => {

    let monitor = document.getElementById('char_monitor');

    let box = document.getElementById('toast_box');
    if(!box){
        box = document.createElement('div');
        box.setAttribute('class', 'toast_box')
        box.setAttribute('id', 'toast_box')
    }
    
    let script = document.createElement('div');
    script.setAttribute('class', `shooting_infomation ${color}`)
    
    script.innerHTML = text;

    monitor.appendChild(box);
    box.appendChild(script)

    // box.classList.add('appear');
    script.classList.add('appear');
    setTimeout(() => {
        script.classList.add('disappear');
        setTimeout(() => {
            box.removeChild(script)
        }, 1000);
    }, 4000);
}

// 메인화면 발사형 유튭각 메세지
export const shootingYoutubeMsg = (text = "") => {

    let monitor = document.getElementById('char_bg');

    let script = document.createElement('div');
    script.setAttribute('class', 'shootingMessage')
    script.classList.add('appear');
    script.innerHTML = text;

    monitor.appendChild(script);


    setTimeout(() => {
        script.classList.add('disappear');
        setTimeout(() => {
            monitor.removeChild(script)
        }, 1000);
    }, 3000);
}

// 메인화면 발사형 도네 메세지
export const shootingDonationMsg = (text = "") => {

    let monitor = document.getElementById('char_bg');

    let script = document.createElement('div');
    script.setAttribute('class', 'shootingDonation')

    let image = document.createElement('img');
    image.setAttribute('src', donationImage);
    image.setAttribute('alt', '캐릭터 이미지');
    image.setAttribute('class', 'donationImage');

    let textBox = document.createElement('div');
    textBox.setAttribute('class', 'donationText');
    textBox.innerHTML = text;


    script.appendChild(image)
    script.appendChild(textBox)
    script.classList.add('appear');
    monitor.appendChild(script);


    setTimeout(() => {
        script.classList.add('disappear');
        setTimeout(() => {
            monitor.removeChild(script)
        }, 1000);
    }, 3000);
}

// 메인화면 발사형 코멘트 메세지
export const shootingCommentMsg = (text = "") => {

    let monitor = document.getElementById('character_container');

    let script = document.createElement('div');
    script.setAttribute('class', 'shootingComment')
    script.classList.add('appear');
    script.innerHTML = text;

    if(monitor){ //캐릭터가 활성화 될때만
        monitor.appendChild(script);
        setTimeout(() => {
            script.classList.add('disappear');
            setTimeout(() => {
                monitor.removeChild(script)
            }, 1000);
        }, 3000);
    }
    
}