import React, { memo, useEffect, useState } from 'react'
import { useSelector } from 'react-redux'
import CustomButton from '../../../../../UI/Button/CustomButton';
import { AddComma, sortingForObjectList } from '../../../../../util/commonMethod';
import CoinBuyModal from './CoinBuyModal';

const CoinStatus = memo((props) => {

    const { coinList: COIN_LIST } = useSelector(state => state.coin);
    const [coinList, setCoinList] = useState([]);
    const [priceSort, setPriceSort] = useState(false);
    const [coinBuyModal, setCoinBuyModal] = useState(false);
    const [selectedCoin, setSelectedCoin] = useState();

    useEffect(() => {
        if (COIN_LIST.length > 0) setCoinList(COIN_LIST);
    }, [COIN_LIST])

    const listSort = () => {
        setPriceSort(!priceSort);
        const newList = sortingForObjectList(coinList, !priceSort, 'price');
        setCoinList(newList);
    }
    const onBuyCoin = (coin) => {
        setSelectedCoin(coin);
        setCoinBuyModal(true);
    }



    return (
        <div className={'coin'}>
            <div className={'table'}>
                <div className={'header'}>
                    <span>이름</span>
                    <span onClick={listSort}>가격{priceSort ? '▲' : '▼'}</span>
                    <span>비고</span>
                </div>
                <div className={'body'}>
                    {coinList.map(item => {
                        const { name, delisting, price } = item;
                        return (
                            <div className="row" key={item.key}>
                                <div>{name}</div>
                                <div>{delisting ? '거래정지' : AddComma(price)+'원'}</div>
                                <div>{delisting ? <CustomButton type="danger">정지</CustomButton> : <CustomButton onClick={() => onBuyCoin(item)}>매수</CustomButton>} </div>
                            </div>
                        )
                    })}
                </div>
            </div>
            <CoinBuyModal visible={coinBuyModal} ModalHandler={setCoinBuyModal} coin={selectedCoin} />
        </div>
    )
})

export default CoinStatus
