import moment from 'moment';
import React, { useRef, useState } from 'react'
import { useDispatch } from 'react-redux';
import { setCurrentTime } from '../../_actions/currentTime_action';
import honjul from './img/honjul.jpg'
import crying from './img/crying.jpg'
import crying1 from './img/crying1.jpg'
import crying2 from './img/crying2.png'
import crying3 from './img/crying3.jpeg'

import openingMusic from '../../audio/opening_music.mp3';
import { useEffect } from 'react';
import { useSelector } from 'react-redux';
import { setSetting } from '../../_actions/setting_action';

function IntroComponent(props) {

    const dispatch = useDispatch();
    const introMusicRef = useRef();
    const introText = useRef(
        [
            '엄마 : 철봉아 일어나! 학교 가야지',
            '조철봉 : 으..음?... 벌써 그 시간인가..',
            '허겁지겁 등교 준비를 한다....',
            `철봉 : '아.. 어제 투윗치를 너무 늦게까지 봤나.. 너무 피곤해'`,
            <>등교길....<br /><br />빅헤드의 다시보기를 보면서 걷고 있다.<br /><br /><br /><p className={'small'}>(착한 어른이들은 걸으면서 방송을 보면 안됩니다)</p></>,
            <>'해해... 잼밌당 흐허헣ㅎ'<br />'시공의 폭풍도 자주 해주면 좋겠다..'</>,
            <>'왜 사람들은 시공을 좋아하면서 싫어하는 척을 할까?'</>,
            <>'내가 만약 빅헤드라면 시공부터 켤꺼야..'</>,
            <>의문의 목소리 : "그 말.. 진심인가??"</>,
            <>철봉 : "뭐야? 누구야?"</>,
            <>의문의 목소리<br /><br />너의 소원 마음가짐에 탐복 하였다. 자네의 소원! 들어주도록 하지</>,
            <><span className={'big sigong'}>'시공의 폭풍은 정말 최고야!' </span> <br /> 라고 3번 외쳐보거라. <br />너의 인생은 달리질 것이다.</>,
            <>철봉 : 뭐야 이 미친 소리는.. 아무리 그래도 그걸 어떻게 해?</>,
            <>의문의 목소리 <br /><br />야이~ㅎㅎㅎ 그래서 안할거야?</>,
            <><img src={crying} className={'crying'} /><span className={'small'}>시...시고의...ㅍ</span></>,
            <>의문의 목소리<br /><br /><span className={'big'}>더 크게!</span></>,
            <><img src={crying1} className={'crying1'} /><span className={'big sigong'}>시...시공의 폭풍은 정말 최고야!</span></>,
            <><img src={crying2} className={'crying2'} /><span className={'big sigong'}>시공의 폭풍은 정말 최고야!</span></>,
            <><img src={crying3} className={'crying3'} /><span className={'big sigong'}>시공의 폭풍은 저..정말 최고야!</span></>,
            <><img src={honjul} className={'honjul'} /></>,
            <>까똑! 까똑!</>,
            <>으음... 뭐지?....<br /><br /><br />(뒤척이며 일어난다)</>,
            <>하만 : <br /> &nbsp;&nbsp;&nbsp;사장님아 .<br /> &nbsp;&nbsp;&nbsp;큰일났어 잘 때가 아니야 <br /> &nbsp;&nbsp;&nbsp;유튭각 소스가 떨어졌어.<br /> &nbsp;&nbsp;&nbsp;빨리 촬영해!! <br /></>,
            <>???? 뭔소리야 ??? ...<br /><br />설마...<br /><br /><span className={'small'}>(다급히 일어나 화장실로 간다)</span></>,
            <>나의 것은 아니지만 익숙한 외모...<br />휴지칸이 많이 소모 될 것같은 머리 둘레...<br />설마..</>,
            <><span className={'big'}>나는 빅헤드다.</span></>
        ]
    )
    const [textIndex, setTextIndex] = useState(0);
    const {Music:{on:MusicOn,volume:MusicVolume}} = useSelector(state => state.setting);

    useEffect(()=>{
        const setting = JSON.parse(window.localStorage.getItem('setting'));
        dispatch(setSetting(setting))
    },[])

    useEffect(() => {

        if(textIndex === 1){
            introMusicRef.current.volume = MusicVolume/100;
            if(MusicOn) introMusicRef.current.play();
        }
    }, [textIndex])

    const onNextText = () => {
        setTextIndex(textIndex + 1);
    }

    const onStartGame = () => {
        dispatch(setCurrentTime(moment().set('second', 0).valueOf()))
        props.history.push('/client/status')
    }

    return (
        <div className={'intro'}>
            <div className={'text_container'}>
                {
                    introText.current[textIndex]
                }
            </div>
            {introText.current.length - 1 === textIndex ?
                <div className="btn_wrapper">
                    <div className={'check_btn'} onClick={onStartGame}>
                        게임 시작
                    </div>
                </div>
                :
                <div className='btn_wrapper'> 
                    <div className={'jump_btn'} onClick={onStartGame}>
                        건너뛰기{'>>>'}
                    </div>
                    <div className={'check_btn'} onClick={onNextText}>
                        다음{'>'}
                    </div>
                </div>
            }

            <audio ref={introMusicRef} src={openingMusic} style={{display:'none'}} loop/>
        </div>
    )
}

export default IntroComponent
