import { RECOVER_STRESS, SET_STRESS, UP_STRESS } from '../_actions/type';


const initialState = 0;


export default function (state = initialState, action) {
    switch (action.type) {

        case SET_STRESS: return action.payload === 0 ? 0 : Number(action.payload.toFixed(2));

        case RECOVER_STRESS: //피로도 회복 이거 왜 안되지? 
            let calc = Number((state - action.payload).toFixed(2))
            if (calc < 0) {
                return 0
            } else {
                return calc;
            }

        case UP_STRESS: // 피로도 증가
            const newState = Number((state + action.payload).toFixed(2))
            if(newState < 0){
                return 0 ; 
            }else{
                return newState;
            }
            
        default: return state;
    }
}