import {BUY_ITEM,SET_INVENTORY,USE_INVENTORY_ITEM, SET_PASSIVE_ITEM, SET_SIDE_EFFECT_ITEM,SET_USE_ITEM} from './type'


export function buyItem(item,count){
    
    return {
        type : BUY_ITEM,
        payload : {item,count}
    }

}


export function setInventory(obj){
    return{
        type : SET_INVENTORY,
        payload : obj
    }
}

export function setUseInventoryItem(key){
    return{
        type:USE_INVENTORY_ITEM,
        payload : key
    }
}
export function setPassiveItem(key,buffExpireTime,debuffExpireTime){
    return{
        type : SET_PASSIVE_ITEM,
        payload : {key,buffExpireTime,debuffExpireTime}
    }
}

export function setSideEffectItem(key,flag){
    return{
        type : SET_SIDE_EFFECT_ITEM,
        payload : {key,flag}
    }
}
export function setUseItem(key,flag){
    return{
        type : SET_USE_ITEM,
        payload : {key,flag}
    }
}