import React from 'react'
import { useState } from 'react'
import CustomModal from '../../../UI/Modal/CustomModal'
import AdvClientComponent from './Section/AdvClientComponent'
import AdvQuestComponent from './Section/AdvQuestComponent'

function AdvertisementModal(props) {

    const [selectedTab, setSelectedTab] = useState(1)

    return (
        <CustomModal
            {...props}
            title={'광고의뢰 (퀘스트)'}
            className={'advertisement_modal'}
        >
            <div className='advertisement_modal_list'>
                {
                    selectedTab === 1 && <AdvQuestComponent/>
                }
                {
                    selectedTab === 2 && <AdvClientComponent/>
                }
            </div>
            <div className='tab_container'>
                <div className={`tab_button ${selectedTab===1 ?'selected':''}`} onClick={()=>setSelectedTab(1)}>
                    <div className='hc vc'>
                        의뢰 목록
                    </div>
                </div>
                <div className={`tab_button ${selectedTab===2 ?'selected':''}`} onClick={()=>setSelectedTab(2)}>
                    <div className={`hc vc`}>
                        광고주
                    </div>
                </div>
            </div>

        </CustomModal>
    )
}

export default AdvertisementModal