import React, { useEffect, useRef } from 'react'
import { useState } from 'react'
import { useDispatch } from 'react-redux';
import { useSelector } from 'react-redux';
import { confirmMessage, infoMessage } from '../../../UI/Message/message';
import { setTutorial } from '../../../_actions/tutorial_action';

function TutorialComponent(props) {

    const tutorial = useSelector(state => state.tutorial);
    const dispatch = useDispatch();

    const [tutorialQuestion, setTutorialQuestion] = useState(JSON.parse(window.localStorage.getItem('tutorialShow')) === null ? true : JSON.parse(window.localStorage.getItem('tutorialShow')));
    const [tutorialIndex, setTutorialIndex] = useState(0);
    // const tutorialProgress = JSON.parse(window.localStorage.getItem('tutorial'));
    const prevStyle = useRef();
    const tutorialScript = useRef([
        {
            id: 'char_monitor',
            zIndex: 1,
            info:
            <div className='text' style={{ maxHeight: '40vh' }}>
                    캐릭터의 상황창 입니다. 캐릭터의 활동 내용에 따라 모습이 달라집니다. 배경은 가구상점에서 구매하는 '주거환경'에 따라 달라집니다.<br /><br />
                    우측 상단의 <span className='blue'>D-Day</span>는 게임 내의 경과 시간입니다.<br />

                    우측 하단의 <span className='blue'>책갈피</span>는 캐릭터의 태그 전문성을 나타냅니다. 전문성이 높을 수록 조회수와 수익금이 상승합니다.<br /><br />
                    우측 상단의 <span className='blue'>캐릭터 버튼</span>은 캐릭터의 능력치 정보 입니다.<br />

                </div>,
            textPosition: 50
        },
        {
            id: 'stress_bar',
            zIndex: 1,
            info:
            <div className='text' style={{ maxHeight: '40vh' }}>
                    캐릭터의 피로도 입니다.
                    총 피로도의 <span className="red">80%</span>에 해당하게 되면 <span className='red'>빨간색</span>으로 바뀌게 됩니다.
                    <br />
                    80% 이후에는 확률적으로 기절합니다. 기절하게 되면 건강이 크게 감소하여 총 피로도에 영향을 줍니다.
                </div>,
            textPosition: 60
        },
        {
            id: 'Timer_box',
            zIndex: 1,
            info:
            <div className='text' style={{ maxHeight: '40vh' }}>
                    인 게임 내의 시간입니다.<br />
                    <span className="blue">재생 버튼</span> 을 눌러서 게임을 <span className="red">진행 / 일시정지</span> 할 수 있습니다.<br />
                    <span className="blue">속도 조절 버튼</span> 을 통해서 시간경과를 <span className="red">빠르게 / 느리게</span> 변경 할 수 있습니다.<br />

                </div>,
            textPosition: 65
        }, {
            id: 'control_1',
            zIndex: 1,
            info: 
            <div className='text' style={{ maxHeight: '40vh' }}>
                뉴스 상황을 알 수 있는 버튼입니다.
                매일 0시에 확률적으로 뉴스 이벤트가 발생하며, 뉴스에 따라 각 태그에 버프, 디버프 효과를 줍니다.
            </div>,
            textPosition: 20
        }, {
            id: 'control_2',
            zIndex: 1,
            info: 
            <div className='text' style={{ maxHeight: '40vh' }}>
                활동을 설정하는 버튼입니다.<br />
                <span className="blue">방송</span> : 방송을 시작합니다.<br />
                - 시청자수와 비례하여 확률적으로 수입이 들어옵니다.<br />
                - 태그 전문성과 비례하여 확률적으로 유튜브 각이 생성됩니다.<br />
                - 피로도가 증가합니다. <br />
                <br />
                <span className="blue">편집</span> : 유튜브 편집본을 편집합니다.<br />
                - 무엇을 편집할지 지정해주지 않는 경우 최신본을 먼저 편집합니다.<br />
                - 피로도가 증가합니다. <br />
                <br />
                <span className="blue">휴식</span>: 휴식합니다. 일정시간 마다 피로도를 회복합니다.<br />
                <span className="blue">운동</span> : 일정시간마다 건강 수치를 증가시킵니다. <br />
                - 건강 수치는 최대 피로도에 영향을 줍니다.<br />
                <br />
                <span className="blue">수면</span> : 6~9시간 수면을 취합니다. 피로도를 많이 회복합니다.<br />
                <br />
                <span className="blue">자기계발</span> : 스스로를 수련합니다.<br />
                - 진행하는 컨텐츠에 해당하는 전문성을 빠르게 상승 시킵니다.<br />
                - 피로도가 증가합니다. <br />
            </div>,
            textPosition: 15
        }, {
            id: 'control_3',
            zIndex: 1,
            info: <>
                <span className="blue">방송 컨텐츠</span>를 선택할 수 있습니다.<br />
                방송 컨텐츠는 <span className="red">컨텐츠 상점</span>에서 구입할 수 있으며, 컨텐츠의 태그와 일치하는 경우, 해당 태그의 전문성이 높아집니다.<br />
            </>,
            textPosition: 30
        },
        {
            id: 'control_4',
            zIndex: 1,
            info:
                <>
                    본인이 소유하고 있는 <span className="blue">장비와 장비점수</span>를 확인 할 수 있습니다.<br />
                    또한 소모품의 보유 현황을 확인 할 수 있습니다.<br/>
                    장비는 <span className="red">가구상점</span>에서 구입가능합니다.<br />
                </>,
            textPosition: 30
        }, {
            id: 'control_5',
            zIndex: 1,
            info:
                <>
                    방송 중 발생한 <span className="blue">유튜브각을 관리</span>합니다.<br />
                    타이틀 변경을 통해 <span className="red">제목을 변경</span> 할 수 있습니다.<br />
                    편집 설정을 통해 <span className='red'>담당편집자를 배정</span>할 수 있습니다.<br />
                    편집이 완료된 항목은 <span className="red">업로드</span>를 통해 업로드 조회에서 <span className="red">조회수 및 수익금 확인</span>이 가능합니다.
                </>,
            textPosition: 20
        }, {
            id: 'control_6',
            zIndex: 1,
            info: 
            <div className='text' style={{ maxHeight: '40vh' }}>
                편집자의 상태를 확인 합니다.<br /><br />
                1. 편집자의 급여는 <span className='blue'>매주 월요일 0시</span>에 <span className="red">지급</span>됩니다.<br />
                2. 편집 업무 부여를 통해 <span className="blue">편집자에게 업무를 부여</span> 할 수 있습니다.<br />
                3. 편집자와 담당 컨텐츠의 태그가 <span className='blue'>많이 일치</span> 할 수록 편집속도가 <span className="red">증가</span>합니다.<br />
                4. 편집자의 편집 능력치가 높을수록 편집속도가 <span className="blue">증가</span>합니다.
                5. 편집자는 <span className='red'>근무시간</span>에만 활동합니다.<br />
            </div>,
            textPosition: 30
        }, {
            id: 'control_7',
            zIndex: 1,
            info: <>
                업로드한 영상을 확인 할 수 있습니다. <br /><br />
                <span className="blue">조회수</span>와 <span className="red">수익금</span>을 확인 할 수 있습니다.<br /><br />
                수익금은 <span className="blue">자동 지급</span> 됩니다.
            </>,
            textPosition: 30
        },  {
            id: 'control_8',
            zIndex: 1,
            info: <>
                퀘스트 임무가 될예정입니다. <br />
                업데이트 예정입니다.<br />
                
            </>,
            textPosition: 30
        },{
            id: 'control_9',
            zIndex: 1,
            info: 
            <div className='text' style={{ maxHeight: '40vh' }}>
                나의 자산을 관리 합니다.<br /><br />
                <span className="blue">은행</span>에서 <span className="red">예금/대출</span>을 할 수 있습니다.<br />
                <span className="red">매주 월요일</span> 예금액과 대출 금리가 출금 됩니다.
                <br /><br />
                <span className="blue">코인장</span>에서 <span className="red">코인</span>을 구매 할 수 있습니다.<br />
                <span className="red">인게임 3시간 </span> 간격으로 금액이 변동합니다.<br />
                <span className="red">거래정지</span> 되는 경우 내가 소유하고 있다면 <span className="red">압류</span>되어 사라집니다.
                <br /><br />
                tip . 코인 가격은 순전히 랜덤입니다. 본인의 운을 시험해보세요
                수익금은 <span className="blue">자동 지급</span> 됩니다.
            </div>,
            textPosition: 20
        },
        {
            id: 'control_10',
            zIndex: 1,
            info: 
            <div className='text' style={{ maxHeight: '40vh' }}>
                소모품 상점 : 소모품을 구매합니다.<br />
                각 상품을 눌러보면 상세 정보를 확인 할 수 있습니다.<br />
                버프와 디버프가 있으니 자세히 읽어봐 주세요. :)<br />
                <br />
                컨텐츠 상점 : 컨텐츠를 구매할 수 있습니다.<br />
                나의 장비점수를 초과하는 컨텐츠를 구매하는 경우<br />
                안내메세지가 나옵니다.<br />
                <br />
                가구{"&"}컴퓨터 : 가구와 장비를 구매할 수 있습니다.<br />
                <br />
                <br />
                - 가구 <br />
                마이크 : <span className="blue">목소리, 화술</span>와 시너지<br />
                카메라 : <span className="blue">외모</span>와 시너지<br />
                의자 : <span className="blue">피로도 회복</span>과 시너지<br />
                책상 : <span className="blue">편집 능력</span>과 시너지(예정)<br />
                침대 : <span className="blue">휴식, 수명</span>과 시너지(예정)<br />
                주거환경 : 플렉스~<br />

                <br />
                tip. 좋은 의자를 제일 먼저 사십시요...<br />
                피로도 증가량을 감소시켜 줍니다.
                <br />
                - 컴퓨터 <br />
                본체 : <span className="blue">부품슬롯</span>이 <span className="red">증가</span>합니다.<br />
                CPU : 비싼게 좋습니다.<br />
                GPU : 본체가 다중 슬롯일 경우 먼저 끼워진 부품이 교체됩니다.<br />
                RAM : 본체가 다중 슬롯일 경우 먼저 끼워진 부품이 교체됩니다.<br />

                컴퓨터 장비의 경우 코인 가격에 비례해서 비싸집니다.<br />
                <br />

                
            </div>,
            textPosition: 10
        },
        {
            id: 'control_11',
            zIndex: 1,
            info: <>
                공지사항 입니다.<br /> 패치내역 및 공지사항을 확인할 수 있습니다.
            </>,
            textPosition: 30
        }, {
            id: 'control_12',
            zIndex: 1,
            info: <>
                설정입니다.<br />
                배경음악 및 효과음을 조절할 수 있습니다.
            </>,
            textPosition: 30
        }, {
            id: '',
            info:
                <div style={{ width: '100%', height: '80vh', overflowY: 'auto' }}>
                    진행 방식
                    <br />
                    <br />
                    <span className="blue">1</span>. 방송 진행 <br />
                    <span className="blue">2</span>. 확률적으로 유튜브각 생성<br />
                    <span className="blue">3</span>. 유튜브각 편집<br />
                    <span className="blue">4</span>. 완성된 유튜브각 업로드<br />
                    <span className="blue">5</span>. 수익창출<br />
                    <span className="red">6</span>. 반복
                    <br />
                    <br />
                    <span className="red">초기화</span>: 시작후 1주 뒤의 일요일 0시
                    <br />
                    <br />
                    이 게임의 "엔딩 컨텐츠"는 없습니다.<br />
                    플레이어 본인이 생각 또는 상상하는 "큰머리" 를 육성하는 것이 목표이자 과정이라고 보시면 됩니다.
                    <br />
                    <br />
                    지하철에서 오고갈때, 또는 화장실에 앉아서, 자기 전에 잠깐 하는 느낌의 게임으로 제작하였습니다.<br />
                    저 또한 새로운 컨텐츠를 많이 추가하고 싶고, 계획중에 있습니다.<br />
                    <span style={{ textDecoration: 'line-through' }}>(제로투 추는 빅헤드 육성? 못참지)</span><br />
                    부족한 게임이지만 플레이 해주셔서 정말 감사합니다.<br />
                    빅헤드 유튜브 구독과 좋아요 감사합니다.<br />
                </div>,
            textPosition: 10

        }

    ])

    useEffect(() => {

        if (tutorialQuestion) {
            confirmMessage(`게임 가이드를 보시겠습니까?`, () => {
                dispatch(setTutorial(true));
            }, () => {
                window.localStorage.setItem('tutorialShow', JSON.stringify(false));
                dispatch(setTutorial(false));
                setTutorialQuestion(false);
                infoMessage(`캐릭터 창의 왼쪽 아래의 안내 버튼으로 가이드 내용을 확인 하실 수 있습니다.`)
            })
        }
    }, [tutorialQuestion])
    useEffect(() => {
        if (tutorial) {
            setTutorialQuestion(false);
            const targetElement = document.getElementById(tutorialScript.current[tutorialIndex].id);
            getPrevStyle(targetElement);
            targetElement.style.zIndex = tutorialScript.current[tutorialIndex].zIndex
        }

    }, [tutorial])


    const onJumpTutorial = () => {
        confirmMessage(`정말 튜토리얼을 건너 뛰시겠습니까?`, () => {
            window.localStorage.setItem('tutorialShow', JSON.stringify(false))
            setPrevStyle(document.getElementById(tutorialScript.current[tutorialIndex].id))
            dispatch(setTutorial(false))
            setTutorialIndex(0);
            infoMessage('게임 가이드는 끝났지만, 우측 상단의 안내 버튼으로 내용을 확인 하실 수 있습니다.')
        })
    }
    const onNextGuide = () => {
        //이전 스타일 돌려놓음
        const prevEle = document.getElementById(tutorialScript.current[tutorialIndex].id);
        if (prevEle) {
            setPrevStyle(prevEle)
        }

        if (tutorialIndex + 1 >= tutorialScript.current.length) {
            infoMessage('가이드를 모두 확인 하셨습니다. 감사합니다.', () => {
                setTutorialIndex(0);
                dispatch(setTutorial(false))
                window.localStorage.setItem('tutorialShow', JSON.stringify(false));
            })
        } else {
            //인덱스 증가
            setTutorialIndex(tutorialIndex + 1)
            //새 스타일 입혀서 노출
            const elementContents = tutorialScript.current[tutorialIndex + 1];
            if (elementContents.id) {
                const targetElement = document.getElementById(elementContents.id);
                getPrevStyle(targetElement);
                targetElement.style.zIndex = elementContents.zIndex;
                if (!targetElement.style.position || targetElement.style.position === 'static') targetElement.style.position = 'relative'
            }
        }
    }
    const getPrevStyle = (ele) => {

        const computeStyle = window.getComputedStyle(ele);
        prevStyle.current = {
            zIndex: computeStyle.getPropertyValue('z-index') || 0,
            position: computeStyle.getPropertyValue('position') || 'unset'
        }
    }
    const setPrevStyle = (ele) => {
        if(ele){
            ele.style.position = prevStyle.current.position;
            ele.style.zIndex = prevStyle.current.zIndex;
        }
    }


    if (tutorial) {
        return (
            <div className='tutotial_wrapper'>
                <div className={`tutorial_text `}
                    style={{ top: `${tutorialScript.current[tutorialIndex].textPosition}%` }}
                >
                    {tutorialScript.current[tutorialIndex].info}
                </div>
                <div className="control_btn" >
                    <div className='jump' onClick={onJumpTutorial}>
                        건너뛰기
                    </div>
                    <div className="next" onClick={onNextGuide}>
                        다음{">>>"}
                    </div>
                </div>

            </div>
        )
    } else {
        return <></>
    }

}

export default TutorialComponent