import { combineReducers } from 'redux'
import progress from './progress_reducer';
import character from './character_reducer';
import newsList from './news_reducer';
import interval from './interval_reducer';
import editorList from './editor_reducer';
import youtubeVideo from './video_reducer';
import newEditor from './new_editor_reducer';
import uploadedList from './uploaded_reducer';
import contentsList from './contents_reducer';
import asset from './asset_reducer';
import coin from './coin_reducer';
import bank from './bank_reducer';
import currentTime from './currentTime_reducer';
import stress from './stress_reducer';
import equipment from './equipment_reducer';
import computer from './computer_reducer';
import activity from './activity_reducer';
import sleep from './sleep_reducer';
import tag from './tag_reducer';
import play from './play_reducer';
import setting from './setting_reducer';
import { INIT_GAME, RESET_GAME } from '../_actions/type';
import tutorial from './tutorial_reducer';
import inventory from './inventory_reducer';
import advertisement from './adv_reducer';
import difficulty from './difficulty_reducer';
// const rootReducer = combineReducers({
