// tag
// })
const appReducer = combineReducers({
    play,
    currentTime,
    progress,
    character,
    newsList,
    interval,
    youtubeVideo,
    editorList,
    newEditor,
    uploadedList,
    contentsList,
    asset,
    coin,
    bank,
    stress,
    equipment,
    computer,
    activity,
    sleep,
    tag,
    setting,
    tutorial,
    inventory,
    advertisement,
    difficulty
})

const rootReducer = (state, action) => {
    if (action.type === RESET_GAME||action.type === INIT_GAME) {
        state = { //설정은 살려둠
            setting :  state.setting
        }
    }
    return appReducer(state, action)
};

export default rootReducer;