import React, { memo } from 'react'
import { useSelector } from 'react-redux'
import KrwIcon from '../../../scss/icon/KrwIcon';
import { AddComma } from '../../../util/commonMethod';

const UserStatus = memo((props) => {
    const character = useSelector(state => state.character);
    const { name, money } = character;
    return (
        <div className='character_status'>
            <div className="character_name" >
                <div className='title'>
                    이름
                </div>
                <div className="contents">
                    <div className="status">
                        {name.value}
                    </div>
                    <div className="edit" >
                        *
                    </div>
                </div>
            </div>
            <div className="character_money">
                <div className="title">
                    <KrwIcon />
                </div>
                <div className="contents">
                    <div className="status">
                        {
                            money.value > 100000000 ?
                                '▲99,999,999'
                                :
                                AddComma(Math.ceil(money.value))
                        }
                    </div>
                    <div className="edit" >
                        +
                    </div>
                </div>
            </div>
        </div>
    )
})

export default UserStatus
