
import { BUY_COIN, WITHDRAW_MONEY, DEPOSIT_MONEY, TERMINATE_DEPOSIT, PAY_LOAN_MONEY, SELL_COIN, SIGN_IN_DEPOSIT, SIGN_IN_LOAN, DEPOSIT_INTEREST_APPLY, SET_ASSET_INFORMATION,CHECK_MY_COIN } from './type'

export function setAsset(asset) {
    return {
        type: SET_ASSET_INFORMATION,
        payload: asset
    }
}


export function buyCoin(coin) {
    return {
        type: BUY_COIN,
        payload: coin
    }
}

export function withdrawMoney(contents) {
    return {
        type: WITHDRAW_MONEY,
        payload: contents
    }
}

export function depositMoney(contents) {
    return {
        type: DEPOSIT_MONEY,
        payload: contents
    }
}

export function terminateDeposit(deposit) {
    return {
        type: TERMINATE_DEPOSIT,
        payload: deposit
    }
}

export function payLoanMoney(loan) {
    return {
        type: PAY_LOAN_MONEY,
        payload: loan

    }
}

export function sellCoin(coin) {
    return {
        type: SELL_COIN,
        payload: coin
    }
}

export function SignInDeposit(product) {
    return {
        type: SIGN_IN_DEPOSIT,
        payload: product
    }
}

export function SignInLoan(product) {
    return {
        type: SIGN_IN_LOAN,
        payload: product
    }
}

export function depositInterestApply() {
    return {
        type: DEPOSIT_INTEREST_APPLY,
    }
}

//코인 가격 체크
export function checkMyCoin(coinList){
    return{
        type : CHECK_MY_COIN,
        payload : coinList
    }

}