import React from 'react';
import CustomModal from '../../../UI/Modal/CustomModal';
import { momentToDateString } from '../../../util/commonMethod';

function NoticeDetail(props) {
    const {notice} = props
  return (
      <CustomModal
      title={'공지사항 상세'}
      {...props}
      className={'notice_detail_modal'}
      >
          <div className='title'>
              {notice.title}
          </div>
          <div className='date'>
              날짜 : {momentToDateString(notice.date) }
          </div>
          <div className='contents'>
              {notice.contents}
          </div>
      </CustomModal>
  );
}

export default NoticeDetail;
