import { SET_ADV_LIST, SUCCESS_ADV,CALL_NEW_ADV,EXPIRE_ADV } from "./type";

export function setAdvertisement(adv){
    return{
        type : SET_ADV_LIST,
        payload : {...adv}
    }

}

export function successAdv(quest){
    return {
        type : SUCCESS_ADV,
        payload : quest
    }
}

export function callNewAdv (time,viewer){
    return {
        type : CALL_NEW_ADV,
        payload : {time,viewer}
    }
}

export function expireAdv (time){
    return{
        type : EXPIRE_ADV,
        payload : time
    }
}