import React, { useRef, useState } from 'react'
import f0 from './MonitoringImage/exercise/f0.png';
import f1 from './MonitoringImage/exercise/f1.png';
import f2 from './MonitoringImage/exercise/f2.png';
import f3 from './MonitoringImage/exercise/f3.png';
import f4 from './MonitoringImage/exercise/f4.png';
import f5 from './MonitoringImage/exercise/f5.png';
import f6 from './MonitoringImage/exercise/f6.png';
import f7 from './MonitoringImage/exercise/f7.png';
import f8 from './MonitoringImage/exercise/f8.png';
import f9 from './MonitoringImage/exercise/f9.png';
import f10 from './MonitoringImage/exercise/f10.png';
import f11 from './MonitoringImage/exercise/f11.png';
import { useInterval } from './useInterval';
import { useSelector } from 'react-redux';

function ExerciseComponent() {

    const imageList = useRef([
        f0, f1, f2, f3, f4, f5, f6, f7, f8, f9, f10, f11, f10, f9, f8, f7, f6, f5, f4, f5, f3, f2, f1
    ])
    const showingIndex = useRef(0);
    const [showingImage, setShowingImage] = useState(f0);
    const play = useSelector(state => state.play)
    const { interval } = useSelector(state => state.interval)

    useInterval(() => {
        setShowingImage(imageList.current[showingIndex.current]);
        showingIndex.current++
        if (showingIndex.current === imageList.current.length) {
            showingIndex.current = 0;
        }
    }, interval / 3, play)

    return (
        <div className={'exercise'} style={{ background: `center / cover no-repeat url(${showingImage})` }} />
    )
}

export default ExerciseComponent
