import React, { memo, useEffect, useState } from 'react'
import { useDispatch } from 'react-redux';
import { useSelector } from 'react-redux';
import KrwIcon from '../../../scss/icon/KrwIcon';
import CustomButton from '../../../UI/Button/CustomButton';
import CustomModal from '../../../UI/Modal/CustomModal'
import { AddComma, momentToDateString, sortingForObjectList } from '../../../util/commonMethod';
import { getMoney } from '../../../_actions/character_action';
import { getVideoListRevenue, getVideoRevenue } from '../../../_actions/uploaded_action';

const UploadedListModal = memo((props) => {

    const dispatch = useDispatch();
    const uploadedList = useSelector(state => state.uploadedList)
    const [revenueSortDirection, setRevenueSortDirection] = useState(false);
    const [viewSortDirection, setViewSortDirection] = useState(false)
    const [videoList, setVideoList] = useState([]);

    useEffect(() => {
        if (uploadedList.length > 0) {
            setVideoList(uploadedList);
        }
    }, [uploadedList])

    const onSortListByRevenue = (e) => {
        setRevenueSortDirection(!revenueSortDirection);
        const newList = sortingForObjectList(videoList, !revenueSortDirection, 'total_revenue');
        setVideoList(newList);
    }
    const onSortListByView = (e) => {
        setViewSortDirection(!viewSortDirection);
        const newList = sortingForObjectList(videoList, !viewSortDirection, 'view');
        setVideoList(newList);
    }
    //금액 수금 (단일)
    const onCollectRevenue = (video) => {
        if (video.revenue > 0) {
            dispatch(getMoney(video.revenue));
        }
        dispatch(getVideoRevenue(video));
    }
    const onCollectAllRevenue = () => {
        const allMoney = videoList.reduce((acc, current) => {
            return acc + current.revenue;
        }, 0)
        if (allMoney > 0) {
            dispatch(getMoney(allMoney));
            dispatch(getVideoListRevenue());
        }
    }

    return (
        <CustomModal
            {...props}
            title='업로드 영상 목록'
            className={'uploaded_modal'}
        >
            <div className={`uploaded_container`}>
                <div className={`search_bar`}>
                    <div onClick={onSortListByRevenue}>누적 수익금{revenueSortDirection ? '▼' : '▲'}</div>
                    <div onClick={onSortListByView}>조회수{viewSortDirection ? '▼' : '▲'}</div>
                    {/* <div className='collect_all' onClick={onCollectAllRevenue}>
                        <div>수익금 수령</div>
                    </div> */}
                </div>
                <div className={`video_list`}>
                    {videoList.length > 0 ?
                        videoList.map(item => {
                            return (
                                <div key={item.key} className={'video_contents'}>
                                    <div className={'video_info'}>
                                        <div className={`title`}>
                                            <span>{item.name}</span>
                                        </div>
                                        <div>조회수 : <span className='view_count'>{AddComma(item.view)}</span> 회</div>
                                        <div>누적 수익금 : <span className='revenue'>{AddComma(item.total_revenue)}</span>원</div>
                                        <div>인기도 : <span className='revenue'>{Math.floor(item.majority)}</span></div>
                                        {/* <div>레이팅 : <span className='revenue'>{Math.floor(item.rating)}</span></div> */}
                                        {/* <div>촬영일 : <span>{momentToDateString(item.date)}</span></div> */}
                                        <div>업로드 : <span>{momentToDateString(item.upload_date)}</span></div>
                                    </div>
                                    {/* <CustomButton type="confirm" className={'collect_btn'} onClick={() => onCollectRevenue(item)}>
                                        금액 수령
                                    </CustomButton> */}
                                </div>
                            )
                        })
                        :
                        <div className={'hc vc'}>
                            업로드 된 영상이 없습니다.
                        </div>
                    }

                </div>
            </div>
        </CustomModal>
    )
})

export default UploadedListModal
