import React, { memo, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux';
import CustomButton from '../../../../UI/Button/CustomButton';
import CustomCheckBox from '../../../../UI/Input/CheckBox/CustomCheckBox';
import { confirmMessage, infoMessage, promptMessage } from '../../../../UI/Message/message';
import { JSONtoArray, momentToDateString } from '../../../../util/commonMethod';
import { addUploadVideo } from '../../../../_actions/uploaded_action';
import { removeVideo, setVideoTitle, setWorkEditor, setWorkMine } from '../../../../_actions/video_action';

const VideoContents = memo((props) => {

    const { item, editor, manager } = props;
    const dispatch = useDispatch();
    const currentTime = useSelector(state => state.currentTime);
    const youtubeVideo = useSelector(state => state.youtubeVideo);
    const editorList = useSelector(state => state.editorList);
    const { name: { value: myName } } = useSelector(state => state.character);


    const [editorSet, setEditorSet] = useState(false);
    const [tagShow, setTagShow] = useState(false);

    const onEditorWork = () => { //내가 직접하는 편집
        dispatch(setWorkMine(item));
        setEditorSet(false);
    }

    const videoTitleSet = (video) => {
        promptMessage('제목을 입력해주세요', (name) => {
            dispatch(setVideoTitle(video, name))
        })
    }
    const onRemoveVideo = (video) => {
        confirmMessage('영상이 삭제됩니다.', () => {
            dispatch(removeVideo(video))
        })
    }

    const onUploadVideo = (video) => {
        const uploadVideo = { ...video };
        uploadVideo.upload_date = currentTime;
        
        dispatch(addUploadVideo(uploadVideo))
        dispatch(removeVideo(video));
    }

    const onEditorWorkSet = (editor) => { //편집 업무 부여로 온경우
        const working = JSONtoArray(youtubeVideo).filter(ytb => ytb.editor === editor.key).length
        if (working + 1 > editor.work_count) {
            infoMessage('작업가능량을 초과하였습니다!')
        } else {
            dispatch(setWorkEditor(editor, item))
        }
        setEditorSet(false);
    }

    return (
        <div className={'video_contents'}>
            <div className={'control_grid'}>
                <CustomButton type={'danger'} onClick={() => onRemoveVideo(item)}>
                    삭제
                </CustomButton>
            </div>
            <div className={`title ${item.name ? '' : 'untitle'}`}>
                {item.name ? '제목 : ' + item.name : '제목을 설정해주세요.'}
            </div>
            <div>
                촬영일 : <span>{momentToDateString(item.date)}</span>
            </div>
            <div>
                유튭각 : {Array(5).fill().map((arr, idx) => {
                    if (idx < item.rating) {
                        return <span className={'star'} key={idx}>★</span>
                    } else {
                        return <span className={'star_empty'} key={idx}>☆</span>
                    }
                })}
            </div>
            {/* <div className={`main_tag ${editor?.tag.indexOf(item.category) > -1 ? 'pro' : ''}`}>
                                        태그 : {item.category}
                                    </div> */}
            {
                item.tag &&
                <div className={'sub_tag'}>
                    <div onClick={() => setTagShow(!tagShow)}>태그 {tagShow ? '▼' : '▶'}</div>
                    {tagShow &&
                        <>
                            <hr />
                            <div className={'tag_container'}>
                                {item.tag.map((tag, idx) => (
                                    <span className={` ${editor?.tag.indexOf(tag) > -1 ? 'pro' : ''}`} key={tag}> {tag} </span>
                                ))}
                            </div>
                        </>
                    }

                </div>
            }
            <div>
                담당 편집자 : {item.editor === 'mine' ? myName : manager ? manager.name : <span style={{ color: 'red' }}>담당자 없음</span>}
            </div>
            <div className={'progress_container'} >
                <div className={'title'}> 완성도 </div>
                <div className={'progress_bar'}>
                    <div className={'progress_back'}>
                        <div className={'progress_rating'} style={{ width: `${item.progress}%` }} >
                            &nbsp;
                        </div>
                    </div>
                </div>
                <div className={"progress_number"}>
                    {item.progress}%
                </div>
            </div>

            <div className="setting_button">
                <CustomButton onClick={() => videoTitleSet(item)}>
                    제목 설정
                </CustomButton>
                {item.progress === 100 ?
                    <CustomButton type="confirm" onClick={() => onUploadVideo(item)}> 업로드 </CustomButton>
                    :
                    editor ?
                        <CustomButton onClick={() => onEditorWorkSet(editor)}> 편집 진행 </CustomButton>
                        :
                        <CustomButton onClick={() => setEditorSet(!editorSet)}> 편집자 설정</CustomButton>
                }
            </div>
            {editorSet &&
                <div className='editor_list'>
                    <div className='mine' onClick={onEditorWork}>{myName}</div>
                    {editorList.map(edt => <div key={edt.key} onClick={() => onEditorWorkSet(edt)}>{edt.name}</div>)}
                </div>
            }
        </div>
    )
})

export default VideoContents
