import moment from 'moment';
import React from 'react';
import { useState } from 'react';
import { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { useSelector } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { infoMessage } from '../../UI/Message/message';
import { AddComma, momentToDateString, sortingForObjectList, TimeComparison } from '../../util/commonMethod';
import { ResetGame } from '../../_actions/currentTime_action';

function EndingComponent(props) {
  const dispatch = useDispatch();
  const currentTime = useSelector(state => state.currentTime)
  const character = useSelector(state => state.character)
  const newsList = useSelector(state => state.newsList)
  const youtubeVideo = useSelector(state => state.youtubeVideo)
  const uploadedList = useSelector(state => state.uploadedList)
  const contentsList = useSelector(state => state.contentsList)
  const asset = useSelector(state => state.asset)
  const equipment = useSelector(state => state.equipment)
  const computer = useSelector(state => state.computer)
  const tag = useSelector(state => state.tag)
  const activity = useSelector(state => state.activity)
  const editorList = useSelector(state => state.editorList)

  const [maxView, setMaxView] = useState();
  const [maxRevenue, setMaxRevenue] = useState();

  useEffect(() => {
    maxContents();
  }, []);


  const onInitGame = () => {
    infoMessage(`이용해주셔서 정말 감사합니다 :)<br/>다음 시즌 진행을 위해서 초기화하여 시작합니다.`, () => {
      dispatch(ResetGame());
      props.history.push('/client/intro');
    })
  }

  const maxContents = () => {
    const view = sortingForObjectList(uploadedList, false, 'view');
    setMaxView(view[0]);
    const revenue = sortingForObjectList(uploadedList, false, 'total_revenue');
    setMaxRevenue(revenue[0]);
  }

  return (
    <div className='ending_wrapper'>
      <div className='credit_title'>CREDIT</div>
      <div className='credit'>
        <div>Thank You for Play</div>

        <div className='title'>
          스트리머 타이쿤 : 빅헤드 키우기
        </div>
        <div>당신의 큰머리쟝..의 업적</div>
        <div className="sub_title">
          캐릭터
        </div>
        <div>이름 : {character.name.value}</div>
        <div>소지금 :  {AddComma(character.money.value)}원</div>
        <div style={{ color: 'red', fontSize: 'small' }}>은행 및 코인은 차후 업데이트 됩니다 ;)</div>
        <div>건강 : {Math.round(character.health.value)}</div>
        {
          character.health.value < 5 && <div style={{ color: 'red' }}>건강관리 좀 하세요..눈나 나죽어...</div>
        }
        <div>경과 시간 : {moment(currentTime).diff(activity.gameStartTime, 'day')}일</div>

        <div className='sub_title'>
          유튭 업로드
        </div>
        <div>
          총 업로드 갯수 : {uploadedList.length}
        </div>
        <div>
          최고 인기 컨텐츠
        </div>
        {maxView ?
          <div className="contents_wrapper">
            <div>
              유튜브 명 {">"}
              <div>{maxView.name}</div>
            </div>
            <div>
              유튭각 지수 {"> "}
              {Array(5).fill().map((arr, idx) => {
                if (idx < maxView.rating) {
                  return <span className={'star'} key={idx}>★</span>
                } else {
                  return <span className={'star_empty'} key={idx}>☆</span>
                }
              })}
            </div>
            <div>
              조회수 {"> "}
              <span>{AddComma(maxView.view)}회</span>
            </div>
            <div>
              수익금 {"> "}
              <span>{AddComma(maxView.total_revenue)}원</span>
            </div>
            <div>
              편집자 {"> "}
              <span>{maxView.editor === "mine" ? character.name.value : maxView.editor}</span>
            </div>
            <div>
              업로드 날짜 {"> "}
              <span>{momentToDateString(maxView.upload_date)}</span>
            </div>
          </div>
          :
          <div> 컨텐츠가 없습니다. </div>
        }

        <div>
          최대 수익금
        </div>
        {maxRevenue ?
          <div className="contents_wrapper">
            <div>
              유튜브 명 {">"}
              <div>{maxRevenue.name}</div>
            </div>
            <div>
              유튭각 지수 {"> "}
              {Array(5).fill().map((arr, idx) => {
                if (idx < maxRevenue.rating) {
                  return <span className={'star'} key={idx}>★</span>
                } else {
                  return <span className={'star_empty'} key={idx}>☆</span>
                }
              })}
            </div>
            <div>
              조회수 {"> "}
              <span>{AddComma(maxRevenue.view)}회</span>
            </div>
            <div>
              수익금 {"> "}
              <span>{AddComma(maxRevenue.total_revenue)}원</span>
            </div>
            <div>
              편집자 {"> "}
              <span>{maxRevenue.editor === "mine" ? character.name.value : maxRevenue.editor}</span>
            </div>
            <div>
              업로드 날짜 {"> "}
              <span>{momentToDateString(maxRevenue.upload_date)}</span>
            </div>
          </div>
          :
          <div> 컨텐츠가 없습니다. </div>
        }
        <div className="sub_title">
          보유 컨텐츠 ({contentsList.length}개)
        </div>
        <div className="contents_wrapper">
          {contentsList.map(item => {
            return (
              <div className='contents' key={item.key}>
                {item.name}
              </div>
            )
          })}
        </div>
        <div className="sub_title">
          전문성
        </div>
        <div className="contents_wrapper tags" style={{ height: '200px' }}>
          {tag.map((item, idx) => {
            return (
              <div key={item.name}>{item.name} : {Math.round(item.value)}</div>
            )
          })}
        </div>
        <div className="sub_title">
          장비
        </div>
        <div className="contents_wrapper">
          {Object.keys(computer).map(item => {
            const parts = computer[item];
            if (item === 'RAM' || item === "GPU") {
              return (
                <>
                  <div>{item}</div>
                  <div className="contents_wrapper">
                    {parts.map((p, idx) => {
                      return (
                        <div key={idx}>
                          {p.name}
                        </div>
                      )
                    })}
                  </div>
                </>
              )
            } else {
              return (
                <div key={parts.key}>{item} : {parts.name}</div>
              )
            }
          })}
        </div>
        <div className="sub_title">
          직원 목록
        </div>
        {editorList.map(item => {
          return (
            <div key={item.key} className="contents_wrapper">
              <div>이름 : {item.name}</div>
              <div>급여 : {AddComma(item.cost)}원</div>
              <div>편집한 영상 수 : {(uploadedList.filter(u => u.editor === item.key)).length}개</div>
            </div>
          )
        })}
      </div>
      <div className='confirm_btn' onClick={onInitGame}>확인{">>>"}</div>
    </div>
  );
}

export default withRouter(EndingComponent);
