import React from 'react'
import { useState } from 'react'
import CustomModal from '../../../UI/Modal/CustomModal';
import ExpandableShopComponent from './ExpandableShopComponent';
import FurnitureShopComponent from './FurnitureShopComponent';
import GameShopComponent from './GameShopComponent';

function ShopModal(props) {

    const [tabNumber, setTabNumber] = useState(1);

    return (
        <CustomModal
            {...props}
            title={'통합 상점'}
            style={{ height: '75vh' }}
            bodyStyle={{ padding: '0' }}
            className={'shop_wrapper'}
        >
            <div className={'asset_body'}>
                {tabNumber === 1 && <ExpandableShopComponent />}
                {tabNumber === 2 && <GameShopComponent />}
                {tabNumber === 3 && <FurnitureShopComponent />}
                <div className={'tab_container'}>
                    <div className={`tab_button ${tabNumber === 1 ? 'selected' : ''}`}>
                        <div className={'hc vc'} onClick={() => setTabNumber(1)} >
                            소모품 상점
                        </div>
                    </div>
                    <div className={`tab_button ${tabNumber === 2 ? 'selected' : ''}`}>
                        <div onClick={() => setTabNumber(2)} className={'hc vc'} >
                            컨텐츠 상점
                        </div>
                    </div>
                    <div className={`tab_button ${tabNumber === 3 ? 'selected' : ''}`}>
                        <div onClick={() => setTabNumber(3)} className={'hc vc'}>
                            가구{'&'}컴퓨터
                        </div>
                    </div>
                </div>
            </div>
        </CustomModal>
    )
}

export default ShopModal