import moment from 'moment'
import React, { memo, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import CustomButton from '../../../UI/Button/CustomButton'
import CustomCheckBox from '../../../UI/Input/CheckBox/CustomCheckBox'
import { infoMessage } from '../../../UI/Message/message'
import CustomModal from '../../../UI/Modal/CustomModal'
import { JSONtoArray } from '../../../util/commonMethod'
import { setActivity } from '../../../_actions/activity_action'
import { setWorkMine } from '../../../_actions/video_action'
import ModalUnfoldPanel from '../options/ModalUnfoldPanel'
import { Activities } from '../../../Assets/ActionAsset'
import BroadcastPanel from './Contents/BroadcastPanel'
import EditPanel from './Contents/EditPanel'
import ExercisePanel from './Contents/ExercisePanel'
import RestPanel from './Contents/RestPanel'
import SelfTrainingPanel from './Contents/SelfTrainingPanel'
import SleepPanel from './Contents/SleepPanel'

const ActionChoiceModal = memo((props) => {
    const [AwakeAlarmSet, setAwakeAlarmSet] = useState(false); //수면 탭 토글
    const [ExerciseSet, setExerciseSet] = useState(false);// 운동 토글


    const dispatch = useDispatch();
    const contentsList = useSelector(state => state.contentsList);
    const currentTime = useSelector(state => state.currentTime);
    const youtubeVideo = useSelector(state => state.youtubeVideo);
    const {broadcastContents} = useSelector(state=>state.activity);

    const onBroadCastStart = () => {
        if(Object.keys(broadcastContents).length > 0 ){

            dispatch(setActivity(Activities.broadcast, broadcastContents, currentTime))
        }else{
            const initialContents = contentsList.find(item => item.key === '저스트 채팅');
            dispatch(setActivity(Activities.broadcast, initialContents, currentTime))
        }
        
        props.ModalHandler(false);
    }

    const onRest = () => {
        dispatch(setActivity(Activities.resting, {}, currentTime));
        props.ModalHandler(false);
    }

    const onCloseModal = () => {
        setAwakeAlarmSet(false);
        props.ModalHandler(false);
    }

    const onEditorStart = () => {
        const myEditor = JSONtoArray(youtubeVideo).find(item => item.editor === 'mine');
        const noEditorList = JSONtoArray(youtubeVideo).filter(item => item.editor === null && item.progress < 100);
        if (!myEditor && noEditorList.length === 0) return infoMessage('편집할 수 있는 영상이 없습니다.')
        noEditorList.sort((a, b) => {
            if (moment(a.date) > moment(b.date)) {
                return -1
            } else {
                return 1
            }
        })
        if (!myEditor) dispatch(setWorkMine(noEditorList[0]))
        dispatch(setActivity(Activities.edit, myEditor || noEditorList[0]))
        props.ModalHandler(false);
    }


    return (
        <CustomModal
            {...props}
            ModalHandler={onCloseModal}
            title={'활동선택'}
            className={'action_modal_wrapper'}
        >
            <div className="action_modal_body">
                <ModalUnfoldPanel text="방송" toggle unFoldStyle={{ height: '25%' }} value={<CustomButton onClick={onBroadCastStart} >실행</CustomButton>}>
                    <BroadcastPanel {...props} />
                </ModalUnfoldPanel>
                <ModalUnfoldPanel text="휴식" toggle unFoldStyle={{ height: '25%' }} value={<CustomButton onClick={onRest} >실행</CustomButton>}>
                    <RestPanel {...props} />
                </ModalUnfoldPanel>
                <ModalUnfoldPanel text="영상 편집" toggle unFoldStyle={{ height: '25%' }} value={<CustomButton onClick={onEditorStart} >실행</CustomButton>}>
                    <EditPanel {...props} />
                </ModalUnfoldPanel>
                <ModalUnfoldPanel text="수면" toggle unFoldStyle={{ height: AwakeAlarmSet ? '70%' : '50%' }}>
                    <SleepPanel AwakeAlarmSet={AwakeAlarmSet} setAwakeAlarmSet={setAwakeAlarmSet} {...props} />
                </ModalUnfoldPanel>
                <ModalUnfoldPanel text="운동" toggle unFoldStyle={{ height: '40%' }} >
                    <ExercisePanel {...props}/>
                </ModalUnfoldPanel>
                <ModalUnfoldPanel text="자기계발" toggle unFoldStyle={{ height: '40%' }} >
                    <SelfTrainingPanel {...props}/>
                </ModalUnfoldPanel>
            </div>
        </CustomModal>
    )
})

export default ActionChoiceModal
