import React from 'react'
import { useState } from 'react';
import { useSelector } from 'react-redux'
import { contents_product } from '../../../../Assets/shopAsset';
import { AddComma } from '../../../../util/commonMethod';
import AdvQuestDetailModal from './AdvQuestDetailModal';

function AdvQuestComponent() {
    const { quest, clients } = useSelector(state => state.advertisement);
    const [detailModalOpen, setDetailModalOpen] = useState(false);
    const [selectedQuest, setSelectedQuest] = useState()

    const onViewDetail = (item)=>{
        setDetailModalOpen(true);
        setSelectedQuest(item);
    }

    return (
        <div className='quest_side'>
            {Object.keys(quest).length>0?
            Object.keys(quest).map(key=>{
                const { contentsKey, rating, period, reward, accept, client } = quest[key];
                const contents = contents_product.find(c=>c.key === contentsKey);
                
                const detailObj = {
                    quest : quest[key],
                    contents,
                    client : clients[client]
                }
                return(
                    <div className={`quest_contents ${accept?'accept':''}`} key={key} onClick={()=>onViewDetail(detailObj)}>
                        <div className='contents_name'>
                            [광고 의뢰] {contents.name} 
                        </div>
                        <div className="quest_rating">
                            <span className='star'>★</span> {rating}
                        </div>
                    </div>
                )
            })
            :
            <div className='quest_empty'>
                의뢰온 광고가 없습니다.
                <br/><br/>매주 월요일 0시 확률적으로 의뢰가 옵니다.
                <br/><br/>의뢰를 달성하여 우호도가 증가하면 광고 발생확률과 보상금이 증가합니다.
            </div>
            }
            <AdvQuestDetailModal visible={detailModalOpen} ModalHandler={setDetailModalOpen} quest={selectedQuest}/>
        </div>
    )
}

export default AdvQuestComponent