import React, { useEffect } from 'react'
import { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import CustomButton from '../../../UI/Button/CustomButton';
import { confirmMessage, infoMessage } from '../../../UI/Message/message';
import CustomModal from '../../../UI/Modal/CustomModal'
import { AddComma } from '../../../util/commonMethod';
import { payMoney } from '../../../_actions/character_action';
import { buyItem } from '../../../_actions/inventory_action';

function ExpandableBuyModal(props) {
    const {item} = props;
    const dispatch = useDispatch();
    const { money: { value: MyMoney } } = useSelector(state => state.character);

    const [buyCount, setBuyCount] = useState(0)

    const onBuyItem = () => {
        if(buyCount < 1) return infoMessage('구매량을 입력해주세요.');
        confirmMessage(`${item.name}을(를) 구매하시겠습니까?<br/>비용:${AddComma(item.cost*buyCount)}원`, () => {
            if (MyMoney > item.cost) {
                //아이템 디스패치
                dispatch(buyItem(item,buyCount));
                dispatch(payMoney(item.cost*buyCount))
                props.ModalHandler(false);
            } else {
                infoMessage(`소지금액이 부족합니다.`)
            }
        })
    }
    useEffect(() => {
        if(props.visible){
            setBuyCount(0);
        }
    }, [props.visible])
    

    return (
        <CustomModal
        {...props}
        title={`${item?.name} 구매`}
        className={'expandable_buy_modal'}
        >
            <div className='item_title'>
                {item?.name}
            </div>
            <div className='item_count_input'>
                <input className='count' type={'number'} min={0} value={(buyCount).toString()} onChange={e => setBuyCount(Number(e.currentTarget.value))} />
                개 구매
            </div>
            <div className='button_wrapper'>
                <CustomButton onClick={onBuyItem}>
                    구매
                </CustomButton>
                <CustomButton type={'danger'} onClick={()=>props.ModalHandler(false)}>
                    취소
                </CustomButton>
            </div>
            
        </CustomModal>
    )
}

export default ExpandableBuyModal