import { shootingInfomationMsg, shootingYoutubeMsg } from "../component/Main/Section/shootingToast";
import { calcFaceCoefficient, calcPersonalityCoefficient, calcTagCoefficient, calcVoiceCoefficient, calcYoutubeRating } from "../util/calcAlgorithm";
import { JSONtoArray, momentToDateString } from "../util/commonMethod";
import { ADD_VIDEO, SET_VIDEO_LIST, SET_VIDEO_EDITOR, SET_VIDEO_TITLE, REMOVE_VIDEO, SET_VIDEO_MY_EDIT, SET_VIDEO, UP_PROGRESS_MINE, UP_EDITOR_PROGRESS, SET_STOP_WORKING, DELETE_VIDEO_RATING } from "../_actions/type";
import youtubImage from '../component/Main/Section/MonitoringImage/youtubGak.png';
import moment from "moment";
import { Categories } from "../Assets/ActionAsset";

const initialState = {

}

export default function (state = initialState, action) {
    if (action.type === SET_VIDEO_LIST) {
        return action.payload
    } else if (action.type === ADD_VIDEO) {
        let { voice, speach, MIC, face, CAMERA, tag, contents, personality, time , currection , category } = action.payload
        //마이크계수
        const micCoefficient = calcVoiceCoefficient(voice.value, speach.value, MIC.lvl);
        //카메라계수
        const cameraCoefficient = calcFaceCoefficient(face.value, CAMERA.lvl);
        //컨텐츠 태그 계수
        const contentsTag = calcTagCoefficient(tag, contents.tag);
        //성격 계수
        const personalityCoefficient = calcPersonalityCoefficient(personality.value);
        //기본 발생 확률
        const minPercentage = 10
        //유튭각 계산
        let percent = minPercentage;

        if(Categories?.correction?.ytb[category]){
            percent *= Categories.correction?.ytb[category];
        }

        if(currection.youtube) percent *= currection.youtube;
        percent *= micCoefficient;
        percent *= cameraCoefficient;
        percent *= contentsTag;
        percent *= personalityCoefficient;
        let newVideo = null;
        if ((Math.random() * 100) < percent) {
            //유튭각 !!!
            //단계별 유튭각 생성 확률
            const rating = calcYoutubeRating(percent,currection)
            newVideo = new Object;
            newVideo.key = time;
            newVideo.date = time;
            newVideo.editor = null;
            newVideo.category = contents.category;
            newVideo.tag = contents.tag;
            newVideo.majority = contents.majority * contentsTag;
            newVideo.rating = rating;
            newVideo.progress = 0;
            newVideo.name = `${contents.name}_${momentToDateString(time)}`;
            newVideo.contentsKey = contents.key;

            let ratingText = '';
            Array(5).fill().map((t, i) => {
                if (i < rating) {
                    ratingText += `<span class='star'>★</span>`
                } else {
                    ratingText += `<span class='star_empty'>☆</span>`
                }
            })
            shootingYoutubeMsg(`<div class='youtube_alert'><img src='${youtubImage}' </div><div class='star'>${ratingText}</div><div class='comment'>녹화본 생성</div>`)
        }
        if (newVideo) {
            const newState = { ...state };
            newState[time] = newVideo;
            return newState;
        } else {
            return state;
        }
    } else if (action.type === SET_VIDEO_MY_EDIT) {

        const newList = { ...state };
        const stateKeys = Object.keys(newList);
        stateKeys.map(key => {
            if (newList[key].editor === 'mine') {
                newList[key].editor = '';
            }
        })
        newList[action.payload.key].editor = 'mine';
        return newList;

    } else if (action.type === SET_VIDEO_EDITOR) {

        const { editor, video } = action.payload
        const newList = { ...state };
        newList[video.key].editor = editor.key;
        return newList;

    } else if (action.type === SET_VIDEO_TITLE) {
        const newList = { ...state };
        newList[action.payload.video.key].name = action.payload.name;
        return newList;
    } else if (action.type === REMOVE_VIDEO) {
        const newList = { ...state };
        delete newList[action.payload.key]
        return newList;

    } else if (action.type === SET_VIDEO) {

        const newList = { ...state };
        newList[action.payload.key] = action.payload;
        return newList;

    } else if (action.type === UP_PROGRESS_MINE) {

        const { edit, tag } = action.payload
        let myEditContents = JSONtoArray(state).find(ytb => ytb.editor === 'mine')
        if (myEditContents.progress === 100) {
            return state;
        } else {
            const tagCoefficient = calcTagCoefficient(tag, myEditContents.tag);
            let newProgress = myEditContents.progress + (edit.value * 1.3 * tagCoefficient);
            if (newProgress > 100) { //완성
                newProgress = 100
                myEditContents.progress = 100;
            } else {
                myEditContents.progress = Number(newProgress.toFixed(1));
            }
        }
        const newList = { ...state };
        newList[myEditContents.key] = myEditContents;
        return newList;

    } else if (action.type === UP_EDITOR_PROGRESS) {
        const { newTime, youtubeVideo = [], editorList = [] } = action.payload

        // 편집능력이 5를 기본으로 할때
        // 1개 동영상을 만드는데 8시간이 걸리고
        // 1개만 진행하는 경우는 10을 올리고
        // 2개 동시진행은 6 6
        // 3개 동시진행은 4 4 4
        // 4개 동시진행은 3 3 3을 기본으로 한다.
        const currentHour = moment(newTime).hour();
        const newState = { ...state };
        const stateKeys = Object.keys(state);
        stateKeys.map((key, idx) => {
            const ytb = newState[key];
            if (ytb.editor && ytb.editor !== 'mine' && ytb.progress < 100) {
                //편집자 능치 불러옴
                const { working_time, tag, edit_ability, key } = editorList.find(item => item.key === ytb.editor);
                //편집자 근무 시간인지 확인
                if (currentHour >= working_time[0] && currentHour <= working_time[1]) {
                    //편집자와 맞는 태그 갯수 불러옴
                    let matchingTagCount = 0;
                    ytb.tag.map(t => {
                        if (tag.indexOf(t) > -1) {
                            matchingTagCount = matchingTagCount + 1;
                        }
                    })
                    //편집자가 30분당 올릴수 있는 기본량
                    const _workingRate = edit_ability * 0.32;
                    //유튜브의 태그와 편집자 태그 갯수의 일치율 구함
                    const _matchRate = matchingTagCount > 0 ? ((matchingTagCount / ytb.tag.length) + 1) : 1;
                    //편집자가 진행중인 작업 갯수 
                    const _workingCount = youtubeVideo.filter(i => i.editor === key).length;
                    //해당 유튜브에 올릴수 있는 량
                    let upRate = 0;
                    if (_workingCount > 1) {
                        upRate = _workingRate * _matchRate;
                    } else {
                        upRate = ((_workingRate / _workingCount) * 1.3) * _matchRate;
                    }
                    ytb.progress = Number((ytb.progress + upRate).toFixed(1));

                    if (ytb.progress > 100) {
                        ytb.progress = 100;
                        shootingInfomationMsg(`${ytb.name} 편집완료!`)
                        //레이팅이 올라갈 확률 기본 20% * 태그 맞는 확률
                        const _ratingUpWin = 1.1 * _matchRate * ((edit_ability / 25) + 1);
                        if (Math.random() < _ratingUpWin / 100) {
                            // 레이팅업
                            //1개 오를 확률 80% 2개 오를 확률 15% 3개 오를 확률 5%
                            const _ratingUpPercent = Math.random();
                            if (_ratingUpPercent < 0.05) {
                                ytb.rating = ytb.rating + 1;
                            } else if (_ratingUpPercent < 0.20) {
                                ytb.rating = ytb.rating + 2;
                            } else {
                                ytb.rating = ytb.rating + 3;
                            }
                            ytb.rating = ytb.rating > 5 ? 5 : ytb.rating;
                        }
                    }
                }
            }
            return ytb;
        });
        return newState;
    } else if (action.type === SET_STOP_WORKING) {
        const newState = { ...state }
        newState[action.payload.key].editor = "";
        return newState;
    }else if (action.type === DELETE_VIDEO_RATING){
        const newState = {...state};
        const ratings = action.payload;
        Object.keys(newState).map(key=>{
            if(ratings.indexOf(newState[key].rating) > -1){
                delete newState[key];
            }
        })
        return newState;
    } else {
        return state;
    }
}