import React, { memo, useEffect, useState } from 'react'
import { useSelector } from 'react-redux'
import CustomModal from '../../../UI/Modal/CustomModal'
import { AddComma } from '../../../util/commonMethod'
import ModalUnfoldPanel from '../options/ModalUnfoldPanel'

const CharacterInfoModal = memo((props) => {

    const character = useSelector(state => state.character);
    const difficulty =  useSelector(state => state.difficulty);
    const [keySet, setKeySet] = useState([]);

    useEffect(() => {
        if (character) {
            setKeySet(Object.keys(character));
        }
    }, [character])

    return (
        <CustomModal
            title={"스트리머 정보"}
            {...props}
            style={{
                height: '70vh'
            }}
        >
            <div className="char_modal_body">
                {keySet.map((item, idx) => {
                    const { value, text } = character[item]
                    return (
                        <ModalUnfoldPanel text={text} value={typeof value === 'number' ? AddComma(value) : value} key={text} />
                    )
                })}
                <ModalUnfoldPanel text={'난이도'} value = {difficulty === 1 ? '쉬움' : difficulty === 2?'보통':'하드' }/>
            </div>
        </CustomModal>
    )
})

export default CharacterInfoModal
