import { SET_NEWS_LIST,CALL_NEWS_EVENT,REMOVE_NEWS } from './type'


export function setNewsList(list) {
    return {
        type: SET_NEWS_LIST,
        payload: list,
    }
}
/**
 * 뉴스 생성
 * @param1 현재시간
 * **/
export function callNewsEvent(moment){
    return {
        type: CALL_NEWS_EVENT,
        payload : moment
    }
}
/**
 * 뉴스삭제
 * @param1 현재시간
 * **/
export function removeNews(time){
    return{
        type : REMOVE_NEWS,
        payload : time
    }
}