import React, { memo, useEffect, useState } from 'react'
import { useSelector } from 'react-redux';
import { useDispatch } from 'react-redux';
import CustomButton from '../../../../../../UI/Button/CustomButton';
import { infoMessage } from '../../../../../../UI/Message/message';
import CustomModal from '../../../../../../UI/Modal/CustomModal'
import { payLoanMoney } from '../../../../../../_actions/asset_action';
import { payMoney } from '../../../../../../_actions/character_action';

const LoanPayModal = memo((props) => {
    const { loanIndex } = props;
    const [inputMoney, setInputMoney] = useState(0);
    const dispatch = useDispatch();
    const { loan: loanList } = useSelector(state => state.asset);
    const {money: { value: myMoney }} = useSelector(state=>state.character)
    const [money, setMoney] = useState();

    useEffect(() => {
        if(props.visible && loanList.length > 0){
            setMoney(loanList[loanIndex].money)
        }
        return ()=>{
            setMoney();
        }
    }, [loanIndex,props.visible])

    const onPayLoan = () => {
        if (!inputMoney) return infoMessage('입력값을 확인해주세요')
        if (inputMoney > money) return infoMessage('입력값이 대출금보다 많습니다.')
        if (inputMoney > myMoney) return infoMessage('입력값이 보유금보다 많습니다.')
        const body = {
            ...loanList[loanIndex],
            money: money - inputMoney
        }
        dispatch(payLoanMoney(body));
        dispatch(payMoney(inputMoney))
        onCloseModal();
    }
    const onSetRemainingLoan = () => {
        setInputMoney(money)
    }

    const onCloseModal = () => {
        setInputMoney(0);
        props.ModalHandler(false);
    }

    return (
        <CustomModal
            {...props}
            title={'대출금 지불'}
            className={'loan_panel'}
            ModalHandler={onCloseModal}
        >
            <div className={'loan_modal_body'}>
                <div className={'loan_title hc'} style={{ textDecoration: 'underline' }} onClick={onSetRemainingLoan}>
                    잔금 전액 입력
                </div>
                <div className={'loan_input hc'} >
                    금액 입력
                    <input type={'number'} min={0} max={money} value={(inputMoney).toString()} onChange={e => setInputMoney(Number(e.currentTarget.value))} />
                    원
                </div>
                <div className={'loan_button hc'}>
                    <CustomButton onClick={onPayLoan}>
                        지불
                    </CustomButton>
                    <CustomButton type={'danger'} onClick={onCloseModal}>
                        취소
                    </CustomButton>
                </div>


            </div>

        </CustomModal>
    )
})

export default LoanPayModal
