import { BUY_ITEM, SET_INVENTORY, USE_INVENTORY_ITEM,SET_PASSIVE_ITEM, SET_SIDE_EFFECT_ITEM, SET_USE_ITEM } from "../_actions/type";

const initialState = {

}

export default function (state = initialState, action) {
    let _prevState = {};

    switch (action.type) {
        case BUY_ITEM:
            const _newItem = { ...action.payload.item };
            const _buyCount = action.payload.count;
            const _prevBuyState = { ...state };
            const _prevBuy = _prevBuyState[_newItem.key];
            if (_prevBuy) {
                console.log()
                //갯수 추가
                _prevBuyState[_newItem.key].count = _prevBuyState[_newItem.key].count + _buyCount;
            } else {
                //새로 추가
                _newItem.count = _buyCount;
                _newItem.use = false;
                delete _newItem.desc;
                _prevBuyState[_newItem.key] = {..._newItem}
                
            }
            return _prevBuyState;
        case SET_INVENTORY:
            return action.payload || {}
        case USE_INVENTORY_ITEM:
            _prevState = {...state};
            
            if(_prevState[action.payload].count-1 === 0 ){
                delete _prevState[action.payload]
            }else{
                _prevState[action.payload].count = _prevState[action.payload].count-1
            }

            return _prevState;

        case SET_PASSIVE_ITEM:
            let { key, buffExpireTime, debuffExpireTime } = action.payload;
            _prevState = { ...state }
            _prevState[key].use = true;
            _prevState[key].buffExpireTime = buffExpireTime;
            _prevState[key].debuffExpireTime = debuffExpireTime;
            _prevState[key].count = _prevState[key].count-1;

            return _prevState;

        case SET_SIDE_EFFECT_ITEM:
            _prevState = {...state}
            _prevState[action.payload.key].sideEffect = action.payload.flag;
            //사이드 이펙트 끝났을때 갯수 확인 해서 0개면 인벤에서 삭제
            if(!action.payload.flag && _prevState[action.payload.key].count === 0 ){
                delete _prevState[action.payload.key];
            }
            return _prevState;
        case SET_USE_ITEM:
            _prevState = {...state};
            _prevState[action.payload.key].use = action.payload.flag;
            if(!action.payload.flag && !_prevState[action.payload.key].debuffExpireTime && _prevState[action.payload.key].count === 0 ){
                delete _prevState[action.payload.key];
            }
            return _prevState

        default: return state;
    }
}