import React, { memo, useEffect, useState } from 'react'
import { useSelector } from 'react-redux'
import CustomModal from '../../../../UI/Modal/CustomModal'
import { calcComputerScore } from '../../../../util/commonMethod';
import { imgs } from '../../../../Assets/imgAsset';
import emptyProduct from '../../Shop/img/emptyproduct.png';

const EquipmentModal = memo((props) => {


    const equipment = useSelector(state => state.equipment);
    const computer = useSelector(state => state.computer);
    const [totalEquipScore, setTotalEquipScore] = useState(0);
    const [Equips, setEquips] = useState([]);
    const [Furnitures, setFurnitures] = useState([])
    const [comKeys, setComKeys] = useState([]);

    useEffect(() => {
        if (Object.keys(equipment).length > 0) {
            let eq = [];
            let fu = [];
            Object.keys(equipment).map(item => {
                if (equipment[item].category === 'equipment') eq.push(equipment[item])
                if (equipment[item].category === 'furniture') fu.push(equipment[item])
            })
            setEquips(eq);
            setFurnitures(fu);
        }
    }, [equipment])

    useEffect(async () => {
        if (Object.keys(computer).length > 0) {
            setComKeys(Object.keys(computer));
            const computerScore = calcComputerScore(computer);
            setTotalEquipScore(computerScore.toFixed(2));
        }
    }, [computer])

    return (
        <CustomModal
            {...props}
            title={'장비 목록'}
            className={'equip_modal'}
        >
            <div className={'category'}>
                컴퓨터 : 장비 스코어 <span>{totalEquipScore}점</span>
            </div>
            <hr />
            {/* 컴퓨터 본체 */}
            {comKeys.map(item => {
                //램, GPU
                if (computer[item].length > 0) {
                    return (
                        <div className={'subItem'} key={item}>
                            <div className={'title'}>{item}</div>
                            {
                                computer[item].map((subItem, idx) => {
                                    const { name, key, tag, lvl, category, equip_point } = subItem;
                                    const imgSource = imgs[`${tag[0]}_${lvl}`] ? imgs[`${tag[0]}_${lvl}`] : null;
                                    return (
                                        <div className={'product_box'} key={`${key}_${idx}`}>
                                            <div className={'product_image'} style={{ background: `center / contain no-repeat url(${imgSource})` }} />
                                            <div className={'product_name'}>{name}</div>
                                            <div className={'product_category'}>카테고리 : {category}</div>
                                            <div>장비 점수 : {equip_point}</div>
                                        </div>
                                    )
                                })
                            }
                        </div>
                    )
                } else {
                    //일반 부품
                    const { name, key, tag, lvl, category, ramslot, gpuslot, equip_point } = computer[item]
                    const imgSource = imgs[`${tag[0]}_${lvl}`] ? imgs[`${tag[0]}_${lvl}`] : null;
                    return (
                        <div className={'product_box'} key={key}>
                            <div className={'product_image'} style={{ background: `center / contain no-repeat url(${imgSource})` }} />
                            <div className={'product_name'}>{name}</div>
                            <div className={'product_category'}>카테고리 : {category}</div>
                            <div>장비점수 : {equip_point}</div>
                            {ramslot && <div>램 슬롯 : {ramslot}개</div>}
                            {gpuslot && <div>GPU 슬롯 : {gpuslot}개</div>}
                        </div>
                    )
                }
            })}
            <div>
                방송장비
            </div>
            <hr />
            {Equips.map(item => {
                const { name, key, tag, lvl, category } = item;
                const imgSource = imgs[`${tag[0]}_${lvl}`];
                return (
                    <div className={'product_box'} key={key}>
                        <div className={'product_image'} style={{ background: `center / contain no-repeat url(${imgSource || emptyProduct})` }} />
                        <div className={'product_name'}>{name}</div>
                        <div className={'product_category'}>카테고리 : {category}</div>
                        <div>레벨 : {lvl}</div>
                        <div className={'tag_list'}>{tag.map(tag => (
                            <div className={'tag'} key={tag}>{tag}</div>
                        ))}</div>
                    </div>
                )
            })}
            <div>
                주거환경
            </div>
            <hr />
            {Furnitures.map(item => {
                const { name, key, tag, lvl, category } = item;
                const imgSource = imgs[`${tag[0]}_${lvl}`];
                return (
                    <div className={'product_box'} key={key}>
                        <div className={'product_image'} style={{ background: `center / contain no-repeat url(${imgSource || emptyProduct})` }} />
                        <div className={'product_name'}>{name}</div>
                        <div className={'product_category'}>카테고리 : {category}</div>
                        <div>레벨 : {lvl}</div>
                        <div className={'tag_list'}>{tag.map(tag => (
                            <div className={'tag'} key={tag}>{tag}</div>
                        ))}</div>
                    </div>
                )
            })}

        </CustomModal>
    )
})

export default EquipmentModal
