import React from 'react'
import { useState } from 'react'
import { useSelector } from 'react-redux';
import CustomButton from '../../../../../UI/Button/CustomButton';
import { confirmMessage } from '../../../../../UI/Message/message';
import BlackJackModal from './BlackJack/BlackJackModal';

function GambleComponent() {
    const [blackJackModalOpen, setBlackJackModalOpen] = useState(false);
    const difficulty = useSelector(state=>state.difficulty);

    const onOpenGamble = ()=>{
        confirmMessage(`
        갬블 중 캐릭터가 활동하여 기타 이벤트의 손해에 관해서는 책임지지 않습니다.
        <br/>
        게임 결과의 저장은 시간의 흐름에 저장됩니다.
        <br/>
        결과를 저장 하시려면 인게임 1분을 경과시켜 주세요
        `, () => {
            if (difficulty === 2) {
                setBlackJackModalOpen(true);
            }
        })
    }
    return (
        <>
            <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', width: '100%', height: '100%' }}>
                <CustomButton onClick={onOpenGamble} style={{padding:'15px'}}>
                    도박장 입장
                </CustomButton>

            </div>
        <BlackJackModal visible={blackJackModalOpen} ModalHandler={setBlackJackModalOpen}/>
        </>
    )
}

export default GambleComponent;