import React, { memo, useState } from 'react'
import { useSelector } from 'react-redux';
import KrwIcon from '../../../../../../scss/icon/KrwIcon';
import CustomButton from '../../../../../../UI/Button/CustomButton';
import { AddComma } from '../../../../../../util/commonMethod';
import CoinSellModal from './CoinSellModal';

const AssetCoin = memo((props) => {
    const asset = useSelector(state => state.asset);
    const { coinList } = useSelector(state => state.coin);


    const [selectedCoin, setSelectedCoin] = useState({});
    const [coinSellModal, setCoinSellModal] = useState(false);

    const onCoinSellModalOpen = (coin) => {
        setCoinSellModal(true);
        setSelectedCoin(coin);
    }

    return (
        <>
            <div className={'title'}>
                <span>보유 코인</span>
                <span className={'money'}>
                    <KrwIcon className={'krw_icon'} />
                    {AddComma(asset.coin.reduce((acc, current) => { return acc + (current.averagePrice*current.totalCount) }, 0))}
                </span>
            </div>
            <div className={'list'}>
                <div className={'list_contents header'}>
                    <span>코인명</span>
                    <span>수익률</span>
                    <span>평가손익</span>
                    <span>평가금액</span>
                    <span>보유량</span>
                    <span>투자금액</span>
                    <span>비고</span>
                </div>
                {asset.coin.map(item => {
                    const coinObject = coinList.find(co => co.key === item.key);
                    const { price } = coinObject;
                    const { key, name, averagePrice, totalCount } = item;
                    const revenue = (price * totalCount) - (averagePrice * totalCount);
                    const revenuePercentage = (revenue / (averagePrice * totalCount)) * 100;
                    return (
                        <div className={'list_contents'} key={key}>
                            <span>{name}</span>
                            <span className={`${revenue < 0 ? 'minus' : 'plus'}`}>{AddComma(revenuePercentage)}%</span>
                            <span className={`${revenue < 0 ? 'minus' : 'plus'}`}>{AddComma(revenue)}</span>
                            <span>{AddComma(Math.round(price * totalCount))}</span>
                            <span>{totalCount}개</span>
                            <span>{AddComma(averagePrice * totalCount)}</span>
                            <span><CustomButton type={'danger'} style={{ width: '80%', minWidth: 'auto' }} onClick={() => onCoinSellModalOpen(item)}>매도</CustomButton></span>
                        </div>
                    )
                })}
            </div>
            <CoinSellModal visible={coinSellModal} ModalHandler={setCoinSellModal} selectedCoin={selectedCoin} />
        </>
    )
})

export default AssetCoin
