import { SET_INTERVAL,UP_SPEED, DOWN_SPEED,INIT_SPEED } from './type'

export function setTimerInterval(interval) {
    return {
        type: SET_INTERVAL,
        payload: interval,
    }

}

export function UpSpeed(){
    return {
        type : UP_SPEED,
    }
}

export function DownSpeed(){
    return{
        type : DOWN_SPEED
    }
}

export function initSpeed(){
    return{
        type :INIT_SPEED,
    }
}