import React, { memo, useEffect, useState } from 'react'
import { useSelector } from 'react-redux'
import CustomModal from '../../../UI/Modal/CustomModal'
import { sortingForObjectList } from '../../../util/commonMethod';

const TagModal = memo((props) => {

    const tag = useSelector(state => state.tag);
    const [nameSortDirection, setNameSortDirection] = useState(false);
    const [valueSortDirection, setValueSortDirection] = useState(false);
    const [searchTerm, setsearchTerm] = useState('');
    const [tagList, setTagList] = useState([])

    useEffect(() => {
        setTagList(tag);
    }, [tag])


    const onNameSort = () => {
        setNameSortDirection(!nameSortDirection);
        const newList = sortingForObjectList(tag, !nameSortDirection, 'name');
        setTagList(newList);
    }
    const onValueSort = () => {
        setValueSortDirection(!valueSortDirection);
        const newList = sortingForObjectList(tag, !valueSortDirection, 'value');
        setTagList(newList);
    }
    const onSearchTermChange = (e) => {
        const text = e.currentTarget.value
        setsearchTerm(text);
        const newList = tag.filter(item => item.name.indexOf(text.toUpperCase()) > -1);
        setTagList(newList);
    }

    return (
        <CustomModal
            {...props}
            title={'분야별 전문성'}
            style={{ height: '50vh' }}
            className={'tag_modal'}
        >
            <div className={'search_bar'}>
                <div onClick={onNameSort}>
                    이름 {nameSortDirection ? '▲' : '▼'}
                </div>
                <div onClick={onValueSort}>
                    능력치 {valueSortDirection ? '▲' : '▼'}
                </div>
                <div>
                    <input placeholder={'태그명'} value={searchTerm} onChange={onSearchTermChange} />
                </div>
            </div>
            {tagList.map(item => {
                return (
                    <div className={'tag_container'} key={item.name}>
                        <div className={'name'}>
                            {item.name}
                        </div>
                        <div className={'value'}>
                            {item.value.toFixed(2)}
                        </div>
                    </div>
                )
            })}


        </CustomModal>
    )
})

export default TagModal
