import React from 'react'
import CustomModal from '../../../../../../UI/Modal/CustomModal'

function BlackJackGuide(props) {
    return (
        <CustomModal
        {...props}
        title={'블랙잭 가이드'}
        className={'blackjack_guide'}
        >
            <div>
                블랙잭은 기본적으로 21 이하의 카드 조합은 가장 가까운 숫자를 뽑은 사람이 승리하는 겜블입니다.
                <br /><br />
                여러가지 규칙이 있지만, 여기서는 게임 사정에 맞게 일부를 수정하였습니다.
                <br /><br />
                <span style={{ color: 'red' }}>
                    게임의 결과는 인게임 시간이 지나야 저장됩니다. 저장하시려면 인게임 시간을 경과 시켜 주세요.
                </span>
            </div>
            <div>
                1. 배팅 금액을 설정합니다.<br />
                금액은 10,000원 이상 부터 가능합니다.
            </div>
            <div>
                2. 게임 시작 버튼을 누릅니다.
            </div>
            <div>
                3. 딜러와 플레이어에게 카드를 나눠 줍니다.
            </div>
            <div>
                4. 본인의 카드 숫자 합계가 낮다고 생각하는 경우 HIT을 눌러 추가 할 수 있습니다.<br />
                이때 21을 넘어가는 경우 플레이어의 패배 입니다.<br />
                21에 딱 맞춰 지는 경우 블랙잭이 터지게 됩니다.<br />
            </div>
            <div>
                5. 카드를 원하는 만큼 뽑으면 Stay를 눌러 딜러의 패를 확인합니다.
            </div>
            <div>
                6. 딜러는 본인의 카드 합계가 17미만일 경우 한장을 더 뽑습니다.
            </div>
            <div>
                7. 모든 과정이 지나가면 양측의 값을 비교하여 21에 더 가까운 숫자를 가진 사람이 승리하게 됩니다.
            </div>
            <div>
                8. 이길 경우 배팅금액의 2배를 받습니다.<br />
                블랙잭의 경우 배팅금액의 2.5배를 받습니다.
            </div>
        </CustomModal>
    )
}

export default BlackJackGuide