import { PAY_MONEY, SET_CHARTER_INFO, GET_MONEY, UP_HEALTH, DOWN_HEALTH,SET_STATUS } from './type'


export function setCharacterInfo(info) {
    return {
        type: SET_CHARTER_INFO,
        payload: info,
    }

}

export function payMoney(cost) {
    return {
        type: PAY_MONEY,
        payload: cost
    }
}
export function getMoney(cost) {
    return {
        type: GET_MONEY,
        payload: cost
    }
}

export function upHealth(value) {
    return {
        type: UP_HEALTH,
        payload : value
    }
}

export function downHealth() {
    return {
        type: DOWN_HEALTH,
    }
}

export function setStatus(key,value){
    return{
        type : SET_STATUS,
        payload:{key,value}
    }
}