import moment from 'moment';
import { NewsList } from '../Assets/NewsAssets';
import { shootingInfomationMsg } from '../component/Main/Section/shootingToast';
import { CALL_NEWS_EVENT, REMOVE_NEWS, SET_CHARTER_INFO, SET_NEWS_LIST } from '../_actions/type'


/**
 * 캐릭터 정보
 * 성별  sex : man
 * 외모  face : 5
 * 목소리 voice : 5
 * 언변 speach : 5
 * 활동 적합성 action_fitness: {
                    tags : 5
                }
 * 건강 health : 5
 * 신체능력 physical :5
 * 성격 personality : 5
 * 편집 능력 edit : 5
 * 리더쉽 leadership : 5
 * **/

const initialState = [

]

export default function (state = initialState, action) {
    switch (action.type) {
        case SET_NEWS_LIST:
            return [...state, ...action.payload]

        case CALL_NEWS_EVENT:
            const percent = Math.random();
            const myNewsKey = state.map(item => item.key);

            const RandomIndex = () => {
                return Math.floor(Math.random() * NewsList.length)
            }

            const callNews = (index) => {
                if (myNewsKey.length < NewsList.length &&  myNewsKey.indexOf(NewsList[index].key) > -1) {
                    let newIndex = RandomIndex();
                    return callNews(newIndex)
                } else {
                    return NewsList[index];
                }
            }
            let newNews = {};
            if (percent < 0.2) {
                //기사 1개 할지 2개 할지 3개 할지 정함

                let i = RandomIndex();
                newNews = callNews(i);
                shootingInfomationMsg(newNews.title);
                newNews.dateTime = action.payload;
                return [newNews, ...state];
            }else{
                return [...state];
            }
        case REMOVE_NEWS:
            //제거 기준 시간
            const standardTime = moment(action.payload).add(1, 'minute');
            const removeResultList = [...state]
            //제거할 기사 배열
            const removeNewsArr = removeResultList.filter(news => moment(news.dateTime).add(news.duration, 'd') < standardTime);
            removeNewsArr.map(targetNews => {
                const index = removeResultList.findIndex(news => news.key === targetNews.key);
                removeResultList.splice(index, 1);
            })
            return removeResultList
        default: return state;
    }
}