import { SET_ACTIVITY, SET_ACTIVITY_CONTENTS, SET_VIEWER_COUNT,INIT_ACTIVITY } from './type';

export function initActivity(activity){
    return {
        type : INIT_ACTIVITY,
        payload : activity
    }
}

export function setActivity(activity, contents = {}, startTime) {

    return {
        type: SET_ACTIVITY,
        payload: { activity, contents, startTime, contentsStartTime:startTime }
    }
}

export function setActivityContents(contents,contentsStartTime) {
    return {
        type: SET_ACTIVITY_CONTENTS,
        payload: {contents,contentsStartTime}
    }
}

export function setViewerCount(obj){
    return{
        type : SET_VIEWER_COUNT,
        payload : obj
    }
}
