import moment from 'moment';
import { DOWN_HEALTH, GET_MONEY, PAY_MONEY, SET_CHARTER_INFO, SET_STATUS, UP_HEALTH } from '../_actions/type'


/**
 * 캐릭터 정보
 * 성별  sex : man
 * 외모  face : 5
 * 목소리 voice : 5
 * 언변 speach : 5
 * 활동 적합성 action_fitness: {
                    tags : 5
                }
 * 건강 health : 5
 * 신체능력 physical :5
 * 성격 personality : 5
 * 편집 능력 edit : 5
 * 리더쉽 leadership : 5
 * **/

//시즌추가

const initialState = {
    name: {
        text: '캐릭터 명',
        value: '조철봉',
    },
    money: {
        text: '소지금',
        value: 1500000
    },
    sex: {
        text: '성별',
        value: 'man'
    },
    face: {
        text: '외모',
        value: 5,
    },
    voice: {
        text: "목소리",
        value: 5
    },
    speach: {
        text: '화술',
        value: 5
    },
    health: {
        text: '건강',
        value: 5
    },
    physical: {
        text: '신체 활력',
        value: 5
    },
    personality: {
        text: '성격',
        value: 5
    },
    edit: {
        text: '편집능력',
        value: 5
    },
    // leadership: {
    //     text: '리더쉽',
    //     value: 5
    // },
    season :{
        text : '시즌',
        value : moment().week()
    }
}

export default function (state = initialState, action) {
    switch (action.type) {
        case SET_CHARTER_INFO:
            return { ...state, ...action.payload }
        case PAY_MONEY:
            const newState = { ...state };
            newState.money.value = Number(newState.money.value) - Number(action.payload);
            return newState;
        case GET_MONEY:
            return { ...state, money: { ...state.money, value: Number(state.money.value) + Number(action.payload) } }
        case UP_HEALTH:
            const upHealth = { ...state.health };
            let upValue = upHealth.value + action.payload > 10 ? 10 : upHealth.value + action.payload;
            upHealth.value = Math.round(upValue*1000)/1000

            return { ...state, health: upHealth }
        case DOWN_HEALTH:
            const downHealth = { ...state.health };

            let downValue = downHealth.value - 0.01 < 1 ? 1 : downHealth.value - 0.01;
            downHealth.value = Math.round(downValue*1000)/1000

            return { ...state, health: downHealth }
        case SET_STATUS:
            const newStatusState = {...state}
            newStatusState[action.payload.key] = {...newStatusState[action.payload.key],value:action.payload.value}
            return newStatusState;
            
        default: return state;
    }
}