import { BUY_CONTENTS, SET_CONTENTS_LIST } from '../_actions/type';

const initialState = [
    {
        key: '저스트 채팅',
        name: '저스트 채팅',
        category: 'justchatting',
        tag: ['TALK'],
        majority: 45,
        equip_point: 0
    }
]

export default function (state = initialState, action) {

    switch (action.type) {
        case SET_CONTENTS_LIST:
            if (action.payload.length > 0) {
                return action.payload;
            } else {
                return state;
            }
        case BUY_CONTENTS:
            return [...state, action.payload]


        default: return state;
    }
}