import moment from 'moment';
import { Activities } from '../Assets/ActionAsset';
import { INIT_ACTIVITY, SET_ACTIVITY, SET_ACTIVITY_CONTENTS, SET_VIEWER_COUNT } from '../_actions/type'

const initialState = {
    activity: 'resting',
    broadcastContents:{}, // 방송중이던 컨텐츠
    editingContents:{}, //편집중이던 컨텐츠
    selfDevelopContents:{}, //자기계발하던 컨텐츠
    contents: {},
    startTime: moment().valueOf(),
    contentsStartTime: 0,
    totalBroadcastTime: 0, //누적 방송시간
    currentViewer: 0, //현재 시청자수
    averageViewer: 150, //평균 시청자수
    maxViewer: 0, // 최대 시청자수
    gameStartTime: moment().valueOf(), //게임 시작
    accumulateBroadcastTime : 0, //누적 방송시간
}

export default function (state = initialState, action) {

    switch (action.type) {
        case INIT_ACTIVITY:
            return { ...state, ...action.payload };
        case SET_ACTIVITY:
            
            if (action.payload.activity === Activities.broadcast) {
                return { ...state, ...action.payload , broadcastContents : action.payload.contents}; 
            } else if(action.payload.activity === Activities.edit){
                return { ...state, ...action.payload, editingContents : action.payload.contents, currentViewer: 0 }; // 방송 시작할때는 시청자 초기화
            } else if(action.payload.activity === Activities.self_develop){
                return { ...state, ...action.payload, selfDevelopContents:action.payload.contents, currentViewer: 0 }; // 방송 시작할때는 시청자 초기화
            }else{
                return {...state,...action.payload}
            }

        case SET_ACTIVITY_CONTENTS:

            if (state.activity === Activities.broadcast) {
                
                return { ...state, ...action.payload , broadcastContents : action.payload.contents}; 
            } else if(state.activity === Activities.edit){

                return { ...state, ...action.payload, editingContents : action.payload.contents }; // 방송 시작할때는 시청자 초기화

            } else if(state.activity === Activities.self_develop){

                return { ...state, ...action.payload, selfDevelopContents : action.payload.contents }; // 방송 시작할때는 시청자 초기화
            }else{
                return {...state,...action.payload}
            }

        case SET_VIEWER_COUNT:

            return { ...state, ...action.payload }

        default: return state;
    }

}