import moment from "moment";
import { SET_SLEEP, INIT_SLEEP, SET_AWAKE_TIME,SET_FAINT } from "../_actions/type";

const initialState = {

    flag: false,
    faint : false,
    awakeTime: moment().valueOf(),
    alarm: {

    }
}

export default function (state = initialState, action) {

    switch (action.type) {
        case SET_SLEEP: return { ...state, ...action.payload };
        case INIT_SLEEP: return { ...state, ...action.payload };
        case SET_AWAKE_TIME : return {...state, ...action.payload};
        case SET_FAINT : return {...state ,...action.payload}
        default: return state
    }
}