import React, { memo } from 'react'
import { useEffect } from 'react';
import { useRef } from 'react';
import { useState } from 'react';
import { useSelector } from 'react-redux';
import { Activities } from '../../../Assets/ActionAsset';
import dawn from './MonitoringImage/weather/dawn.png';
import night from './MonitoringImage/weather/night.png';
import morning from './MonitoringImage/weather/morning.png';
import sunset from './MonitoringImage/weather/sunset.png';

import HOUSE_1 from './MonitoringImage/house/house1/bg.png';
import bag from './MonitoringImage/house/house1/bag.png';
import dust from './MonitoringImage/house/house1/dust.png';


import HOUSE_2 from './MonitoringImage/house/house2/bg.png';
import carrier from './MonitoringImage/house/house2/carrier.png';
import light from './MonitoringImage/house/house2/light.png';
import dreamcatcher from './MonitoringImage/house/house2/dreamcatcher.png';
import plant from './MonitoringImage/house/house2/plant.png';
import shelf from './MonitoringImage/house/house2/shelf.png';
import badProp from './MonitoringImage/house/house2/badProp.png';



import HOUSE_3 from './MonitoringImage/house/house3/bg.png';
import aircon from './MonitoringImage/house/house3/aircon.png';
import light3 from './MonitoringImage/house/house3/light.png';
import drawing from './MonitoringImage/house/house3/drawing.png';
import shoper from './MonitoringImage/house/house3/shoper.png';
import plant3 from './MonitoringImage/house/house3/plant.png';


import HOUSE_4 from './MonitoringImage/house/house4/bg.png';
import aircon4 from './MonitoringImage/house/house4/aircon.png';
import light4 from './MonitoringImage/house/house4/light.png';
import clock from './MonitoringImage/house/house4/clock.png';
import shelf4 from './MonitoringImage/house/house4/shelf.png';
import plant4 from './MonitoringImage/house/house4/plant.png';

import HOUSE_5 from './MonitoringImage/house/house5/bg.png';
import carpet from './MonitoringImage/house/house5/carpet.png';
import light5 from './MonitoringImage/house/house5/light.png';
import drawing5 from './MonitoringImage/house/house5/drawing.png';
import selfPortrait from './MonitoringImage/house/bigheadArt.png';
import plant5 from './MonitoringImage/house/house5/plant.png';
import table from './MonitoringImage/house/house5/table.png';
import tropy from './MonitoringImage/house/house5/tropy.png';
import moment from 'moment';


const BackgroundComponent = memo((props)=>{
    const {className,id, ref, style } = props;
    const equipment = useSelector(state => state.equipment);
    const { activity } = useSelector(state => state.activity);
    const currentTime = useSelector(state => state.currentTime);
    const [weather, setWeather] = useState(morning)

    const charBgRef = useRef();

    useEffect(() => {
        const backWrapper = document.getElementById('char_monitor')
            //캐릭터가 위치한 곳으로 스크롤 이동 시켜둠
            // 이미지가 로드 되고 난 다음에 크기 구해서 해당 크기의 10프로 부분
            charBgRef.current.onload=()=>{
                //모바일 기준 화면의 60% 위치에 스크롤 옮기면 캐릭터 위치 배정
                const charWidth = charBgRef.current.clientWidth;
                if(activity === Activities.resting||activity === Activities.exercise){
                    backWrapper.scrollLeft=charWidth*0;
                }else {
                    backWrapper.scrollLeft=charWidth*0.8;
                }
                
            }
            if(charBgRef.current.clientWidth > 0){
                const charWidth = charBgRef.current.clientWidth;
                if(activity === Activities.resting||activity === Activities.exercise){
                    backWrapper.scrollLeft=charWidth*0;
                }else {
                    backWrapper.scrollLeft=charWidth*0.8;
                }
            }
    }, [activity])

    useEffect(() => {
        const currentHour = moment(currentTime).get('hour');
        if(currentHour >= 7 && currentHour < 17){
            setWeather(morning);
        }else if(currentHour >= 17 && currentHour < 19){
            setWeather(sunset);
        }else if(currentHour >= 19 || currentHour < 5){
            setWeather(night);
        }else{
            setWeather(dawn);
        }
        
      
    }, [currentTime])
    


    return(
        <div
        className={className}
        id={id}
        ref={charBgRef}
        > 
        {
            equipment.HOUSE.lvl === 1 &&
            <>
                <img className='weather' src={weather}/>
                <img className='houseBg' src={HOUSE_1} />
                <img className='house_1 dust a' src={dust}/>
                <img className='house_1 dust b' src={dust}/>
                <img className='house_1 dust c' src={dust}/>
                <img className='house_1 dust d' src={dust}/>
                <img className='house_1 bag' src={bag}/>
            </>
        }
        {
            equipment.HOUSE.lvl === 2 &&
            <>
                <img className='weather' src={weather}/>
                <img className='houseBg' src={HOUSE_2} />
                <img className='house_2 plant' src={plant}/>
                <img className='house_2 carrier' src={carrier}/>
                <img className='house_2 shelf' src={shelf}/>
                <img className='house_2 light' src={light}/>
                <img className='house_2 badprop' src={badProp}/>
                <img className='house_2 dreamcatcher' src={dreamcatcher}/>
            </>
        }
        {
            equipment.HOUSE.lvl === 3 &&
            <>
                <img className='weather' src={weather}/>
                <img className='houseBg' src={HOUSE_3} />
                <img className='house_3 shoper' src={shoper}/>
                <img className='house_3 light' src={light3}/>
                <img className='house_3 plant' src={plant3}/>
                <img className='house_3 drawing' src={drawing}/>
                <img className='house_3 aircon' src={aircon}/>
            </>
        }
        {
            equipment.HOUSE.lvl === 4 &&
            <>
                <img className='weather' src={weather}/>
                <img className='houseBg' src={HOUSE_4} />
                <img className='house_4 aircon' src={aircon4}/>
                <img className='house_4 light' src={light4}/>
                <img className='house_4 clock' src={clock}/>
                <img className='house_4 shelf' src={shelf4}/>
                <img className='house_4 plant' src={plant4}/>
            </>
        }
        {
            equipment.HOUSE.lvl === 5 &&
            <>
                <img className='weather' src={weather}/>
                <img className='houseBg' src={HOUSE_5}/>
                <img className='house_5 aircon' src={aircon4}/>
                <img className='house_5 drawing' src={drawing5}/>
                <img className='house_5 self_portrait' src={selfPortrait}/>
                <img className='house_5 tropy' src={tropy}/>
                <img className='house_5 carpet' src={carpet}/>
                <img className='house_5 table' src={table}/>
                <img className='house_5 plant' src={plant5}/>
                <img className='house_5 light' src={light5}/>
            </>
        }
            


        {props.children}
        </div >
    )
})

export default BackgroundComponent