import { DOWN_TAG, SET_TAG, UP_TAG } from "./type";


export function setTag(tagList) {
    return {
        type: SET_TAG,
        payload: tagList
    }
}

export function upTag(tagList=[],param) {
    return {
        type: UP_TAG,
        payload: {tagList,param}
    }
}

export function donwTag(tagList=[]){
    return {
        type: DOWN_TAG,
        payload: tagList
    }
}