import './message.scss';
// import confirmImg from '../../../css/img/confirm.png';
// import successImg from '../../../css/img/success.png';
// import errorImg from '../../../css/img/error.png';
// import infoImg from '../../../css/img/info.png';
// import promptImg from '../../../css/img/prompt2.png';

export const infoMessage = (msg, cb, img) => {
    createMsg('info', msg, cb, img);
}

export const errorMessage = (msg, cb, img) => {
    createMsg('error', msg, cb, img);
}

export const successMessage = (msg, cb, img) => {
    createMsg('success', msg, cb, img);
}

export const confirmMessage = (msg, okCb, cancelCb,img) => {
    createMsg('confirm', msg, okCb, img,cancelCb);
}
export const promptMessage = (msg, cb, img) => {
    createMsg('prompt', msg, cb, img);
}

const createMsg = (type, msg, cb, img, cancelCb) => {

    let root = document.getElementById('root');

    let panel = document.createElement('div');
    panel.setAttribute('id', 'alertBackground')
    panel.setAttribute('class', 'alertBackground');
    panel.addEventListener('click', () => { root.removeChild(panel) })

    let alert = document.createElement('div');
    alert.setAttribute('class', 'alertPanel');
    alert.addEventListener('click', (e) => { e.stopPropagation() })
    panel.appendChild(alert)

    // let header;
    // header = document.createElement('div');
    // header.setAttribute('class','alertHeader')
    // alert.appendChild(header)

    let body;
    body = document.createElement('div');
    body.setAttribute('class', 'alertBody');
    // if(!img){
    //     switch (type) {
    //         case 'success': img = successImg; break;
    //         case 'error': img = errorImg;  break;
    //         case 'confirm': img = confirmImg; break;
    //         case 'info': img = infoImg; break;
    //         case 'prompt': img = promptImg; break;
    //     }
    // }
    //입력된 이미지 추가
    // let imageContainer = document.createElement('div');
    // imageContainer.setAttribute('class','imageContainer')
    // imageContainer.setAttribute('style',`background : url(${img}) no-repeat center; background-size: contain; height: 120px; width: 120px; animation: slideinBottom 0.5s`)

    // let image =  document.createElement('img');
    // image.setAttribute('src',img);
    // image.setAttribute('alt','캐릭터 이미지');
    // image.setAttribute('class','alertImg');

    // imageContainer.appendChild(image);
    // body.appendChild(imageContainer);

    // 입력된 메세지 추가
    let textBox = document.createElement('div');
    textBox.setAttribute('class', 'alertText');
    textBox.innerHTML = msg;
    body.appendChild(textBox);

    // 입력창 추가
    let inputBox;
    if (type === "prompt") {
        inputBox = document.createElement('input');
        inputBox.setAttribute('class', 'alertInput');
        // inputBox.setAttribute('maxlength',10);
        body.appendChild(inputBox);
    }
    alert.appendChild(body);

    //버튼 추가
    let footer;
    footer = document.createElement('div');
    footer.setAttribute('class', 'alertFooter');

    if (type === 'confirm' || type === 'prompt') {
        let okBtn = document.createElement('div');
        okBtn.setAttribute('class', 'okBtn');
        okBtn.addEventListener('click', () => {
            root.removeChild(panel);
            if (cb) {
                if (inputBox) {
                    cb(inputBox.value);
                } else {
                    cb();
                }
            }
        })
        okBtn.innerText = "확인";
        footer.appendChild(okBtn);
        
        //취소 버튼
        let cancelBtn = document.createElement('div');
        cancelBtn.setAttribute('class', 'cancelBtn');
        cancelBtn.innerText = "취소";
        cancelBtn.addEventListener('click', () =>{
          if(cancelCb){
            cancelCb()
          }
          root.removeChild(panel)
        })
        footer.appendChild(cancelBtn);

    } else {
        let okBtn = document.createElement('div');
        okBtn.setAttribute('class', 'okBtn');
        okBtn.innerText = "확인";
        okBtn.addEventListener('click', () => {
            root.removeChild(panel);
            if (cb) {
                if (inputBox) {
                    cb(inputBox.value);
                } else {
                    cb();
                }
            }
        })
        footer.appendChild(okBtn);
    }


    alert.appendChild(footer);
    root.append(panel);
}