import { SET_SLEEP, INIT_SLEEP,SET_AWAKE_TIME,SET_FAINT } from './type';

export function initSleep(sleep) {
    return {
        type: INIT_SLEEP,
        payload: sleep
    }
}

export function setSleep(flag = false, alarm = {}) {
    return {
        type: SET_SLEEP,
        payload: { flag, alarm }
    }
}

export function setAwakeTime(awakeTime){
    return {
        type: SET_AWAKE_TIME,
        payload: { awakeTime }
    }
}

export function setFaint(faint){
    return {
        type : SET_FAINT,
        payload : {faint}

    }
}