import React from 'react';
import { useState } from 'react';
import './scss/CustomSwitch.scss';



function CustomSwitch(props) {
    const { value = false, onChange = () => { }, round } = props;
    const [check, setCheck] = useState(value);

    const onChangeSwitch = (e) => {
        const flag = e.currentTarget.checked;
        setCheck(flag)
        if (onChange) {
            onChange(flag);
        }
    }
    return (
        <label className={`switch ${round ? 'round' : ''}`}>
            <input type="checkbox" className='chkBox' checked={check} onChange={onChangeSwitch} />
            <span className="slider round"></span>
        </label>
    );
}

export default CustomSwitch;
