import { coinList } from '../Assets/coinAsset';
import { shootingCommentMsg, shootingInfomationMsg } from '../component/Main/Section/shootingToast';
import { SET_COIN_LIST, CHANGE_COIN_PRICE, SET_COIN,INIT_COIN } from '../_actions/type'

const coinAverage = () => {
    return coinList.reduce((a, b) => { return a + b.price }, 0)
}

const initialState = {
    standardAverage: coinAverage(),
    coinList: [...coinList]
};

export default function (state = initialState, action) {
    if (action.type === SET_COIN) {
        return { ...state, ...action.payload };
    } else if (action.type === SET_COIN_LIST) {
        return { ...state, coinList: action.payload }
    }else if (action.type === INIT_COIN){
        return initialState;
    } else if (action.type === CHANGE_COIN_PRICE) {
        const newCoinList = [...state.coinList];
        //상승할지 안할지는 50% 확률
        //상승 최대 300프로
        //하락 최대 100프로
        //가격 1억 넘어가면 하락장 파이를 올림

        newCoinList.map(coin => {
            if (coin.delisting) {
                //상장 폐지는 5% 확률로 복귀
                if (Math.random() < 0.05) {
                    coin.price = 500;
                    coin.delisting = false;
                }
            }else{
                const _UpOrDown = Math.random() * 100;
                if (coin.price > 100000000) {
                    if (_UpOrDown < 10) { //상승
                        coin.price = upPrice(coin.price);
                    } else if (_UpOrDown < 80) {
                        const _newPrice = downPrice(coin.price);
                        if(_newPrice === 0 ) shootingInfomationMsg(`${coin.name}이 거래정지 되었습니다.`,'red')
                        coin.price = _newPrice
                    }
                } else {
                    if (_UpOrDown < 30) { //상승
                        coin.price = upPrice(coin.price);
                    } else if (_UpOrDown < 60) {
                        const _newPrice = downPrice(coin.price);
                        if(_newPrice === 0 ) shootingInfomationMsg(`${coin.name}이 거래정지 되었습니다.`,'red')
                        coin.price = _newPrice
                    }
                }
            }
            
            // 코인 가격 0이면 상폐
            if (coin.price === 0){
                coin.delisting = true;
            } 
            coin.price = Math.floor(coin.price);
        })
        return { ...state, coinList: newCoinList }



    } else {
        return state;
    }
}



const upPrice = (price) => {
    let _newPrice = 0;
    const _UpPercentage = Math.random() * 100;
    if (_UpPercentage < 60) {
        _newPrice = price * 1.1;
    } else if (_UpPercentage < 65) {
        _newPrice = price * 1.2;
    } else if (_UpPercentage < 68) {
        _newPrice = price * 1.32;
    } else if (_UpPercentage < 75) {
        _newPrice = price * 1.38;
    } else if (_UpPercentage < 85) {
        _newPrice = price * 1.5;
    } else if (_UpPercentage < 90) {
        _newPrice = price * 1.7;
    } else if (_UpPercentage < 93) {
        _newPrice = price * 1.9;
    } else if (_UpPercentage < 97) {
        _newPrice = price * 2.0;
    } else {
        _newPrice = price * 2.5;
    }
    return _newPrice;
}

const downPrice = (price) => {
    let _newPrice = 0;
    const _UpPercentage = Math.random() * 100;
    if (_UpPercentage < 50) {
        _newPrice = price * 0.88;
    } else if (_UpPercentage < 58) {
        _newPrice = price * 0.78;
    } else if (_UpPercentage < 68) {
        _newPrice = price * 0.75;
    } else if (_UpPercentage < 75) {
        _newPrice = price * 0.70;
    } else if (_UpPercentage < 85) {
        _newPrice = price * 0.66;
    } else if (_UpPercentage < 90) {
        _newPrice = price * 0.58;
    } else if (_UpPercentage < 93) {
        _newPrice = price * 0.43;
    } else if (_UpPercentage < 97) {
        _newPrice = price * 0.35;
    } else if (_UpPercentage < 99) {
        _newPrice = price * 0.1;
    } else {
        _newPrice = price * 0;
    }
    return _newPrice;
}